<template>
    <v-container>
        <v-layout row v-if="error" :class="{ 'mb-4' : error }">
            <v-flex xs12 sm6 offset-sm3>
                <app-alert @dismissed="onDismissed" :text="error.message"></app-alert>
            </v-flex>
        </v-layout>
        <v-layout>
            <v-flex xs12 sm6 offset-sm3>
                <v-card color="blue" class="white--text" >
                    <v-card-text>
                        <h2 @click="passwordToSend()" class="ml-3" style="cursor: pointer" title="Se desejar podes actualizar a sua senha">
                            Envie os seus emails para quem desejas
                            <v-icon right dark class="green--text">mail</v-icon>
                        </h2>

                    </v-card-text>
                </v-card>

                <!-- Campos do formulario -->
                <v-card v-if="canSend">
                    <v-card-text>
                        <v-container>
                                <!--Campo assunto-->
                            <v-flex xs12>
                                <v-text-field
                                            label="Assunto do email"
                                            v-model="subject"
                                            type="text"
                                            required></v-text-field>
                            </v-flex>

                            <!--Campo mensagem-->
                                <v-flex xs12>
                                <v-text-field
                                    hint="Escreva aqui a mensagem que desejas enviar"
                                    v-model="description"
                                    multi-line
                                    rows="3"
                                    type="text"
                                    required></v-text-field>
                                </v-flex>
                                <v-flex xs12 sm12>
                                    <v-select
                                            label="Selecione os emails"
                                            :items="userEmails"
                                            v-model="selectedEmails"
                                            autocomplete
                                            multiple
                                            chips
                                            hint="Pode selecionar varios"
                                            persistent-hint
                                    ></v-select>
                                </v-flex>

                                <v-flex xs12 sm12>
                                    <v-select
                                            label="Explicadores do parceiro Fakul."
                                            :items="fakulUserEmails"
                                            v-model="selectedEmails"
                                            autocomplete
                                            multiple
                                            chips
                                            :hint="'Total dos explicadores da fakul: ' + fakulUserEmails.length"
                                            persistent-hint
                                    ></v-select>
                                </v-flex>

                                <!--Botoes do formulario-->
                                <v-layout row class="mt-5">

                                    <!--Botao cancelar-->
                                    <v-flex xs12 sm6>

                                        <v-btn
                                            block
                                            round
                                        >
                                            Cancelar
                                            <v-icon right dark class="red--text">highlight_off</v-icon>
                                            <span slot="loader" class="custom-loader">
                                                <v-icon light>cached</v-icon>
                                            </span>
                                        </v-btn>

                                    </v-flex>

                                    <!--Botao Gravar-->
                                    <v-flex xs12 sm6>

                                        <v-btn
                                                round
                                                flat
                                                block=""
                                                @click="onSave"
                                                :disabled="loading"
                                                :loading="loading">
                                            Enviar
                                            <v-icon right class="green--text">mail</v-icon>
                                            <span slot="loader" class="custom-loader">
                                                <v-icon light>cached</v-icon>
                                            </span>
                                        </v-btn>

                                    </v-flex>

                                </v-layout>
                                <v-layout row>

                                </v-layout>

                            <!--Botao enviar para os confirmados-->
                                <v-layout row>

                                    <v-flex xs12>

                                        <v-btn
                                                round
                                                block=""
                                                :disabled="loading"
                                                :loading="loading"
                                                @click.prevent="onSave('allConfirmed')"
                                                title="Enviar para todos os utilizadores confirmados - Nao inclui os da Fakul">
                                            Enviar p os confirmados
                                            <v-icon right class="green--text">mail</v-icon>
                                            <span slot="loader" class="custom-loader">
                                                <v-icon light>cached</v-icon>
                                            </span>
                                        </v-btn>

                                    </v-flex>

                                </v-layout>
                            <!--Botao enviar para todos-->
                                <v-layout row>


                                    <v-flex xs12>

                                        <v-btn
                                                round
                                                block=""
                                                :disabled="loading"
                                                :loading="loading"
                                                @click.prevent="onSave('all')"
                                                title="Enviar para todos os utilizadores - Nao inclui os da Fakul">
                                            Enviar p Todos tirando os da Fakul
                                            <v-icon right class="green--text">mail</v-icon>
                                            <span slot="loader" class="custom-loader">
                                                <v-icon light>cached</v-icon>
                                            </span>
                                        </v-btn>

                                    </v-flex>
                                    <v-flex xs12>

                                        <v-btn
                                                round
                                                block=""
                                                :disabled="loading"
                                                :loading="loading"
                                                @click.prevent="onSave('allUserNotExplicador')"
                                                title="Enviar para todos os utilizadores - Nao inclui os da Fakul">
                                            Enviar p Todos nao explicadores os da Fakul
                                            <v-icon right class="green--text">mail</v-icon>
                                            <span slot="loader" class="custom-loader">
                                                <v-icon light>cached</v-icon>
                                            </span>
                                        </v-btn>

                                    </v-flex>

                                </v-layout>

                        </v-container>
                    </v-card-text>
                </v-card>

            </v-flex>
        </v-layout>
        <!--<v-layout>-->
            <!--<v-->
        <!--</v-layout>-->
    </v-container>

</template>

<script>
    import { eventBus } from '../../main'
    export default {
        data () {
            return {
                description: '',
                editSubject: '',
                subject:  '', //assunto
                selectedEmails: [],
                canSend: false,
            }
        },
        watch: {

            loading (value) {
                if (!value && !this.error && this.canSend) {

                    this.notifyUser('green')
                    this.canSend = false

                }
            }

        },
        computed: {
            userAuth () {
                return this.$store.getters.userAuth
            },
            isUserAuth () {
                return this.userAuth !== null && this.userAuth !== undefined
            },
            users () {
                return this.$store.getters.getUsers
            },

            fakulUsers () {
                return this.$store.getters.getFakulUsers
            },

            userEmails () {
                if (this.users.length  <= 0) return []

                const emails = []
                this.users.forEach((u) => {

                    emails.push(u.email)

                })
                return emails
            },

            fakulUserEmails () {

                if (this.fakulUsers.length  <= 0) return []

                const emails = []
                this.fakulUsers.forEach((u) => {

                    if (u.email.includes('@'))
                    {
                        emails.push(u.email)
                    }


                })
                return emails
            },

            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            },
        },
        methods: {

            onSave (value) {

                if (this.description ===  '' || this.subject === '') {
                    alert('O assunto e a descricao sao obrigatorios')
                    return
                }

                if (value === 'allConfirmed' && confirm('Tem certeeza que quer enviar para todos confirmados?'))
                {

                    this.selectedEmails = this.allConfirmedUser()
                }
                else if (value === 'all' && confirm('Tem certeeza que quer enviar para todos?'))
                {

                    this.selectedEmails = this.allUser()

                }
                else if (value === 'allUserNotExplicador' && confirm('Tem certeeza que quer enviar para todos que nao sao explicadores?'))
                {

                    this.selectedEmails = this.allUserNotExplicador()

                }
                if (this.selectedEmails.length <= 0) {
                    alert('Selecione os emails')
                    return
                }

                let emailObject = {
                    message: this.description,
                    subject: this.subject,
                    // to: 'joseeugenioseie@gmail.com',
                    emails: this.selectedEmails
                }

                this.$store.dispatch('sendMail', emailObject)
            },

            allConfirmedUser ()
            {
                let emails = []

                this.users.forEach((u) => {

                    if (u.emailVerified)
                    {
                        emails.push(u.email)
                    }
                })

                return emails

            },

            allUser () {
                let emails = []

                this.users.forEach((u) => {
                    emails.push(u.email)
                })

                return emails

            },

            allUserNotExplicador () {
                let emails = []

                this.users.forEach((u) => {
                    if (!u.Explicador || !u.name)
                    {
                        emails.push(u.email)
                    }

                })

                return emails

            },

            passwordToSend()
            {
                let pass = prompt('Digite  a senha')
                if (pass === '1234554321')
                {
                    this.canSend = true
                }
            },
            onDismissed () {
                this.$store.dispatch('clearError')
            },
            notifyUser (color)
            {
                let text = "Emails enviados com sucesso!"
                eventBus.$emit('showSnackbar', text, color); //Esse evento eh tratado no ficheiro App.vue, eh la onde foi montado o componente snackbar
            },
        },

    }
</script>
<style>
    .active {
        background: lightslategray;
    }
</style>


