<template>

    <div style="width: 100%">

        <v-flex xs12 sm12>

            <v-card>

                <!--Cabecalho-->
                <v-card-title class="darken-1" :class="!hasSubjects ? 'white' : 'blue' ">
                    <span
                        class="headline white--text"
                        :class="{'red--text' : !hasSubjects }">
                        {{ subjectsTitle }}
                    </span>

                    <v-spacer></v-spacer>
                    <div v-if="isMyProfile" class="white" style="border-radius: 100px; padding: 2px">
                        <v-progress-circular
                                :size="circularSize"
                                :width="7"
                                :rotate="360"
                                title="Percentagem de preenchimento"
                                style="float: right;cursor: pointer"
                                :value="personalInfoFillPercent"
                                :color="hasSubjects ? 'green' : 'red'"
                                @click="toggle = !toggle"
                        >
                            <span v-if="hasSubjects && toggle">{{ personalInfoFillPercent }}</span>
                            <span v-else-if="hasSubjects" title="Disciplinas registadas!" ><v-icon>done</v-icon></span>
                            <span v-else="" title="Clique em editar, para registar as disciplinas que explicas">{{ personalInfoFillPercent }}</span>
                        </v-progress-circular>
                    </div>
                    <explicador-edit-subjects-dialog
                            style="float: right;"
                            v-if="isMyProfile"
                            :isMobile="true"
                            :showSubjectsEditDialog="showSubjectsEditDialog"
                            :user="user">
                    </explicador-edit-subjects-dialog>
                    <template v-else="">
                        <v-btn
                                :class="{'red--text' : !hasSubjects }"
                                fab=""
                                flat=""
                                small=""
                                outline=""
                                style="float: right;"
                        >
                            <v-icon class="white--text" :class="{'red--text' : !hasSubjects }">more_horiz</v-icon>
                        </v-btn>
                    </template>



                </v-card-title>

                <v-list v-if="explainClassesDetails && hasSubjects">

                    <v-subheader>{{ isMyProfile ? 'Niveis que eu explico' : 'Niveis que explica:' }}</v-subheader>

                    <v-chip v-for="item in explainLevels" v-bind:key="item">
                        <v-avatar class="blue">{{ item.charAt(0).toString().toUpperCase() + item.charAt(1).toString().toUpperCase() }}</v-avatar>
                        {{ item }}
                    </v-chip>


                    <v-subheader class="mt-5">
                        {{ isMyProfile ? 'Disciplinas que explicas por classe ou niveis' : 'Disciplinas por classe ou nivel' }}
                    </v-subheader>

                    <template v-for="(item, i) in explainClassesDetails">
                        <v-subheader>
                            {{ item.classe.length <= 4 ? item.classe : item.level }}  - ( {{ item.subjects.length }} disciplinas )
                        </v-subheader>

                        <template v-for="(subject, i) in item.subjects" v-if="subject">

                            <v-list-tile @click="">

                                <v-list-tile-action>
                                    <v-icon>library_books</v-icon>
                                </v-list-tile-action>
                                <v-list-tile-content>
                                    <v-list-tile-title>{{ subject }}</v-list-tile-title>
                                </v-list-tile-content>

                            </v-list-tile>
                        </template>

                        <v-divider inset v-if="i !== explainClassesDetails.length -1"></v-divider>
                    </template>

                </v-list>
            </v-card>
        </v-flex>

    </div>

</template>

<script>
    export default {
        props: ['user', 'circularSize', 'showSubjectsEditDialog'],
        data: () => ({
            hidden: false,
            nameU: '',
            toggle: true,
        }),
        created () {
        },
        computed: {
            options () { //usado para fazer o scroll
                return {
                    duration: 1000,
                    offset: 0,
                    easing: 'easeInQuint'
                }
            },
            userAuth () {
                return this.$store.getters.userAuth
            },

            isUserAuth () {
                return this.userAuth !== null && this.userAuth !== undefined
            },

            isUser () {
                return this.user !== null && this.user !== undefined
            },
            isMyProfile () {
                return this.isUserAuth && this.isUser && this.user.id === this.userAuth.id
            },
            explainLevels ()
            {
                return this.isExplicador ? this.user.Explicador.explainLevels : ''
            },
            img () {
                return this.isUser ? this.user.photoURL : '//ssl.gstatic.com/s2/oz/images/sge/grey_silhouette.png'
            },
            nomeUser() {
                return this.isUser && this.user.name !== null && this.user.name !== '' ? this.user.name : 'Sem Nome'
            },

            isExplicador ()
            {
                if (!this.isUser) return false
                return this.user.Explicador && this.user.Explicador.explainLevels
            },

            explainClassesDetails () {

                if (!this.isExplicador) return false

                return this.user.Explicador.subjectDetails
            },
            userFaculty () {

                const negative = 'Sem Faculdade'

                if (!this.isUser) return negative

                let fac = this.user.Educations.faculty
                return this.isUser && fac !== null && fac !== undefined && fac !== '' ? 'Faculdade de ' + fac : negative
            },
            userCourse () {

                const negative = 'Sem Curso'

                if (!this.isUser) return negative

                let course = this.user.Educations.course

                return course && course !== null && course !== undefined && course !== '' ? 'Curso de ' + course : negative

            },
            userDepartment () {

                const negative = 'Sem Departamento'

                if (!this.isUser) return negative

                let department = this.user.Educations.department

                return department && department !== null && department !== undefined && department !== '' ? department : negative


            },
            userNivel () {

                const negative = 'Sem Nivel'

                if (!this.isUser) return negative

                let nivel = this.user.Educations.nivel

                return nivel && nivel !== null && nivel !== undefined && nivel !== '' ? nivel : negative


            },
            hasSubjects () {

                return this.isExplicador && this.user.Explicador.explainLevels
            },
            personalInfoFillPercent ()
            {
                return this.hasSubjects ? 100 : this.isExplicador ? 20 : 5;
            },

            subjectsTitle () {

                if (!this.hasSubjects && !this.isMyProfile) return 'Ainda não registou as disciplinas que explica'
                if (!this.hasSubjects && this.isMyProfile) return 'Não explicas nenhuma discipplina ?'

                let username = this.nomeUser.length > 18 ? this.nomeUser.slice(0,19) + '...'  : this.nomeUser

                return this.isMyProfile ? 'Minhas disciplinas' : 'Disciplinas do ' + username
            },
            userSubjects () {

                const negative = []

                if (!this.isUser) return negative

                let subjects = this.user.Educations.subjects

                return subjects


            },

            hasSomeLocation () {

                if ( !this.isUser ) return false

                return this.user.Location

            },

            userProvince () {

                const negative = 'Provincia Não registada'

                if (!this.hasSomeLocation) return negative

                let userProv = this.user.Location.province

                return userProv !== null && userProv !== undefined && userProv !== '' ? userProv : negative

            },

            userCity () {

                const negative = 'Cidade Não registada'

                if (!this.hasSomeLocation) return negative

                let userCity = this.user.Location.city

                return userCity !== null && userCity !== undefined && userCity !== '' ? userCity : negative

            },

            userBairro () {

                const negative = 'Bairro Não registado'

                if (!this.hasSomeLocation) return negative

                let userBairro = this.user.Location.bairro

                return userBairro !== null && userBairro !== undefined && userBairro !== '' ? userBairro : negative

            },

        },
        methods: {

            scrollTo (top) {

                window.scroll({
                    top: top,
                    left: 10,
                    behavior: 'smooth'
                });

                // Scroll to a certain element
//                document.querySelector(el).scrollIntoView({
//                    behavior: 'smooth'
//                });

            },

        },
    }
</script>
<style scoped>
    div.card__title {
        margin-top: 0px;
        padding: 0;
        font-size: 8pt;
    }
    .list__tile__title {
        font-size: 11px!important;
    }
    .headline {
        margin-left: 1em;
    }
    .list__tile__title {
        font-size: 12px!important;
    }
    .explicador-bar-fixed {
        z-index: 0;
    }
    .explicador-edit-item {
        float: right;
        cursor: pointer
    }
    .icon {
        font-size: 18px;
    }
    .img-radius {
        width: 50px;
        height: 50px!important;
    }
    .container {
        padding: 0px;
    }


</style>


