<template>
    <v-container>
           <!--Lateral direito-->
        <v-layout class="mt-0">

            <v-flex>
                <v-container>
                    <v-layout row v-if="error" :class="{ 'mb-4 mt-4'  : error }">
                        <v-flex xs12 sm6 offset-sm3>
                            <app-alert @dismissed="onDismissed" :text="error.message"></app-alert>
                        </v-flex>
                    </v-layout>
                    <v-layout>
                        <v-flex xs12 sm4 offset-sm4>
                            <v-card>
                                <v-card-text>
                                    <h2 style="color: darkgray">Faça Login
                                        <!--<v-btn title="Ir para a pagina principal" flat="" to="/" style="float: right;margin: -5px 0">-->
                                            <!--<v-icon>home</v-icon> Voltar-->
                                        <!--</v-btn>-->
                                    </h2>
                                </v-card-text>

                            </v-card>

                            <v-card>
                                <v-card-text>
                                    <v-container>
                                        <form @submit.prevent="onLogin">

                                            <v-layout row>
                                                <v-flex xs12>
                                                    <v-btn flat round block  class="yellow" outline @click="googleSignIn" :disabled="loading && isLoginProcessing">
                                                        Google
                                                    </v-btn>
                                                </v-flex>

                                                <v-flex xs12>
                                                    <v-btn flat round block  class="blue" outline @click="facebookSignIn" :disabled="loading && isLoginProcessing">
                                                        Facebook
                                                    </v-btn>
                                                </v-flex>
                                            </v-layout>

                                            <div class="text-xs-center mt-3 mb-4">OR</div>

                                            <v-layout row>
                                                <v-flex xs12>
                                                    <v-text-field
                                                            name="email"
                                                            label="Mail"
                                                            prepend-icon="mail"
                                                            v-model="email"
                                                            type="email"
                                                            required></v-text-field>
                                                </v-flex>
                                            </v-layout>
                                            <v-layout row>
                                                <v-flex xs12>
                                                    <v-text-field
                                                            name="password"
                                                            label="Password"
                                                            prepend-icon="lock"
                                                            v-model="password"
                                                            type="password"
                                                            required></v-text-field>
                                                </v-flex>
                                            </v-layout>

                                            <v-layout row class="mt-2">

                                                <v-flex x12 sm3>
                                                    <!--Botao cancelar-->
                                                    <v-btn block flat="" color="red" to="/">
                                                        Cancelar
                                                    </v-btn>

                                                </v-flex>

                                                <v-flex xs12 sm3>
                                                    <!--Botao Entrar-->
                                                    <v-btn flat block=""
                                                           class="ml-3" color="green" type="submit"
                                                           :disabled="loading && isLoginProcessing" :loading="loading && isLoginProcessing">
                                                        Entrar
                                                        <span slot="loader" class="custom-loader">
                                                <v-icon light>cached</v-icon>
                                            </span>
                                                    </v-btn>

                                                </v-flex>

                                                <v-spacer></v-spacer>

                                                <v-flex xs1>
                                                    <password-reset-dialog></password-reset-dialog>
                                                </v-flex>

                                            </v-layout>

                                        </form>
                                    </v-container>
                                </v-card-text>
                            </v-card>

                            <v-card>
                                <v-card-text>
                                    <p style="color: darkgray">Ainda nao tem conta? <v-btn block="" flat="" round="" router to="/register">Clique aqui para  criar uma conta</v-btn></p>
                                </v-card-text>
                            </v-card>
                        </v-flex>
                    </v-layout>
                </v-container>
            </v-flex>
        </v-layout>

    </v-container>
</template>

<script>
    import { eventBus } from '../../main'
    export default {
        data () {
            return {
                email: '',
                password: '',
                isLoginProcessing: false,
            }
        },
        computed: {
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            }
        },
        watch: {
            loading (value)
            {
                if (!value)
                {
                    this.isLoginProcessing = false
                }
            }
        },
        methods: {
            onLogin () {
                //validar primeiro os dados de login
                this.isLoginProcessing = true
                this.$store.dispatch('loginUser', {email: this.email, password: this.password})
            },
            onDismissed () {
                this.$store.dispatch('clearError')
            },
            googleSignIn () {
                this.$store.dispatch('googleSignIn')
            },
            facebookSignIn () {
                this.$store.dispatch('facebookSignIn')
            }
        },
        beforeCreate () {
//            eventBus.$emit('ocultarMenus', false); //Esse evento eh tratado no ficheiro App.vue,  eh onde sao ocultados os menus
            this.$Progress.start()
            this.$Progress.set(80)
        },
        mounted () {
        },
    }
</script>


