<template>
    <v-dialog v-model="dialog" persistent="" max-width="500px">
        <!--Botao de activacao-->
        <v-btn
                small=""
                slot="activator"
                :color="isMobile ? '' : 'orange'"
                :flat="isMobile"
                title="Editar os meus dados Academicos"
                @click.stop="dialog = !dialog"
        >
            Editar
            <v-icon>edit</v-icon>
        </v-btn>

        <!--Corpo principal da dialog-->
        <v-card>
            <!--Cabecalho da dialog-->
            <v-card-title class="headline">
                Edite os seus dados Académicos
                <v-spacer></v-spacer>
                <v-btn title="Fechar" @click="onCancel()" class="mb-0 mt-0" icon=""><v-icon>close</v-icon></v-btn>
            </v-card-title>

            <!--Separador-->
            <v-divider></v-divider>

            <v-container grid-list-sm class="pa-4 mb-5">

                <!--Cabecalho de reporte de erros-->
                <v-layout row v-if="error">
                    <v-flex xs12>
                        <app-alert @dismissed="onDismissed" :text="error.message"></app-alert>
                    </v-flex>
                </v-layout>

                <v-card-text>
                    <v-container grid-list-md>

                        <!--Os niveis de educacao-->
                        <v-layout row>
                            <v-flex xs12>
                                <v-select
                                        label="Qual é o seu nivel?"
                                        required
                                        v-model="level"
                                        :items="studiesLevels"
                                ></v-select>
                            </v-flex>
                         </v-layout>

                        <!--Para nivel Primario-->
                        <v-layout wrap v-if="isPrimaryLevel">
                            <v-flex xs12>
                                <v-select
                                        label="Qual é a sua escola?"
                                        required
                                        autocomplete
                                        multiple
                                        v-model="userSchool"
                                        :items="schools"
                                ></v-select>
                            </v-flex>
                            <v-flex xs12>
                                <v-select
                                        label="Em que classe estas?"
                                        required
                                        v-model="userClass"
                                        :items="primaryClasses"
                                ></v-select>
                            </v-flex>

                            <v-flex xs12 v-if="userClass">
                                <v-select
                                        chips
                                        label="Em que estado está a sua formação"
                                        required
                                        v-model="educationState"
                                        :items="educationStates"
                                ></v-select>
                            </v-flex>

                            <v-flex xs12 v-if="userClass.length && educationState === 'em curso'">
                                <v-select
                                        multiple
                                        chips
                                        label="Essas são as suas disciplinas?"
                                        required
                                        v-model="selectedUserSubjects"
                                        :items="primarySubjects2ndDegree"
                                ></v-select>
                            </v-flex>
                            <!--<div style="height: 200px"></div>-->
                        </v-layout>

                        <!--Para nivel Secundario-->
                        <v-layout wrap v-if="isSecundaryLevel">
                            <v-flex xs12>
                                <v-select
                                        label="Qual é a sua escola?"
                                        required
                                        v-model="userSchool"
                                        :items="schools"
                                ></v-select>
                            </v-flex>
                            <v-flex xs12>
                                <v-select
                                        label="Em que classe estas?"
                                        required
                                        v-model="userClass"
                                        :items="secundaryClasses"
                                ></v-select>
                            </v-flex>

                            <v-flex xs12>
                                <v-select
                                        chips
                                        label="Em que estado está a sua formação"
                                        required
                                        v-model="educationState"
                                        :items="educationStates"
                                ></v-select>
                            </v-flex>

                            <v-flex xs12 v-if="userClass.length && educationState === 'em curso'">
                                <v-select
                                        multiple
                                        chips
                                        label="Essas são as suas disciplinas?"
                                        required
                                        v-model="selectedUserSubjects"
                                        :items="secundarySubjects"
                                ></v-select>
                            </v-flex>
                            <!--<div style="height: 200px"></div>-->
                        </v-layout>

                        <!--Para nivel Tecnico-->
                        <v-layout wrap v-if="isTenicalLevel">
                            <v-flex xs12>
                                <v-select
                                        label="Onde voçê estuda?"
                                        required
                                        v-model="userSchool"
                                        :items="institutions"
                                ></v-select>
                            </v-flex>
                            <v-flex xs12>
                                <v-select
                                        label="Que nivel frenquentas?"
                                        required
                                        v-model="userClass"
                                        :items="tecnicalDegrees"
                                ></v-select>
                            </v-flex>
                            <v-flex xs12>
                                <v-select
                                        chips
                                        label="Em que estado está a sua formação"
                                        required
                                        v-model="educationState"
                                        :items="educationStates"
                                ></v-select>
                            </v-flex>
                            <v-flex xs12 v-if="userClass.length && educationState === 'em curso'">
                                <v-select
                                        chips=""
                                        multiple=""
                                        autocomplete
                                        label="Que disciplinas fazes actualmente?"
                                        required
                                        v-model="selectedUserSubjects"
                                        :items="otherSubjects"
                                >
                                    <template slot="selection" slot-scope="data">
                                        <v-chip
                                                close
                                                @input="data.parent.selectItem(data.item)"
                                                :selected="data.selected"
                                                class="chip--select-multi"
                                                :key="JSON.stringify(data.item)"
                                        >
                                            {{ data.item }}
                                        </v-chip>
                                    </template>
                                </v-select>
                            </v-flex>
                            <!--<div style="height: 200px"></div>-->
                        </v-layout>

                        <!--Para nivel Universitario-->
                        <v-layout wrap v-if="isMasterLevel">
                            <v-flex xs12>
                                <v-select
                                        label="Onde Voçê estuda?"
                                        required
                                        v-model="institution"
                                        :items="universities"
                                ></v-select>
                            </v-flex>
                            <v-flex xs12>
                                <v-select
                                        label="Sua Faculdade"
                                        chips=""
                                        autocomplete
                                        v-model="faculty"
                                        :items="facultyNames"
                                ></v-select>
                            </v-flex>
                            <v-flex xs12>
                                <v-text-field
                                        label="Seu Departamento"
                                        hint="Apenas siglas ex: DMI"
                                        v-model="department"
                                        required
                                ></v-text-field>
                            </v-flex>
                            <v-flex xs12>
                                <v-select
                                        label="Seu curso"
                                        required
                                        chips=""
                                        autocomplete
                                        v-model="course"
                                        :items="courseNames"
                                ></v-select>
                            </v-flex>
                            <v-flex xs12>
                                <v-select
                                        label="Que ano frenquenta?"
                                        required
                                        v-model="courseNivel"
                                        :items="['1º ano', '2º ano', '3º ano', '4º ano', '5º ano', '6º ano']"
                                ></v-select>
                            </v-flex>

                            <v-flex xs12>
                                <v-select
                                        chips
                                        label="Em que estado está a sua formação"
                                        required
                                        v-model="educationState"
                                        :items="educationStates"
                                ></v-select>
                            </v-flex>
                            <v-flex xs12 v-if="educationState === 'em curso'">
                                <v-select
                                        label="Suas disciplinas neste semestre"
                                        hint="Se nao lembras de todas, podes actualizar depois"
                                        v-model="selectedUserSubjects"
                                        chips=""
                                        multiple=""
                                        autocomplete
                                        :items="subjectNames"
                                >
                                    <template slot="selection" slot-scope="data">
                                        <v-chip
                                                close
                                                @input="data.parent.selectItem(data.item)"
                                                :selected="data.selected"
                                                class="chip--select-multi"
                                                :key="JSON.stringify(data.item)"
                                        >
                                            {{ data.item }}
                                        </v-chip>
                                    </template>
                                </v-select>
                            </v-flex>
                        </v-layout>

                        <!--Para nivel Outro-->
                        <v-layout wrap v-if="isOtherLevel">
                            <v-flex xs12>
                                <v-select
                                        label="Onde voçê estuda?"
                                        required
                                        v-model="userSchool"
                                        :items="institutions"
                                ></v-select>
                            </v-flex>
                            <v-flex xs12>
                                <v-select
                                        chips
                                        label="Que curso frenquentas?"
                                        required
                                        v-model="course"
                                        :items="courseNames"
                                ></v-select>
                            </v-flex>
                            <v-flex xs12>
                                <v-select
                                        chips
                                        label="Em que estado está a sua formação"
                                        required
                                        v-model="educationState"
                                        :items="educationStates"
                                ></v-select>
                            </v-flex>
                            <v-flex xs12 v-if="educationState === 'em curso'">
                                <v-select
                                        multiple
                                        chips
                                        label="Que disciplinas fazes actualmente?"
                                        required
                                        v-model="selectedUserSubjects"
                                        :items="otherSubjects"
                                ></v-select>
                            </v-flex>
                        </v-layout>

                        <v-layout v-if="level !== ''">
                            <v-flex xs12>
                                <small>Se Alguma informação estiver em falta, clique no link abaixo 'Sugerir registo' para nos informar!</small>
                                <br>
                                <a title="Clique para preencher a disciplina" target="_blank" href="https://docs.google.com/forms/d/e/1FAIpQLSerGzBmhk9hdPUgC9awFRP6bdNETUtfo5rUG8MuywPmqtWIVA/viewform?usp=sf_link">
                                    Sugerir registo
                                </a>
                            </v-flex>
                        </v-layout>

                    </v-container>
                </v-card-text>
            </v-container>

            <v-divider></v-divider>

            <v-card-actions>
                <v-btn flat color="primary" @click="onHelp">Ajuda</v-btn>
                <v-spacer></v-spacer>
                <v-btn flat color="primary" @click="onCancel()">Fechar</v-btn>
                <v-btn
                    flat
                    @click="onSave"
                    class="green--text"
                    :disabled="loading"
                    :loading="loading"
                >
                    Salvar

                    <span slot="loader" class="custom-loader">
                        <v-icon light>cached</v-icon>
                    </span>
                </v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>
<script>
    import { eventBus } from '../../main'
    export default {
        props: ['user', 'isMobile'],
        data () {
            return {

                dialog: false,
                institution: '',
                course: '',
                selectedUserSubjects: [],
                faculty: '',
                department: '',
                courseNivel: '',
                level: '',
                userClass: '',
                userSchool: '',
                educationState: '',
                showAcademicProcess: false,
                isInicializing: false,
                canceled: false,
            }
        },
        computed: {
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            },
            userAuth () {
                return this.$store.getters.userAuth
            },
            isUserAuth () {
                return this.userAuth !== null && this.userAuth !== undefined
            },
            isPrimaryLevel () {
               return this.level === 'Primário'
            },
            isSecundaryLevel () {
                return this.level === 'Secundário'
            },
            isTenicalLevel () {
                return this.level === 'Técnico'
            },
            isMasterLevel () {
                return this.level === 'Superior'
            },
            isOtherLevel () {
                return this.level === 'Outro'
            },
            studiesLevels () {
                return this.$store.getters.getStudiesLevels
            },
            primaryClasses () {
                return this.$store.getters.getPrimaryClasses.slice(1-4) //retirando as primeiras 4 classes.
            },

            secundaryClasses () {
                return this.$store.getters.getSecundaryClasses
            },

            tecnicalDegrees () {
                return this.$store.getters.getTecnicalDegrees
            },

            primarySubjects2ndDegree () {
                return this.$store.getters.getPrimarySubjects2ndDegree
            },

            secundarySubjects () {
                return this.$store.getters.getSecundarySubjects
            },

            otherSubjects () {
                return this.$store.getters.getOtherSubjects
            },

            schools () {
                return this.$store.getters.getSchools
            },

            institutions () {
                return this.$store.getters.getInstitutions
            },

            universities () {
                return this.$store.getters.getUniversities
            },

            educationStates () {
                return this.$store.getters.getEducationState
            },

            faculties () {
                return this.$store.getters.getFaculties
            },
            facultyNames () {
                return this.$store.getters.getFacultieNames
            },

            departments () {
                return this.$store.getters.getDepartments
            },
            departmentNames () {
                return this.$store.getters.getDepartmentNames
            },

            courses () {
                return this.$store.getters.getCourses
            },
            courseNames () {
                return this.$store.getters.getUEMCourses
            },

            subjects () {
                return this.$store.getters.getSubjects
            },
            subjectNames () {

                return this.$store.getters.getInternal_APISubjects

            },

            isUser () {
                return this.user !== null && this.user !== undefined
            },
            isMyProfile () {
                return this.userIsAuth && this.isUser && this.user.id === this.userAuth.id
            }
        },
        watch: {

            level (value)
            {
                if (!this.isInicializing)
                {
                    this.userClass = this.userAuth && this.userAuth.Educations && this.userAuth.Educations.level === value ? this.userAuth.Educations.userClass : ''
                    this.userSchool = this.userAuth && this.userAuth.Educations && this.userAuth.Educations.level === value ? this.userAuth.Educations.userSchool : ''
                    this.educationState = this.userAuth && this.userAuth.Educations && this.userAuth.Educations.level === value ? this.userAuth.Educations.educationState : ''
                }
                this.isInicializing = false
            },
            isPrimaryLevel (value) {
                if (value !== null &&  value)
                {
                    this.selectedUserSubjects = this.primarySubjects2ndDegree
                }
            },

            isSecundaryLevel (value) {
                if (value !== null && value)
                {
                    this.selectedUserSubjects = this.secundarySubjects
                }
            },
            loading (value) {

                if (!value && !this.error && this.showAcademicProcess && !this.canceled) {

                    let text = "Os seus dados academicos foram actualizados com sucesso!"
                    let color = 'green'
                    eventBus.$emit('showSnackbar', text, color, 5000); //Esse evento eh tratado no ficheiro App.vue, eh la onde foi montado o componente snackbar
                    this.showAcademicProcess = false
                    this.dialog = false
                }
            },
            dialog (value)
            {
                if (value) {
                    this.inicializeUserInfo()
                }
                else //Entao a dialog esta a fechar
                {
                    this.$store.dispatch('updateTheUserInArray', this.userAuth)
                    this.$store.dispatch('setLoading', false)
                    this.canceled = true
                }

            }

        },
        methods: {
            onSave () {

                this.canceled = false
                let user = this.userAuth

                let today = new Date()
                user.Educations = {
                    educationState: this.educationState, //Estado da educacao: Em curso, desistido, concluido,...
                    level:          this.level,     //Nivel ou ensino em que ele esta afecto: Primario, Secundario, Tecnico, Superior, Outro
                    institution:    this.institution,   //Caso estiver no ensino: Superior, Tecnico, Outro
                    userSchool:     this.userSchool,    //Caso estiver no ensino: Primario, Secundario
                    userClass:      this.userClass,     //Caso estiver no ensino Primario ou secundario: como explicador so pode ser aprovado, se estiver de 5-12 classes.
                    course:         this.course,        //O curso que o explicador frenquenta.
                    faculty:        this.faculty,       //A faculdade em que ele esta afecto
                    department:     this.department,    //Departamento em que ele esta afecto
                    courseNivel:    this.courseNivel,         //Nivel dele caso estiver no ensino superior: de 1 a 8 ano dependendo do curso.
                    subjects:       this.selectedUserSubjects, //As disciplinas que ele frenquenta neste semestre.
                    created_at:     user.Educations && user.Educations.created_at? user.Educations.created_at : today,
                    updated_at:     today
                }
                this.showAcademicProcess = true

                this.$store.dispatch('updateOnlineUserData', user)
                // Chamar o metodo que vai gravar as disciplinas
//                this.saveNewSubjectsToDB()
//                this.saveFacultieToDB()
                this.saveDepartmentToDB()

            },

            saveNewSubjectsToDB () {

                let vm = this
                this.subjectNames.forEach(function (selectSub) {

                    vm.$store.dispatch('saveSubject', {
                        name: selectSub,
                        creatorId: vm.userAuth.id,
                        created_at: new Date(),
                        confirmed: false
                    })
                })


            },


            saveDepartmentToDB () {

                if (this.department !== '')
                {
                    //gravar faculdade
                    this.$store.dispatch('saveDepartment', {
                        name: this.department,
                        creatorId: this.userAuth.id,
                        created_at: new Date(),
                        confirmed: false
                    })
                }

            },

            getObjectNames (type, array) {
                let itemNames = []
                let objects = []


                    if (type === 'institutions')
                    {
                        objects = array !== undefined ? array : this.institutions
                    }
                    else if (type === 'faculties')
                    {
                        objects = array !== undefined ? array : this.faculties
                    }
                    else if (type === 'courses')
                    {
                        objects = array !== undefined ? array : this.courses
                    }
                    else if (type === 'departments')
                    {
                        objects = array !== undefined ? array : this.departments
                    }
                    else if (type === 'subjects')
                    {
                        objects = array !== undefined ? array : this.subjects
                    }

                    objects.forEach(function (o) {

                        itemNames.push(o.name)

                    })

                return itemNames
            },

            onCancel () {
                this.inicializeUserInfo()
                this.$store.dispatch('setLoading', false)
                this.canceled = true
                this.dialog = false
            },

            onDismissed () {
                this.$store.dispatch('clearError')
                this.$store.dispatch('setLoading', false)
            },

            inicializeUserInfo () {

                if (!this.isUserAuth) return //se nao estiver autenticado nao precisamos de inicializar

                const education = this.isUserAuth ? this.userAuth.Educations : {}

                if (!education) return //se o user nao tiver registado ainda os dados da educacao nao precisamos de inicializar

                this.isInicializing = true


                this.level = this.isUserAuth && education && education.level ? education.level : ''
                this.educationState = this.isUserAuth && education && education.educationState ? education.educationState : ''
                this.institution = this.isUserAuth && education && education.institution ? education.institution : ''
                this.userSchool = this.isUserAuth && education && education.userSchool ? education.userSchool : ''
                this.userClass = this.isUserAuth && education && education.userClass ? education.userClass : this.userClass
                this.course = this.isUserAuth && education && education.course ? education.course : ''
                this.faculty = this.isUserAuth && education && education.faculty ? education.faculty : ''
                this.department = this.isUserAuth && education && education.department ? education.department : ''
                this.courseNivel = this.isUserAuth && education && education.courseNivel ? education.courseNivel : ''

//                Implementacao temporaria, ao mudar para id, mudar isso.
                this.selectedUserSubjects = education && education.subjects ? education.subjects  : []

            },

            onHelp () {
                this.dialog = false
                this.$router.push('/help')
            }
        }
    }
</script>


