<template>
    <div>
        <template>
           <v-layout justify-center right fill-height>
               <v-btn
                   fab
                   bottom
                   left
                   flat
                   round
                   color="red"
                   title="Ocultar "
                   slot="activator"
                   @click.stop="closeDrawer"
                   v-if="temporary"
               >
                   <v-icon>close</v-icon>
               </v-btn>
           </v-layout>
        </template>
        <v-list dense class="height">
            <template>

                <!--Select da disciplina encima no drawer esquerdo-->
                <v-layout justify-center fill-height v-if="isHomePage">
                    <v-flex xs11>
                        <v-select
                                v-bind:items="tipos"
                                v-model="e2"
                                label="Selecione o filtro"
                                prepend-icon="content_paste"
                                single-line
                                auto
                                hide-details
                        ></v-select>
                    </v-flex>
                </v-layout>

                <!--Campo de pesquisa encima no drawer esquerdo-->
                <v-layout v-if="isHomePage">
                    <v-flex>
                        <v-text-field
                                light
                                solo
                                v-model="searchedData"
                                prepend-icon="search"
                                :placeholder="'Pesquisar ' + comp_searchedData"
                                class="elevation-0"
                                style="max-width: 400px; min-width: 128px"
                        ></v-text-field>
                    </v-flex>
                </v-layout>

                <!--Todo: Layout com a lista das discipplinas que sao mostradas, falta colocar o scroll-->
                <v-container  v-if="isLoading" fill-height>
                    <v-layout justify-center align-center >
                        <v-flex xs12 offset-xs5>
                            <v-progress-circular indeterminate :size="30" color="blue"></v-progress-circular>
                        </v-flex>
                    </v-layout>
                </v-container>

                <!--Notificacao que aparece quando nao ha resuultados
                 de pesquisa.-->
                <div v-else-if="items.length === 0">
                    <template>
                        <v-list-tile @click.prevent="">
                            <v-list-tile-action>
                                <v-icon class="red--text">youtube_searched_for</v-icon>
                            </v-list-tile-action>
                            <v-list-tile-content>
                                <v-list-tile-title class="red--text">
                                    Sem resultados . . .
                                </v-list-tile-title>
                            </v-list-tile-content>
                        </v-list-tile>
                    </template>
                </div>

                <div v-for="item in items">
                    <v-divider v-if="totalOfThis(e2, item)"></v-divider>
                    <template v-if="totalOfThis(e2, item)">
                        <v-list-tile
                            :title="'Ver explicadores de ' + item"
                            :class="{'list__tile--active': filtering === item }">
                            <v-list-tile-action>
                                  <v-icon>library_books</v-icon>
                            </v-list-tile-action>
                            <v-list-tile-content @click="onSelect(item);" style="cursor: pointer">
                                <v-list-tile-title>

                                   {{ item }}  ( {{ totalOfThis(e2, item) }} )

                                </v-list-tile-title>
                            </v-list-tile-content>
                            <v-list-tile-action v-if="filtering === item" title="Apagar o filtro" @click="onCloseFilter" style="cursor: pointer">
                                <v-icon color="red">close</v-icon>
                            </v-list-tile-action>
                        </v-list-tile>
                    </template>
                </div>

            </template>

            <!--Todo: Segundo divider que poe a linha de divisao-->
            <v-divider></v-divider>
        </v-list>

        <!--Botao para o envio de sugestao, que aparece apenas para os users autenticados-->
        <div v-if="isUserAuth">
            <sugestion-dialog></sugestion-dialog>
        </div>
    </div>
</template>
<script>
    import { eventBus } from '../../main'
    export default {
        props: ['temporary'],
        beforeCreate ()
        {
            this.$store.dispatch('setLoading', true)
        },
        created () {
            eventBus.$on('resetExplicadoresFilter', () => {

                this.filtering = ''
                this.searchedData = ''
                this.e2 = 'Disciplinas'

            })

            eventBus.$on('totalExplicadoresFilter', (listOfExplicadores, selectedNivel) => {

                this.listOfExplicadores = listOfExplicadores
                this.selectedNivel = selectedNivel


            })
        },
        data: () => ({
            searchedData: '',
            active: false,
            isProcessingHere: false,
            filtering: '',
            selectedNivel: '',
            listOfExplicadores: [],
            e2: 'Disciplinas'
        }),
        computed: {
            comp_searchedData ()  {
                return this.e2;
            },
            isLoading ()
            {
                return this.$store.getters.loading
            },
            tipos() {

                let tiposSup = [
                    'Províncias','Distritos','Dist. Municipais','Bairros',
                    'Disciplinas','Cursos','Faculdades','Universidades'
                ]

                if (this.selectedNivel === 'Superior') return tiposSup
                if (this.selectedNivel === 'Primário' || this.selectedNivel === 'Secundário') return ['Províncias', 'Distritos', 'Dist. Municipais','Bairros', 'Disciplinas', 'Escolas','Classes']
                if (this.selectedNivel === 'Outro' || this.selectedNivel === 'Técnico') return ['Províncias','Distritos','Dist. Municipais','Bairros', 'Disciplinas']



            },
            provinces () {
                return this.$store.getters.getProvinceNames
            },

            districtNames () {

                let dists = this.$store.getters.getDistrictNames

                if (dists.length)
                {
//                    console.log('Ja buscou os distritos, nao precisa buscar...', new Date())
                    return dists
                }
//                console.log('Ainda nao tinha buscado os Distritos, vai buscar agora...', new Date())

                this.$store.dispatch('getAllDistricts')
                return this.$store.getters.getDistrictNames.sort()
            },

            municipalDistrict () {
                let distMun = this.$store.getters.getMunicipalDistrictNames

                if (distMun.length)
                {
//                    console.log('Ja buscou os distritos municiapais, nao precisa buscar...', new Date())
                    return this.$store.getters.getMunicipalDistrictNames
                }
//                console.log('Ainda nao tinha buscado, vai buscar agora...', new Date())

                this.$store.dispatch('getAllMunicipalDistricts')
                return this.$store.getters.getMunicipalDistrictNames

            },

            bairros () {
                let bMC = this.$store.getters.getMCBairrosNames
                let bMP = this.$store.getters.getMPBairrosNames
                return bMC.concat(bMP).sort()
            },
            subjects () {
//                return this.$store.getters.getSubjectNames
                return this.$store.getters.getInternal_APISubjects
            },
            primaryEP1Subjects ()
            {
                return this.$store.getters.getPrimarySubjects2ndDegree //Busca todas disciplinas para 1 classe a 7 classe.
            },

            secundarySubjects () {
                return this.$store.getters.getSecundarySubjects
            },

            tecnicalSubjects () {
                return this.$store.getters.getTecnicalSubjects
            },

            otherSubjects () {
                return this.$store.getters.getOtherSubjects
            },

            universitySubjects () {
                return this.$store.getters.getInternal_APISubjects
            },
            courses () {
                return this.$store.getters.getUEMCourses
            },
            //Essas instituicoes sao universidades
            institutions () {
                return this.$store.getters.getUniversities
            },
            schools () {
                return this.$store.getters.getSchools
            },
            primaryClasses () {
                return this.$store.getters.getPrimaryClasses
            },

            secundaryClasses () {
                return this.$store.getters.getSecundaryClasses
            },

            faculties () {
                return this.$store.getters.getFacultieNames
            },
            items () //Itens que sao filtrados pelo select
            {

                return (this.comp_searchedData === '' || this.comp_searchedData === 'Províncias') ? this.searchedProvinces :
                       (this.comp_searchedData === 'Disciplinas') ? this.searchedSubjects :
                       (this.comp_searchedData === 'Distritos') ? this.searchedDistricts :
                       (this.comp_searchedData === 'Dist. Municipais') ? this.searchedMunicipalDistricts :
                       (this.comp_searchedData === 'Bairros') ? this.searchedBairros :
                       (this.comp_searchedData === 'Cursos') ? this.searchedCourses :
                       (this.comp_searchedData === 'Faculdades') ? this.searchedFaculties :
                       (this.comp_searchedData === 'Universidades') ? this.searchedInstitutions :
                       (this.comp_searchedData === 'Classes') ? this.searchedClasses :
                       (this.comp_searchedData === 'Escolas') ? this.searchedSchools : []
            },
            searchedProvinces () //Quando as provincias sao pesquisadas
            {
                if (this.searchedData === '') return this.provinces //Quando nao se pesquisa nada nas opcoes de filtro

                return this.provinces.filter(item => {
                    return item.toLowerCase().includes(this.searchedData.toLowerCase())
                })
            },
            searchedDistricts () //Quando os Distritos sao pesquisados
            {
                if (this.searchedData === '') return this.districtNames //Quando nao se pesquisa nada nas opcoes de filtro

                return this.districtNames.filter(item => {
                    return item.toLowerCase().includes(this.searchedData.toLowerCase())
                })
            },
            searchedMunicipalDistricts () //Quando as provincias sao pesquisadas
            {
                if (this.searchedData === '') return this.municipalDistrict

                return this.municipalDistrict.filter(item => {
                    return item.toLowerCase().includes(this.searchedData.toLowerCase())
                })
            },
            searchedBairros () //Quando as provincias sao pesquisadas
            {
                if (this.searchedData === '') return this.bairros

                return this.bairros.filter(item => {
                    return item.toLowerCase().includes(this.searchedData.toLowerCase())
                })
            },
            searchedSubjects ()
            {
                let subjects = (this.selectedNivel === 'Superior') ? this.universitySubjects :
                    (this.selectedNivel === 'Secundário') ? this.secundarySubjects :
                    (this.selectedNivel === 'Primário') ? this.primaryEP1Subjects :
                    (this.selectedNivel === 'Técnico') ? this.tecnicalSubjects :
                    (this.selectedNivel === 'Outro') ? this.otherSubjects : []


                if (this.searchedData === '') return subjects //Quando nao se pesquisa nada nas opcoes de filtro

                return subjects.filter(s => {
                    return s.toLowerCase().includes(this.searchedData.toLowerCase())
                })
            },
            searchedCourses ()
            {
                if (this.searchedData === '') return this.courses

                return this.courses.filter(c => {
                    return c.toLowerCase().includes(this.searchedData.toLowerCase())
                })
            },
            searchedInstitutions () {
                if (this.searchedData === '') return this.institutions

                return this.institutions.filter(i => {
                    return i.toLowerCase().includes(this.searchedData.toLowerCase())
                })
            },

            searchedSchools () {
                if (this.searchedData === '') return this.schools

                return this.schools.filter(i => {
                    return i.toLowerCase().includes(this.searchedData.toLowerCase())
                })
            },

            searchedClasses () {
                let classes = this.selectedNivel === 'Primário' ? this.primaryClasses : this.selectedNivel === 'Secundário' ? this.secundaryClasses : []

                if (this.searchedData === '' && this.selectedNivel === 'Primário') return this.primaryClasses
                if (this.searchedData === '' && this.selectedNivel === 'Secundário') return this.secundaryClasses

                return classes.filter(c => {
                    return c.toLowerCase().includes(this.searchedData.toLowerCase())
                })
            },

            searchedFaculties ()
            {
                if (this.searchedData === '') return this.faculties

                return this.faculties.filter(f => {
                    return f.toLowerCase().includes(this.searchedData.toLowerCase())
                })
            },
            isHomePage () {
                return this.$store.getters.isHomePage
            },
            isUserAuth () {
                return this.$store.getters.isUserAuth
            },
        },
        watch: {
            comp_searchedData (value) {
                this.searchedData = ''
            },
        },
        methods: {

            totalOfThis (filterType, item)
            {
                let total = 0;
                let validation = [];

                this.listOfExplicadores.forEach((exp) => {

//                    if (!exp.name.length) return

                    if ((filterType === '' || filterType === 'Províncias') && exp.name && exp.Explicador && exp.Explicador.explainLevels.includes(this.selectedNivel)) {
                        if (exp.Locations && exp.Locations.province === item)
                        {
                            total ++
                        }

                    }
                    if ((filterType === '' || filterType === 'Distritos') && exp.name &&  exp.Explicador && exp.Explicador.explainLevels.includes(this.selectedNivel)) {
                        if (exp.Locations && exp.Locations.district === item)
                        {
                            total ++
                        }

                    }
                    if ((filterType === '' || filterType === 'Dist. Municipais') && exp.name &&  exp.Explicador && exp.Explicador.explainLevels.includes(this.selectedNivel)) {
                        if (exp.Locations && exp.Locations.municipalDistrict === item)
                        {
                            total ++
                        }

                    }
                    if ((filterType === '' || filterType === 'Bairros') && exp.name &&  exp.Explicador && exp.Explicador.explainLevels.includes(this.selectedNivel)) {
                        if (exp.Locations && exp.Locations.bairro === item)
                        {
                            total ++
                        }

                    }

                    if ((filterType === '' || filterType === 'Disciplinas') && exp.name && exp.Explicador && exp.Explicador.explainLevels.includes(this.selectedNivel)) {

                        let subjsDT = exp.Explicador.subjectDetails
                        let combine = exp.name + '' + item

                        subjsDT.forEach((sub) => {
                            if (sub.level === this.selectedNivel && sub.subjects.includes(item) && !validation.includes(combine))
                            {
                                total ++
                                validation.push(combine)
                                return
                            }

                        })
                    }

                    if ((filterType === '' || filterType === 'Cursos') && exp.name &&  exp.Explicador && exp.Explicador.explainLevels.includes(this.selectedNivel)) {
                        if (exp.Educations && exp.Educations.course === item)
                        {
                            total ++
                        }

                    }
                    if ((filterType === '' || filterType === 'Faculdades') && exp.name &&  exp.Explicador && exp.Explicador.explainLevels.includes(this.selectedNivel)) {
                        if (exp.Educations && exp.Educations.faculty === item)
                        {
                            total ++
                        }

                    }
                    if ((filterType === '' || filterType === 'Universidades') && exp.name &&  exp.Explicador && exp.Explicador.explainLevels.includes(this.selectedNivel)) {
                        if (exp.Educations && exp.Educations.institution === item)
                        {
                            total ++
                        }

                    }
                    if ((filterType === '' || filterType === 'Escolas') && exp.name &&  exp.Explicador && exp.Explicador.explainLevels.includes(this.selectedNivel)) {
                        if (exp.Educations && exp.Educations.userSchool === item)
                        {
                            total ++
                        }

                    }
                    if ((filterType === '' || filterType === 'Classes') && exp.name &&  exp.Explicador && exp.Explicador.explainLevels.includes(this.selectedNivel)) {
                        if (exp.Explicador && (exp.Explicador.explainPrimaryClasses.includes(item) || exp.Explicador.explainSecundaryClasses.includes(item)))
                        {
                            total ++
                        }

                    }

                })


                //Verificar o tipo de objecto
                //Verifica o nivel selecionado
                //Contar...

                return total
            },
            onSelect (item)
            {

                if (this.filtering === item) return

                this.$store.dispatch('setLoading', true)

                this.filtering = item

//                Enviar o valor e o tipo do valor, ex: Matematica Basica, tipo: Disciplina
                let tipoPesquisado = this.comp_searchedData === '' ? 'Províncias' : this.comp_searchedData
                eventBus.$emit('filterExplicadoresBy', item, tipoPesquisado); //Evento sendo escutado no ficheiro Home.vue
                eventBus.$emit('showWhatIsFiltering', item, tipoPesquisado); //Evento sendo escutado no ficheiro Home.vue

            },
            onCloseFilter () {
                // Reseta os criterios de pesquisa de dados na pagina, para o default
                // Este evento eh tratado nos metodos created () dos Componentes Home.vue e MainMenuLeft.vue
                this.filtering = ''

                eventBus.$emit('resetExplicadoresFilter');

            },
            closeDrawer () {
                eventBus.$emit('closeDrawer', 'left')
            }

        }
    }
</script>
<style>
    div.menu__content {
        max-height: 400px!important;
        background: red;
    }
</style>


