<template>
    <v-container>
        <v-layout row v-if="error" :class="{ 'mb-4 mt-4' : error }">
            <v-flex xs12 sm6 offset-sm3>
                <app-alert @dismissed="onDismissed" :text="error.message"></app-alert>
            </v-flex>
        </v-layout>
        <v-layout>
            <v-flex xs12 sm4 offset-sm4>

                <!--Titulo do formulario-->
                <v-card>
                    <v-card-text>
                        <h2 style="color: darkgray">Criação de conta</h2>
                    </v-card-text>
                </v-card>

                <!--Card com o formulario-->
                <v-card>
                    <v-card-text>
                        <v-container>
                            <form @submit.prevent="onSignup">
                                <v-layout row class="mt-0">
                                    <v-flex xs12>
                                        <v-btn flat round block  class="yellow" outline @click="googleSignIn" :disabled="loading">
                                            Google
                                        </v-btn>
                                    </v-flex>

                                    <v-flex xs12>
                                        <v-btn flat round block  class="blue" outline @click="facebookSignIn" :disabled="loading">
                                            Facebook
                                        </v-btn>
                                    </v-flex>
                                </v-layout>

                                <div class="text-xs-center mt-3 mb-4">OR</div>

                                <v-layout row>
                                    <v-flex xs12>
                                        <v-text-field
                                                label="Seu email"
                                                prepend-icon="mail"
                                                v-model="email"
                                                type="email"
                                                required></v-text-field>
                                    </v-flex>
                                </v-layout>
                                <v-layout row>
                                    <v-flex xs12>
                                        <v-text-field
                                            name="password"
                                            label="Password"
                                            prepend-icon="lock"
                                            v-model="password"
                                            type="password"
                                            required></v-text-field>
                                    </v-flex>
                                </v-layout>
                                <v-layout row>
                                    <v-flex xs12>
                                        <v-text-field
                                                label="Confirmar Password"
                                                prepend-icon="lock"
                                                id="confirmPassword"
                                                v-model="confirmPassword"
                                                type="password"
                                                :rules="[comparePasswords]"></v-text-field>
                                    </v-flex>
                                </v-layout>

                                <v-layout row class="mt-2">

                                    <v-flex xs12 sm3>
                                        <!--Botao cancelar-->
                                        <v-btn block flat="" color="red" to="/">
                                            Cancelar
                                        </v-btn>

                                    </v-flex>

                                    <v-flex xs12 sm3>
                                        <!--Botao Entrar-->
                                        <v-btn flat block="" class="ml-3" color="green" type="submit" :disabled="isRegisterProcessing && (loading || !password || password !== confirmPassword)" :loading="loading && isRegisterProcessing">
                                            Entrar
                                            <span slot="loader" class="custom-loader">
                                                <v-icon light>cached</v-icon>
                                            </span>
                                        </v-btn>

                                    </v-flex>

                                    <v-spacer></v-spacer>
                                    <v-flex sm3>
                                        <v-checkbox hint="Recomendado" title="Fui recomendado" v-model="recomended"></v-checkbox>
                                    </v-flex>
                                </v-layout>

                            </form>
                        </v-container>
                    </v-card-text>
                </v-card>

                <!--Opcao de login para quem ja possui uma conta-->
                <v-card>
                    <v-card-text>
                        <p style="color: darkgray">Ja possui uma conta? <v-icon>help</v-icon><v-btn block="" flat="" round="" router to="/login">Clique aqui para login</v-btn></p>
                    </v-card-text>
                </v-card>
            </v-flex>
        </v-layout>
    </v-container>
</template>

<script>
    import { eventBus } from '../../main'
    export default {

        data () {
            return {
                email: '',
                password: '',
                confirmPassword: '',
                recomended: false,
                isRegisterProcessing: false,
            }
        },
        computed: {
            comparePasswords () {
                return this.password !== this.confirmPassword
                    ? 'Senha de confirmacao nao corresponde' : ''
            },
            userAuth () {
                return this.$store.getters.userAuth
            },
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            }
        },
        watch: {
            loading (value) {

                if (!value && !this.error && this.isRegisterProcessing) {

                    this.isRegisterProcessing = false
                    let text = "Enviamos um email de confirmacao para si, confirme clicado no link"
                    let color = 'green'
                    eventBus.$emit('showSnackbar', text, color,10000); //Esse evento eh tratado no ficheiro App.vue, eh la onde foi montado o componente snackbar

                }
            }

        },
        methods: {
            onSignup () {
                this.isRegisterProcessing = true
                this.$store.dispatch('createUser', {email: this.email, password: this.password, recomended: this.recomended})
            },
            onDismissed () {
                this.$store.dispatch('clearError')
            },
            googleSignIn () {
                this.$store.dispatch('googleSignIn')
            },
            facebookSignIn () {
                this.$store.dispatch('facebookSignIn')
            }
        },
        beforeCreate () {
//            eventBus.$emit('ocultarMenus', false); //Esse evento eh tratado no ficheiro App.vue,  eh onde sao ocultados os menus
            this.$Progress.start()
            this.$Progress.set(80)
        },
        mounted () {
//            this.$Progress.finish()
        },
    }
</script>


