<template>
    <v-dialog v-model="dialog" width="350px">
        <v-avatar size="40px" title="Clique para alterar a sua foto de perfil" slot="activator" class="mr-3">
          <img
                style="cursor: pointer"
                v-if="isUser"
                :src="user.photoURL"
                alt=""
                >
            <img
               v-else
               src="//ssl.gstatic.com/s2/oz/images/sge/grey_silhouette.png"
               alt=""
               >
        </v-avatar>
        <v-card>
            <!--Titulo da dialog-->
            <v-card-title
               class="blue white--text py-4 title">
                Carregue a sua foto de perfil
            </v-card-title>

            <!--Corpo  da dialog-->
            <v-container grid-list-sm class="pa-4 mb-5">

                <!--Espaco para o reposrte dos erros-->
                <v-layout row v-if="error">
                    <v-flex xs12>
                        <app-alert @dismissed="onDismissed" :text="error.message"></app-alert>
                    </v-flex>
                </v-layout>

                <!--Corpo com os botoes da dialog-->
                <v-layout row wrap class="mt-0">

                    <!--Botao de carregamento-->
                    <v-flex xs12 class="mt-0 ">
                        <v-btn block flat @click="onPickFile">
                            Carregar nova imagem
                            <v-icon right dark>cloud_upload</v-icon>
                        </v-btn>
                        <form>
                            <input
                                    type="file"
                                    style="display: none"
                                    ref="fileInput"
                                    name="file"
                                    accept="image/*"
                                    @change="onFilePicked">
                        </form>
                    </v-flex>

                    <!--Espaco para a previsualizacao da imagem-->
                    <v-card xs12 height="180px" class="mt-4">
                        <v-flex xs12 v-if="imageUrl !== ''">
                            <img style="width: 100%; cursor: pointer" :src="imageUrl" @click="onPickFile" height="175">
                        </v-flex>
                    </v-card>

                </v-layout>

            </v-container>

            <v-divider class="mb-2"></v-divider>

            <!--Action da dialog-->
            <v-card-actions class="pt-0">
                <!--<v-spacer></v-spacer>-->
                <v-btn flat block v-if="isUserAuth" color="primary" @click="dialog = false; imageUrl = ''">Cancelar</v-btn>
                <v-btn
                    flat
                    block
                    v-if="isUserAuth"
                    @click="uploadPhoto"
                    class="green--text"
                    :disabled="loading"
                    :loading="loading">

                    Actualizar foto

                    <span slot="loader" class="custom-loader">
                        <v-icon light>cached</v-icon>
                    </span>
                </v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>
<script>
    import { eventBus } from '../../main'
    import axios from 'axios'
    import VForm from "vuetify/src/components/VForm/VForm";
    export default {
        components: {VForm},
        props: ['user', 'showDialog'],
        data () {
            return {
                validation_token: 'base64:dXeGr7/AvrT16YlEp35hYlGfv654OGdPvUbPBF7GphM=2018',
                dialog: false,
                imageUrl: '',
                image: null,
                file: null,
                photoURLServer: null,
            }
        },
        computed: {
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            },
            isUserAuth () {
                return this.$store.getters.isUserAuth
            },
            userAuth () {
                return this.$store.getters.userAuth
            },
            isUser () {
                return this.user !== null && this.user !== undefined
            },
            isMyProfile () {
                return this.userIsAuth && this.isUser && this.user.id === this.userAuth.id
            }
        },
        watch: {
            dialog (value) {

                if (value) {

                    this.imageUrl = this.user !== null ? this.user.photoURL : ''

                } else {

                    this.$emit('closing')
                    this.dialog = false,
                    this.$store.dispatch('clearError')

                }
            },
            showDialog(value) {

                if (value)
                {
                    this.dialog = value
                }

            },
            loading (value) {

                if (!value && !this.error && this.dialog) {

                    let text = "A sua imagem foi actualizada com sucesso..."
                    let color = 'green'
                    eventBus.$emit('showSnackbar', text, color); //Esse evento eh tratado no ficheiro App.vue, eh la onde foi montado o componente snackbar
                }
            }

        },

        methods: {
            uploadPhoto () {

                this.$store.dispatch('setLoading', true)
                this.$store.dispatch('uploadPhoto', {image: this.image, photoURLServer:  ''})

                //Depois de implementar o upload para um server eh so descomentar essa linha.
//                this.resizePhotoOnAPI()

            },

            resizePhotoOnAPI ()
            {
//                let urlLocal = 'http://localhost:8000/api/sizeImage'
//                let urlLocal = 'http://explicador-api.herokuapp.com'
                let urlOnline = 'https://explicador-api.herokuapp.com/api/sizeImage'


                let photo = this.user.photoURLServer ? this.user.photoURLServer : ''

                if (this.user.photoURL !== this.imageUrl)
                {
                    axios.post(urlOnline, {image:  this.imageUrl, photoURLServer: photo,
                        userId: this.user.id ,validation_token: this.validation_token})
                        .then((response) =>{

                            this.photoURLServer = response.data.photoURLServer
                            return this.photoURLServer

                        })
                        .then((serverPhoto) => {

                            this.$store.dispatch('uploadPhoto', {image: this.image, photoURLServer:  serverPhoto})

                        })
                        .catch((error)=>{
                        console.log('Ocorreu um erro ao carregar a foto', error)
    //                        commit('setLoading',false)
    //                        commit('setError', error)
                        });
                }
                else
                {
                    console.log('mesma imagem nao precisamos de upload...')
                }
            },

            onDismissed () {
                this.$store.dispatch('clearError')
            },
            onHelp () {
                this.dialog = !this.dialog
                this.$router.push('/help')
            },
            onPickFile ()  {
                this.$refs.fileInput.click()
            },
            createImage(file) {
                let reader = new FileReader();
                let vm = this;
                reader.onload = (e) => {
                    vm.image = e.target.result;
                };
                reader.readAsDataURL(file);
            },
            onFilePicked (event) {
                const files = event.target.files
                this.file = event.target.files[0]

                //pegar aqui


                let filename = files[0].name
                if (filename.lastIndexOf('.') <= 0)
                {
                    return alert('Por favor adicione um ficheiro valido!')
                }
                const fileReader = new FileReader()
                fileReader.addEventListener('load', () => {
                    this.imageUrl = fileReader.result
                })
                fileReader.readAsDataURL(files[0])
                this.image = files[0]
            }
        }

    }
</script>


