<template>
    <v-dialog v-model="dialog" max-width="500px">

        <!--Botao de activacao fixa da pagina do perfil-->
        <v-btn
            class="mt-5 elevation-0 mr-0"
            :style="$vuetify.breakpoint.mdAndDown ? 'right: 8px;!important;' : 'right: 120px;!important;top: 30px;!important;'"
            title="Ver o contacto"
            v-if="onExplicadorTopProfile && !isMyProfile"
            fab
            top
            right
            color="blue"
            dark
            fixed
            small
            slot="activator"
        >
            <v-icon>phone</v-icon>
        </v-btn>
        <v-btn
            v-else=""
            flat=""
            style="margin-top: -5px"
            :icon="!canPersonalize"
            color="orange"
            slot="activator"
            @click.stop="dialog = !dialog"
            :round="canPersonalize"
            :block="canPersonalize"
            :outline="canPersonalize"
            >
            {{ canPersonalize ? 'Ver contacto do explicador' : '' }}
            <v-icon class="ml-3" :large="canPersonalize">phone</v-icon>
        </v-btn>


        <v-card>
            <!--Cabecalho da dialog-->
            <v-card-title class="headline pb-1 pt-1">
                Contacto do explicador <v-icon class="ml-3 hidden-xs-only" :medium="$vuetify.breakpoint.mdAndUp">phone</v-icon>

                <v-spacer></v-spacer>
                <v-btn
                   icon=""
                   title="Fechar a Tela"
                   @click.stop="dialog = !dialog">
                    <v-icon>close</v-icon>
                </v-btn>
            </v-card-title>

            <!--Separador-->
            <v-divider></v-divider>

            <v-container grid-list-sm>

                <!--Espaco de erros-->
                <v-layout row v-if="error">
                    <v-flex xs12>
                        <app-alert @dismissed="onDismissed" :text="error.message"></app-alert>
                    </v-flex>
                </v-layout>

                <!--Corpo da dialog-->
                <v-card-text>
                    <v-container grid-list-md>
                        <v-layout wrap>
                            <v-flex xs12>

                                <small v-if="!isUserAuth && !canShow">
                                    <b class="text-xs-center">
                                        <v-icon style="height: 4px">info</v-icon>
                                        Olha o {{ nomeUser }}
                                        gostaria de saber quem lhe procurou. Por isso vamos pedir o seu contacto primeiro!
                                    <a flat="" round="" title="Fazer Login">Ou faz login</a>
                                    </b>
                                </small>
                                <br v-if="!isUserAuth && !canShow">
                                <br v-if="!isUserAuth && !canShow">
                                <b class="text-xs-center">Termos e condições</b>

                                <br>
                                <br>

                                <p>* O contacto é apenas para o uso académico</p>
                                <p>* Não lhe é permitido usar o contacto do <b>{{  nomeUser }}</b> para outros assuntos diferentes de académicos</p>

                                <br>
                            </v-flex>

                            <!--Espaco com o botao-->
                            <v-flex xs12 sm12>

                                <!--<explicador-contact-requester-dialog v-if="!isUserAuth && !canShow" :user="user"></explicador-contact-requester-dialog>-->

                                <v-layout v-if="isUserAuth && !canShow">
                                    <v-flex xs12>
                                        <v-btn
                                                flat=""
                                                color="blue"
                                                round=""
                                                class=""
                                                :style="$vuetify.breakpoint.smAndDown ? 'height: 30px' : 'height: 35px'"
                                                small=""
                                                block=""
                                                title="Visualizar o contacto desse explicador"
                                                outline=""
                                                @click.stop="onSave()"
                                        >
                                            {{ !$vuetify.breakpoint.mdAndUp ? 'Ver contacto' : 'Ver o contacto do explicador' }}
                                            <v-icon class="ml-3" :small="$vuetify.breakpoint.smAndDown">phone</v-icon>
                                        </v-btn>
                                    </v-flex>
                                </v-layout>

                                <v-layout v-else-if="!isUserAuth">

                                    <v-flex xs12>

                                        <v-layout>
                                            <v-flex v-if="showInfo">
                                                <v-icon title="clique para saber mais"
                                                        style="cursor: pointer"
                                                        class="blue--text"
                                                   @click="showInfo = !showInfo">help</v-icon>
                                                Voçê não esta autenticado, entre ou crie a sua conta.
                                            </v-flex>
                                            <v-flex v-else=""
                                                title="fechar"
                                                @click="showInfo = !showInfo"
                                                xs12 class="pa-3"
                                                style="border-bottom-right-radius: 20px;border-top-left-radius: 20px; background: #EEE; cursor: pointer">
                                                <p style="font-size: 11pt;line-height: 20px; font-weight: normal">
                                                    O contacto dos explicadores é disponibilizado apenas para os Utilizadores identificados,
                                                    pedimos que faça <b>login</b> ou <b>crie a sua conta</b> para poder ter acesso ao contacto do <b>{{  nomeUser }}</b>.

                                                    <br>
                                                    Por dia voçê só pode ver o contacto de no máximo 3 explicadores.
                                                    Por isso te sugerimos que selecione bem os explicadores que desejas contactar.
                                                </p>
                                            </v-flex>
                                        </v-layout>

                                        <v-layout>
                                            <v-flex xs6>

                                                <v-layout>
                                                    <v-flex xs12>
                                                        <v-btn
                                                                router
                                                                :to="{ name: 'Login'}"
                                                                flat=""
                                                                block="">Entrar</v-btn>
                                                        <!--<a flat="" href="#/register" round="" title="Fazer Login">Registar-me</a>-->
                                                    </v-flex>
                                                </v-layout>

                                            </v-flex>
                                            <v-flex xs6>

                                                <v-layout>
                                                    <v-flex xs12>
                                                        <v-btn
                                                            router
                                                            :to="{ name: 'Register'}"
                                                            flat=""
                                                            block="">Registar-me</v-btn>
                                                        <!--<a flat="" href="#/register" round="" title="Fazer Login">Registar-me</a>-->
                                                    </v-flex>
                                                </v-layout>

                                            </v-flex>
                                        </v-layout>

                                    </v-flex>
                                </v-layout>


                            </v-flex>

                        </v-layout>


                    </v-container>

                </v-card-text>


            </v-container>

            <!--Contacto do explicador-->
            <v-container v-if="canShow" style="padding: 10px; font-weight: 100; font-size: 15pt">

                <b class="text-xs-center">Contactos:</b>
                <br>
                <br>
                <v-layout>
                    <v-flex xs12 class="ml-3">
                        <span class="ma-2">
                            <v-icon>person</v-icon>
                        </span>
                        <span class="ma-2">Nome:</span>
                        <span class="ma-2">{{ nomeUser }}</span>
                    </v-flex>
                </v-layout>
                <v-layout>
                    <v-flex xs12 class="ml-3">
                        <span class="ma-2">
                            <v-icon>mail</v-icon>
                        </span>
                        <span class="ma-2">Email:</span>
                        <span class="ma-2">{{ user.email }}</span>
                    </v-flex>
                </v-layout>
                <v-layout>
                    <v-flex xs12 class="" class="ml-3">
                        <span class="ma-2">
                            <v-icon>phone</v-icon>
                        </span>
                        <span class="ma-2">Telefone:</span>
                        <span class="ma-2">{{ user.telfone ? user.telfone : 'Ainda nao registou numero' }}</span>
                    </v-flex>
                </v-layout>
            </v-container>

            <!--Processamento-->
            <div v-else-if="isUserAuth || !canShow"
                 :style="showInfo && isUserAuth ? 'height: 155px' : showInfo ? 'height: 95px': 'height: 0px'">

                <v-btn
                        @click=""
                        block=""
                        icon=""
                        v-if="loading"
                        :disabled="loading"
                        :loading="loading"
                >
                    Gravar

                    <span slot="loader" class="custom-loader">
                        <v-icon light large="">cached</v-icon>
                    </span>
                </v-btn>

            </div>

            <div v-if="isUserAuth || canShow" style="height: 40px"></div>

            <v-container>
                <v-layout>
                    <v-flex class="ml-2">
                        <small v-if="!canShow">Ao clicar em ver contacto, voçê  concorda com os Termos</small>
                        <!--<div v-else="" style="height: 35px"></div>-->
                    </v-flex>
                </v-layout>
            </v-container>

            <div :style="showInfo ? 'height: 40px' : ''"></div>
            <div :style="canShow ? 'height: 80px' : ''"></div>

            <v-divider></v-divider>

            <!--Botoes do form-->
            <v-card-actions>
                <v-btn flat color="blue" @click="onHelp">Ajuda</v-btn>
                <v-spacer></v-spacer>
                <v-btn flat color="primary" @click="onCancel">Fechar</v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>
<script>
    import { eventBus } from '../../main'
    export default {
        props: ['user', 'canPersonalize', 'onExplicadorTopProfile', 'showContactDialogByUserProfile'],
        created () {
//            Escutando o evento emitido no ficheiro: dialog/ContactRequesterDialog.vue
//            eventBus.$on('saveContactRequest', (value) => {
//                this.onSave(value)
//            })

        },
        data () {
            return {

                dialog: false,
                selectedUserSubjects: [],
                saveOnce: true, //criado so para garantir que grava apenas uma vez.
                canShow: false,
                showInfo: true,
            }
        },
        computed: {
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            },
            userStore () {
                return this.$store.getters.user
            },
            userAuth () {
                return this.$store.getters.userAuth
            },
            isUserAuth () {
                return this.userAuth !== null && this.userAuth !== undefined
            },
            isUser () {
                return this.user !== null && this.user !== undefined
            },
            isUserStore () {
                return this.userStore !== null && this.userStore !== undefined
            },
            isMyProfile () {
                return (this.userIsAuth && this.isUser && this.user.id === this.userAuth.id)
                || (this.userIsAuth && this.isUserStore && this.userStore.id === this.userAuth.id)
            },
            nomeUser() {
                return this.isUser && this.user.name && this.user.name !== '' ? this.user.name : 'Ainda nao registou o nome'
            },


            subjects () {
                return this.$store.getters.getSubjects
            },

        },
        watch: {

            loading (value) {

                if (!value && !this.error) {

                    this.canShow = true

                }
            },
            dialog (value)
            {
                if (value) {
                    this.inicializeUserInfo()
                }
                else //Entao a dialog esta a fechar
                {
                    this.$store.dispatch('updateTheUserInArray', this.userAuth)
                    this.$store.dispatch('setLoading', false)
                }

            },
            showContactDialogByUserProfile (value)
            {
                this.dialog = value > 0 //Se o value for maior que 0 mostre a dialog

            }

        },
        methods: {
            onSave () {

                if (true) //verificando se foi recebido o object value que vem do filho
                {
                    // Gravando os dados que vem do filho:
                    let object = {
                        explicadorId: this.user.id,
                        explicadorName: this.user.name ? this.user.name : '',
                        explicadorEmail: this.user.email,
                        userRequesterId: this.userAuth.id,
                        userRequesterName: this.userAuth.name,
                        userRequesterEmail: this.userAuth.email,
                        userRequesterTelefone: this.userAuth.telefone ? this.userAuth.telefone : '',
                        created_at: new Date(),
                        updated_at: new Date(),
                        permited: true,
                    }
                    this.$store.dispatch('contactRequest', object)
                }
            },

            onCancel() {

                this.inicializeUserInfo()
                this.$store.dispatch('setLoading', false)
                this.dialog = false

            },

            getObjectNames (type, array) {
                let itemNames = []
                let objects = []


                    if (type === 'institutions')
                    {
                        objects = array !== undefined ? array : this.institutions
                    }
                    else if (type === 'faculties')
                    {
                        objects = array !== undefined ? array : this.faculties
                    }
                    else if (type === 'courses')
                    {
                        objects = array !== undefined ? array : this.courses
                    }
                    else if (type === 'departments')
                    {
                        objects = array !== undefined ? array : this.departments
                    }
                    if (type === 'subjects')
                    {
                        objects = array !== undefined ? array : this.subjects
                    }

                    objects.forEach(function (o) {

                        //buscando as disciplinas que explica
                        if (o.status_explain)
                        {
                            itemNames.push(o.name)
                        }

                    })

                return itemNames
            },

            subjectsObject () {

                let userSubjects = this.userAuth.Educations.subjects
                let finalSubject = []
                const vm = this

                //Implementar aqui a busca das disciplinas selecionadas pelo user
                // para depois se retornar a lista com todas disciplinas
                //
                this.selectedUserSubjects.forEach(function (selectSub) {

                    let sub = userSubjects.find(sub => {
                        return sub.name === selectSub
                    })

                    if (sub !== undefined && sub !== null ) {

                        if (!sub.status_explain)
                        {
                            sub.status_explain = true
                        }
                        finalSubject.push(sub) //essa que guardamos aqui, eh a disciplina do user

                    } else {

                        finalSubject.push({
                            name: selectSub,
                            status_explain: true,
                            status_like: true,
                            created_at: new Date(),
                        })
                    }

                })

                return finalSubject
            },


            onDismissed () {
                this.$store.dispatch('clearError')
                this.$store.dispatch('setLoading', false)
            },

            inicializeUserInfo () {

                if (!this.isUserAuth) return //se nao estiver autenticado nao precisamos de inicializar

                const education = this.isUserAuth ? this.userAuth.Educations : {}

//                Implementacao temporaria, ao mudar para id, mudar isso.
                this.selectedUserSubjects = education && education.subjects ? this.getObjectNames('subjects', education.subjects)  : ''

            },

            onHelp () {
                this.dialog = false
                this.$router.push('/help')
            }
        }
    }
</script>
<style>
    .dialog:not(.dialog--fullscreen) {
        max-height: 100%;
    }
    .icon {
        font-size: 15px;
    }
</style>


