<template>

    <v-container fluid>
        <v-card xs12 class="mb-1">
            <v-list class="text-xs-center">
                <!--<h4>Seie > Perfil > Dados Pessoais</h4>-->
                <v-breadcrumbs divider="/">
                    <v-breadcrumbs-item >
                        <v-btn round flat color="white" class="grey--text" router to="/">
                            Home
                        </v-btn>

                    </v-breadcrumbs-item>

                    <v-breadcrumbs-item >
                        {{ name }}
                    </v-breadcrumbs-item>

                    <v-breadcrumbs-item  disabled>
                        Profile
                    </v-breadcrumbs-item>
                </v-breadcrumbs>
            </v-list>
            <v-list></v-list>
        </v-card>

        <!--Componente com informacao pessoal-->
        <personal-info :user="user"></personal-info>

        <!--Componente com informacao academica-->
        <academic-info :user="user"></academic-info>

        <!--Componente com as informacoes de localizacao-->
        <location-info :user="user"></location-info>


    </v-container>

</template>

<script>
    export default {
        props: ['user', 'name']
    }
</script>


