<template>
    <v-dialog v-model="dialog" persistent max-width="500px">
        <!--Botao de activacao-->
        <v-btn
                small=""
                slot="activator"
                :color="isMobile ? '' : 'blue'"
                :flat="isMobile"
                @click.stop="dialog = !dialog"
        >
            Editar
            <v-icon>edit</v-icon>
        </v-btn>

        <!--Corpo principa da dialog-->
        <v-card>
            <!--Cabecalho da dialog-->
            <v-card-title class="headline">
                Disciplinas que explicas
                <v-spacer></v-spacer>
                <v-btn title="Fechar" @click="dialog = !dialog" class="mb-0 mt-0" icon=""><v-icon>close</v-icon></v-btn>
            </v-card-title>

            <!--Separador-->
            <v-divider></v-divider>

            <v-container grid-list-sm class="pa-4 mb-5">
                <v-layout row v-if="error">
                    <v-flex xs12>
                        <app-alert @dismissed="onDismissed" :text="error.message"></app-alert>
                    </v-flex>
                </v-layout>

                <v-card-text>
                    <v-container grid-list-md>
                        <v-layout wrap>

                            <!--Que niveis voce explica?  -->
                            <v-flex xs12>
                                <v-select
                                    label="Que niveis voçê explica?"
                                    multiple
                                    v-model="explainLevels"
                                    chips
                                    :items="studiesLevels"
                                >
                                    <template slot="selection" slot-scope="data">
                                        <v-chip
                                                close
                                                @input="data.parent.selectItem(data.item)"
                                                :selected="data.selected"
                                                class="chip--select-multi"
                                                :key="JSON.stringify(data.item)"
                                        >
                                            {{ data.item }}
                                        </v-chip>
                                    </template>
                                </v-select>
                            </v-flex>

                            <v-flex
                                class="red--text"
                                style="background: #fcffea"
                                x12
                                v-if="studiesLevels.length === 0" >
                                <h5>Importante!</h5>
                                <br>
                                <p>
                                    Olá, primeiro tens de registar as suas <b>informações académicas</b>!
                                </p>
                            </v-flex>

                            <!--Ensino Primario-->
                            <v-flex xs12 v-if="canShowPrimary">
                                <v-flex xs12 class="">
                                    <h3>Ensino Primário</h3>
                                </v-flex>

                                <v-flex xs12>
                                    <v-select
                                        multiple=""
                                        label="Quais sao as classes que voce explica?"
                                        v-model="explainPrimaryClasses"
                                        chips
                                        autocomplete
                                        :items="primaryClasses"
                                    >
                                        <template slot="selection" slot-scope="data">
                                            <v-chip
                                                    close
                                                    @input="data.parent.selectItem(data.item)"
                                                    :selected="data.selected"
                                                    class="chip--select-multi"
                                                    :key="JSON.stringify(data.item)"
                                            >
                                                {{ data.item }}
                                            </v-chip>
                                        </template>
                                    </v-select>
                                </v-flex>

                                <!--Classes selecionadas para o nivel primario-->
                                <v-flex xs12 v-for="(item, i) in explainPrimaryClasses" v-bind:key="item">
                                    <!--1ra classe-->
                                    <v-select
                                        v-if="item.includes('1')"
                                        multiple=""
                                        :label="'Que disciplinas explicas na ' + item + ' classe?'"
                                        v-model="selectedSubjectsClass1"
                                        autocomplete
                                        chips
                                        :items="primarySubjects1stDegree">
                                        <template slot="selection" slot-scope="data">
                                            <v-chip
                                                    close
                                                    @input="data.parent.selectItem(data.item)"
                                                    :selected="data.selected"
                                                    class="chip--select-multi"
                                                    :key="JSON.stringify(data.item)"
                                            >
                                                {{ data.item }}
                                            </v-chip>
                                        </template>
                                    </v-select>

                                    <!--2nda classe-->
                                    <v-select
                                            v-else-if="item.includes('2')"
                                            multiple=""
                                            :label="'Que disciplinas explicas na ' + item + ' classe?'"
                                            v-model="selectedSubjectsClass2"
                                            chips
                                            autocomplete
                                            :items="primarySubjects1stDegree">
                                        <template slot="selection" slot-scope="data">
                                            <v-chip
                                                    close
                                                    @input="data.parent.selectItem(data.item)"
                                                    :selected="data.selected"
                                                    class="chip--select-multi"
                                                    :key="JSON.stringify(data.item)"
                                            >
                                                {{ data.item }}
                                            </v-chip>
                                        </template>
                                    </v-select>

                                    <!--3ra classe-->
                                    <v-select
                                            v-else-if="item.includes('3')"
                                            multiple=""
                                            :label="'Que disciplinas explicas na ' + item + ' classe?'"
                                            v-model="selectedSubjectsClass3"
                                            chips
                                            autocomplete
                                            :items="primaryEP1Subjects">
                                        <template slot="selection" slot-scope="data">
                                            <v-chip
                                                    close
                                                    @input="data.parent.selectItem(data.item)"
                                                    :selected="data.selected"
                                                    class="chip--select-multi"
                                                    :key="JSON.stringify(data.item)"
                                            >
                                                {{ data.item }}
                                            </v-chip>
                                        </template>
                                    </v-select>

                                    <!--4rta classe-->
                                    <v-select
                                            v-else-if="item.includes('4')"
                                            multiple=""
                                            :label="'Que disciplinas explicas na ' + item + ' classe?'"
                                            v-model="selectedSubjectsClass4"
                                            chips
                                            autocomplete
                                            :items="primaryEP1Subjects">
                                        <template slot="selection" slot-scope="data">
                                            <v-chip
                                                    close
                                                    @input="data.parent.selectItem(data.item)"
                                                    :selected="data.selected"
                                                    class="chip--select-multi"
                                                    :key="JSON.stringify(data.item)"
                                            >
                                                {{ data.item }}
                                            </v-chip>
                                        </template>
                                    </v-select>

                                    <!--5nta classe-->
                                    <v-select
                                            v-else-if="item.includes('5')"
                                            multiple=""
                                            :label="'Que disciplinas explicas na ' + item + ' classe?'"
                                            v-model="selectedSubjectsClass5"
                                            chips
                                            autocomplete
                                            :items="primaryEP1Subjects">
                                        <template slot="selection" slot-scope="data">
                                            <v-chip
                                                    close
                                                    @input="data.parent.selectItem(data.item)"
                                                    :selected="data.selected"
                                                    class="chip--select-multi"
                                                    :key="JSON.stringify(data.item)"
                                            >
                                                {{ data.item }}
                                            </v-chip>
                                        </template>
                                    </v-select>

                                    <!--6xta classe-->
                                    <v-select
                                        v-else-if="item.includes('6')"
                                        multiple=""
                                        :label="'Que disciplinas explicas na ' + item + ' classe?'"
                                        v-model="selectedSubjectsClass6"
                                        chips
                                        autocomplete
                                        :items="primarySubjects2ndDegree">
                                        <template slot="selection" slot-scope="data">
                                            <v-chip
                                                    close
                                                    @input="data.parent.selectItem(data.item)"
                                                    :selected="data.selected"
                                                    class="chip--select-multi"
                                                    :key="JSON.stringify(data.item)"
                                            >
                                                {{ data.item }}
                                            </v-chip>
                                        </template>
                                    </v-select>

                                    <!--7tima classe-->
                                    <v-select
                                         v-else-if="item.includes('7')"
                                         multiple=""
                                         :label="'Que disciplinas explicas na ' + item + ' classe?'"
                                         v-model="selectedSubjectsClass7"
                                         chips
                                         autocomplete
                                         :items="primarySubjects2ndDegree">
                                        <template slot="selection" slot-scope="data">
                                            <v-chip
                                                    close
                                                    @input="data.parent.selectItem(data.item)"
                                                    :selected="data.selected"
                                                    class="chip--select-multi"
                                                    :key="JSON.stringify(data.item)"
                                            >
                                                {{ data.item }}
                                            </v-chip>
                                        </template>
                                    </v-select>

                                </v-flex>

                            </v-flex>

                            <!--Ensino secundario: explainSecundaryClasses -->
                            <v-flex xs12 v-if="canShowSecundary">
                                <v-flex xs12 class="">
                                    <h3>Ensino Secundário</h3>
                                </v-flex>

                                <v-flex xs12>
                                    <v-select
                                            multiple=""
                                            label="Quais sao as classes que voce explica no nivel secundario?"
                                            v-model="explainSecundaryClasses"
                                            chips
                                            autocomplete
                                            :items="secundaryClasses"
                                    >
                                        <template slot="selection" slot-scope="data">
                                            <v-chip
                                                    close
                                                    @input="data.parent.selectItem(data.item)"
                                                    :selected="data.selected"
                                                    class="chip--select-multi"
                                                    :key="JSON.stringify(data.item)"
                                            >
                                                {{ data.item }}
                                            </v-chip>
                                        </template>
                                    </v-select>
                                </v-flex>

                                <v-flex xs12 v-for="(item, i) in explainSecundaryClasses" v-bind:key="item">
                                    <!--8va classe-->
                                    <v-select
                                            v-if="item.includes('8')"
                                            multiple=""
                                            :label="'Que disciplinas explicas na ' + item + ' classe?'"
                                            v-model="selectedSubjectsClass8"
                                            chips
                                            autocomplete
                                            :items="secundarySubjects">
                                        <template slot="selection" slot-scope="data">
                                            <v-chip
                                                    close
                                                    @input="data.parent.selectItem(data.item)"
                                                    :selected="data.selected"
                                                    class="chip--select-multi"
                                                    :key="JSON.stringify(data.item)"
                                            >
                                                {{ data.item }}
                                            </v-chip>
                                        </template>
                                    </v-select>

                                    <!--9na classe-->
                                    <v-select
                                            v-if="item.includes('9')"
                                            multiple=""
                                            :label="'Que disciplinas explicas na ' + item + ' classe?'"
                                            v-model="selectedSubjectsClass9"
                                            chips
                                            autocomplete
                                            :items="secundarySubjects">
                                        <template slot="selection" slot-scope="data">
                                            <v-chip
                                                    close
                                                    @input="data.parent.selectItem(data.item)"
                                                    :selected="data.selected"
                                                    class="chip--select-multi"
                                                    :key="JSON.stringify(data.item)"
                                            >
                                                {{ data.item }}
                                            </v-chip>
                                        </template>
                                    </v-select>

                                    <!--10ma classe-->
                                    <v-select
                                            v-if="item.includes('10')"
                                            multiple=""
                                            :label="'Que disciplinas explicas na ' + item + ' classe?'"
                                            v-model="selectedSubjectsClass10"
                                            chips
                                            autocomplete
                                            :items="secundarySubjects">
                                        <template slot="selection" slot-scope="data">
                                            <v-chip
                                                    close
                                                    @input="data.parent.selectItem(data.item)"
                                                    :selected="data.selected"
                                                    class="chip--select-multi"
                                                    :key="JSON.stringify(data.item)"
                                            >
                                                {{ data.item }}
                                            </v-chip>
                                        </template>
                                    </v-select>

                                    <!--11ra classe-->
                                    <v-select
                                            v-if="item.includes('11')"
                                            multiple=""
                                            :label="'Que disciplinas explicas na ' + item + ' classe?'"
                                            v-model="selectedSubjectsClass11"
                                            chips
                                            autocomplete
                                            :items="secundarySubjects">
                                        <template slot="selection" slot-scope="data">
                                            <v-chip
                                                    close
                                                    @input="data.parent.selectItem(data.item)"
                                                    :selected="data.selected"
                                                    class="chip--select-multi"
                                                    :key="JSON.stringify(data.item)"
                                            >
                                                {{ data.item }}
                                            </v-chip>
                                        </template>
                                    </v-select>

                                    <!--12nda classe-->
                                    <v-select
                                            v-if="item.includes('12')"
                                            multiple=""
                                            :label="'Que disciplinas explicas na ' + item + ' classe?'"
                                            v-model="selectedSubjectsClass12"
                                            chips
                                            autocomplete
                                            :items="secundarySubjects">
                                        <template slot="selection" slot-scope="data">
                                            <v-chip
                                                close
                                                @input="data.parent.selectItem(data.item)"
                                                :selected="data.selected"
                                                class="chip--select-multi"
                                                :key="JSON.stringify(data.item)"
                                            >
                                                {{ data.item }}
                                            </v-chip>
                                        </template>
                                    </v-select>


                                </v-flex>

                            </v-flex>

                            <!--Ensino tecnico-->
                            <v-flex xs12 v-if="canShowTecnical">
                                <v-flex xs12 class="">
                                    <h3>Ensino Técnico</h3>
                                </v-flex>

                                <v-flex xs12>
                                    <v-select
                                            multiple=""
                                            label="Quais são os niveis explicas no ensino Tecnico?"
                                            v-model="explainTecnicalClasses"
                                            chips
                                            :items="tecnicalDegrees"
                                    ></v-select>
                                </v-flex>

                                <!-- //Niveis do ensino Tecnico-->
                                <v-flex xs12 v-for="(item, i) in explainTecnicalClasses" v-bind:key="item">
                                    <!--nivel elementar-->
                                    <v-select
                                            :title="'Nivel ' + item"
                                            v-if="item.includes('Elementar')"
                                            multiple=""
                                            autocomplete
                                            :label="'Que disciplinas explicas no nivel ' + item + '?'"
                                            v-model="selectedElementarTecnicalSubjects"
                                            chips
                                            closable
                                            :items="tecnicalSubjects"></v-select>

                                    <!--Nivel Basico-->
                                    <v-select
                                            :title="'Nivel ' + item"
                                            v-if="item.includes('Básico')"
                                            multiple=""
                                            autocomplete
                                            :label="'Que disciplinas explicas no nivel ' + item + '?'"
                                            v-model="selectedBasicTecnicalSubjects"
                                            chips
                                            :items="tecnicalSubjects"></v-select>

                                    <!--Nivel Medio-->
                                    <v-select
                                            :title="'Nivel ' + item"
                                            v-if="item.includes('Médio')"
                                            multiple=""
                                            autocomplete
                                            :label="'Que disciplinas explicas no nivel ' + item + '?'"
                                            v-model="selectedMediumTecnicalSubjects"
                                            chips
                                            :items="tecnicalSubjects"></v-select>

                                </v-flex>
                            </v-flex>

                            <!--Ensino Superior-->
                            <v-flex xs12 v-if="canShowMaster">
                                <v-flex xs12 class="">
                                    <h3>Ensino Superior</h3>
                                </v-flex>

                                <v-flex xs12>
                                    <v-select
                                            multiple=""
                                            title="Que disciplinas do ensino superior explicas?"
                                            label="Que disciplinas do ensino superior explicas?"
                                            v-model="selectedMasterSubjects"
                                            chips
                                            autocomplete
                                            :items="universitySubjects"
                                    >
                                        <template slot="selection" slot-scope="data">
                                            <v-chip
                                                    close
                                                    @input="data.parent.selectItem(data.item)"
                                                    :selected="data.selected"
                                                    class="chip--select-multi"
                                                    :key="JSON.stringify(data.item)"
                                            >
                                                {{ data.item }}
                                            </v-chip>
                                        </template>
                                    </v-select>
                                </v-flex>
                            </v-flex>

                            <!--Outras disciplinas-->
                            <v-flex xs12 v-if="canShowOther">
                                <v-flex xs12 class="">
                                    <h3>Outras Disciplinas</h3>
                                </v-flex>

                                <v-flex xs12>
                                    <v-select
                                            multiple=""
                                            autocomplete
                                            label="Que outras disciplinas explicas?"
                                            v-model="selectedOtherSubjects"
                                            chips
                                            :items="otherSubjects"
                                    ></v-select>
                                </v-flex>
                            </v-flex>

                        </v-layout>

                        <v-layout v-if="explainLevels.length">
                            <v-flex xs12>
                                <small>Se Alguma informação estiver em falta, clique no link abaixo 'Sugerir registo' para nos informar!</small>
                                <br>
                                <a title="Clique para preencher a disciplina" target="_blank" href="https://docs.google.com/forms/d/e/1FAIpQLSerGzBmhk9hdPUgC9awFRP6bdNETUtfo5rUG8MuywPmqtWIVA/viewform?usp=sf_link">
                                    Sugerir registo
                                </a>
                            </v-flex>
                        </v-layout>
                    </v-container>

                </v-card-text>

            </v-container>

            <v-divider></v-divider>

            <v-card-actions>
                <v-btn flat color="primary" @click="onHelp">Ajuda</v-btn>
                <v-spacer></v-spacer>
                <v-btn flat color="primary" @click="onCancel()">Fechar</v-btn>
                <v-btn
                    flat
                    @click="onSave"
                    class="green--text"
                    :disabled="loading"
                    :loading="loading"
                >
                    Salvar

                    <span slot="loader" class="custom-loader">
                        <v-icon light>cached</v-icon>
                    </span>
                </v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>
<script>
    import { eventBus } from '../../main'
    export default {
        props: ['user', 'isMobile', 'showSubjectsEditDialog'],
        created() {

        },
        data () {
            return {

                dialog: false,
                explainLevels: [],
                explainPrimaryClasses: [],
                explainSecundaryClasses: [],
                explainTecnicalClasses: [],
                details: '',
                selectedUserSubjects: [],
                selectedSubjectsClass1: [], //disciplinas seleciondas (explicadas) da 1ra classe.
                selectedSubjectsClass2: [], //disciplinas seleciondas (explicadas) da 2ra classe.
                selectedSubjectsClass3: [],
                selectedSubjectsClass4: [],
                selectedSubjectsClass5: [],
                selectedSubjectsClass6: [],
                selectedSubjectsClass7: [],
                selectedSubjectsClass8: [],
                selectedSubjectsClass9: [],
                selectedSubjectsClass10: [],
                selectedSubjectsClass11: [],
                selectedSubjectsClass12: [],
                selectedElementarTecnicalSubjects: [], //Disciplinas Elementar do nivel Tecnico
                selectedBasicTecnicalSubjects: [], //Disciplinas Basicas do nivel Tecnico
                selectedMediumTecnicalSubjects: [], //Disciplinas Medias do nivel Tecnico
                selectedMasterSubjects: [], //disciplinas que o explicador explica no nivel superior
                selectedOtherSubjects: [], //disciplinas que o explicador explica no nivel outro
                showAcademicProcess: false,
                canceled: false,

            }
        },
        computed: {
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            },
            userAuth () {
                return this.$store.getters.userAuth
            },
            isUserAuth () {
                return this.userAuth !== null && this.userAuth !== undefined
            },

            studiesLevels () {
                let levels = this.$store.getters.getStudiesLevels
                let userLevel = this.userAuth.Educations && this.userAuth.Educations.level ? this.userAuth.Educations.level : '' //Aqui nos assumimos que ele ja actualizou o nivel
                levels = (userLevel === '') ? [] : levels
                levels = (userLevel === 'Primário') ? ['Primário'] : levels
                levels = (userLevel === 'Secundário') ? ['Primário','Secundário'] : levels
                return levels
            },

            primaryClasses () {
                return this.$store.getters.getPrimaryClasses
            },

            secundaryClasses () {
                return this.$store.getters.getSecundaryClasses
            },


            primarySubjects1stDegree () {
                return this.$store.getters.getPrimarySubjects1stDegree
            },

            primarySubjects2ndDegree () {
                return this.$store.getters.getPrimarySubjects2ndDegree
            },
            primaryEP1Subjects ()
            {
                return this.$store.getters.getPrimaryEP1Subjects
            },

            secundarySubjects () {
                return this.$store.getters.getSecundarySubjects
            },

            tecnicalSubjects () {
                return this.$store.getters.getTecnicalSubjects
            },

            otherSubjects () {
                return this.$store.getters.getOtherSubjects
            },

            universitySubjects () {
                return this.$store.getters.getInternal_APISubjects
            },

            tecnicalDegrees () {
                return this.$store.getters.getTecnicalDegrees
            },

            canShowPrimary ()  {
                return this.explainLevels.includes('Primário');
            },

            canShowSecundary ()  {
                return this.explainLevels.includes('Secundário');
            },

            canShowTecnical ()  {
                return this.explainLevels.includes('Técnico');
            },

            canShowMaster ()  {
                return this.explainLevels.includes('Superior');
            },

            canShowOther ()  {
                return this.explainLevels.includes('Outro');
            },

            isUser () {
                return this.user !== null && this.user !== undefined
            },
            isMyProfile () {
                return this.userIsAuth && this.isUser && this.user.id === this.userAuth.id
            }
        },
        watch: {

            explainLevels (value)
            {

                if (!this.explainLevels.includes('Primário'))
                {
                    this.resetPrimary()
                }
                if (!this.explainLevels.includes('Secundário'))
                {
                    this.resetSecundary()
                }
                if (!this.explainLevels.includes('Técnico'))
                {
                    this.resetTecnical()
                }
                if (!this.explainLevels.includes('Superior'))
                {
                    this.resetMaster()
                }
                if (!this.explainLevels.includes('Outro'))
                {
                    this.resetOther()
                }
            },
            loading (value) {

                if (!value && !this.error && this.showAcademicProcess) {

                    let text = "Disciplinas actaulizadas com sucesso!"
                    let color = 'green'
                    eventBus.$emit('showSnackbar', text, color, 5000); //Esse evento eh tratado no ficheiro App.vue, eh la onde foi montado o componente snackbar
                    this.showAcademicProcess = false
                    this.dialog = false
                }
            },
            dialog (value)
            {
                if (value) {
                    this.inicializeUserInfo()
                }
                else //Entao a dialog esta a fechar
                {
                    this.$store.dispatch('updateTheUserInArray', this.userAuth)
                    this.$store.dispatch('setLoading', false)
                    eventBus.$emit('resetShowDialogSujects')
                }
            },
            showSubjectsEditDialog (value)
            {
                if (value)
                {
                    this.dialog = true
                }
            },

        },
        methods: {

            onSave() {

                let user = this.userAuth

                let subjectDetails = user.Explicador ? user.Explicador.subjectDetails : []

//               Chamando os 3 metodos que fazem a validacao no 3 niveis
                let validacao1 = this.validateNivelSelection()
                let validacao2 = this.validateClassSelection()
                let validacao3 = this.validateSubjectsSelection()
                let validacao4 = this.validateMaximumSelection()

                let validation = validacao1.length ? validacao1
                    : validacao2.length ? validacao2
                    : validacao3.length ? validacao3
                    : validacao4

                if (validation.length) //Validacao, caso os dados nao forem validos, nao passa.
                {
                    eventBus.$emit('showSnackbar', validation, 'red', 5000);
                    return
                }

                this.concludeSave(user, subjectDetails) //vai esses dados se tudo for valido

            },

            concludeSave (user, subjectDetails)
            {
                let today = new Date()
                user.Explicador = {
                    explainLevels: this.explainLevels,
                    explainPrimaryClasses: this.explainPrimaryClasses,
                    explainSecundaryClasses: this.explainSecundaryClasses,
                    explainTecnicalClasses: this.explainTecnicalClasses,
                    subjectDetails: this.saveNewSubjectDetails(subjectDetails), //esse eh um array [] de detalhes para cada classe.
                    created_at: user.Explicador ? user.Explicador.created_at : today,
                    updated_at: today
                }

                this.showAcademicProcess = true
//                return
                this.$store.dispatch('updateOnlineUserData', user)
                this.$emit('verifyUserProfile', user)
                // Chamar o metodo que vai gravar as disciplinas
                this.saveNewSubjectsToDB()
            },

//          Verifica se foi selecionado algum nivel de explicacao
            validateNivelSelection() {
                return (this.explainLevels.length === 0) ? 'Que niveis voce explica? selecione os niveis primeiro.' : ''
            },

//          Verifica se foram selecionadas as classe
            validateClassSelection() {
                return (this.explainLevels.includes('Primário') && this.explainPrimaryClasses.length === 0) ? 'Selecione as Classes que explicas no ensino Primário'
                    : (this.explainLevels.includes('Secundário') && this.explainSecundaryClasses.length === 0) ? 'Selecione as Classes que explicas no ensino Secundário'
                    : (this.explainLevels.includes('Técnico') && this.explainTecnicalClasses.length === 0) ? 'Selecione os niveis que explicas no ensino Técnico'
                    : ''
            },

            // Verifica se alguma disciplina foi selecionada
            validateSubjectsSelection() {
                return (this.explainPrimaryClasses.includes('1º') && this.selectedSubjectsClass1.length === 0) ? 'Selecione as disciplinas que explicas na 1º classe'
                        : (this.explainPrimaryClasses.includes('2º') && this.selectedSubjectsClass2.length === 0) ? 'Selecione as disciplinas que explicas na 2º classe'
                        : (this.explainPrimaryClasses.includes('3º') && this.selectedSubjectsClass3.length === 0) ? 'Selecione as disciplinas que explicas na 3º classe'
                        : (this.explainPrimaryClasses.includes('4º') && this.selectedSubjectsClass4.length === 0) ? 'Selecione as disciplinas que explicas na 4º classe'
                        : (this.explainPrimaryClasses.includes('5º') && this.selectedSubjectsClass5.length === 0) ? 'Selecione as disciplinas que explicas na 5º classe'
                        : (this.explainPrimaryClasses.includes('6º') && this.selectedSubjectsClass6.length === 0) ? 'Selecione as disciplinas que explicas na 6º classe'
                        : (this.explainPrimaryClasses.includes('7º') && this.selectedSubjectsClass7.length === 0) ? 'Selecione as disciplinas que explicas na 7º classe'
                        : (this.explainSecundaryClasses.includes('8º') && this.selectedSubjectsClass8.length === 0) ? 'Selecione as disciplinas que explicas na 8º classe'
                        : (this.explainSecundaryClasses.includes('9º') && this.selectedSubjectsClass9.length === 0) ? 'Selecione as disciplinas que explicas na 9º classe'
                        : (this.explainSecundaryClasses.includes('10º') && this.selectedSubjectsClass10.length === 0) ? 'Selecione as disciplinas que explicas na 10º classe'
                        : (this.explainSecundaryClasses.includes('11º') && this.selectedSubjectsClass11.length === 0) ? 'Selecione as disciplinas que explicas na 11º classe'
                        : (this.explainSecundaryClasses.includes('12º') && this.selectedSubjectsClass12.length === 0) ? 'Selecione as disciplinas que explicas na 12º classe'
                        : (this.explainTecnicalClasses.includes('Elementar') && this.selectedElementarTecnicalSubjects.length === 0) ? 'Selecione as disciplinas que explicas no nivel Elementar'
                        : (this.explainTecnicalClasses.includes('Básico') && this.selectedBasicTecnicalSubjects.length === 0) ? 'Selecione as disciplinas que explicas no nivel Básico'
                        : (this.explainTecnicalClasses.includes('Médio') && this.selectedMediumTecnicalSubjects.length === 0) ? 'Selecione as disciplinas que explicas no nivel Médio'
                        : (this.explainLevels.includes('Superior') && this.selectedMasterSubjects.length === 0) ? 'Selecione as disciplinas que explicas no ensino Superior'
                        : (this.explainLevels.includes('Outro') && this.selectedOtherSubjects.length === 0) ? 'Ainda não selecionaste as outras disciplinas que explicas'
                        : ''
            },

            getTotalSelectedSubjects ()
            {
                return this.selectedUserSubjects.length +
                this.selectedSubjectsClass1.length +
                this.selectedSubjectsClass2.length +
                this.selectedSubjectsClass3.length +
                this.selectedSubjectsClass4.length +
                this.selectedSubjectsClass5.length +
                this.selectedSubjectsClass6.length +
                this.selectedSubjectsClass7.length +
                this.selectedSubjectsClass8.length +
                this.selectedSubjectsClass9.length +
                this.selectedSubjectsClass10.length +
                this.selectedSubjectsClass11.length +
                this.selectedSubjectsClass12.length +
                this.selectedElementarTecnicalSubjects.length +
                this.selectedBasicTecnicalSubjects.length +
                this.selectedMediumTecnicalSubjects.length +
                this.selectedMasterSubjects.length +
                this.selectedOtherSubjects

            },

            // Verifica se alguma disciplina foi selecionada
            validateMaximumSelection() {
                let maxLevels = 5
                let maxSubjects = 40
                let qtdSelected = this.getTotalSelectedSubjects()

                return (this.explainLevels.length > maxLevels) ? 'Não é possivel gravar mais de 3 niveis a não ser que sejas um Explicador Pro'
                    : (qtdSelected > maxSubjects) ? 'Apenas os explicadores Pro, podem explicar mais de 10 disciplinas, ' +
                        'voce selecionou: ' + qtdSelected + ', enquanto que o seu Maximo de disciplinas é 10'
                    : ''
            },

            saveNewSubjectDetails(previousSubjectDetails) {
                let subjsDetailsArray = []

                if (this.explainLevels.includes('Primário')) {
                    subjsDetailsArray = this.primaryExplainDetails(previousSubjectDetails)
                }
                if (this.explainLevels.includes('Secundário')) {
                    subjsDetailsArray = this.secundaryExplainDetails(previousSubjectDetails, subjsDetailsArray)
                }
                if (this.explainLevels.includes('Técnico')) {
                    subjsDetailsArray = this.tecnicalExplainDetails(previousSubjectDetails, subjsDetailsArray)
                }
                if (this.explainLevels.includes('Superior')) {
                    subjsDetailsArray = this.masterExplainDetails(previousSubjectDetails, subjsDetailsArray)
                }
                if (this.explainLevels.includes('Outro')) {
                    subjsDetailsArray = this.otherExplainDetails(previousSubjectDetails, subjsDetailsArray)
                }

                return subjsDetailsArray
            },

            primaryExplainDetails(previousSubjectDetails) {
                let subjsDetailsArray = []
                const nivel = 'Primário'

                if (this.explainPrimaryClasses.includes('1º')) {

                    subjsDetailsArray.push(this.defaultGetDetailsForClasses(previousSubjectDetails, nivel, '1º', this.selectedSubjectsClass1))

                }
                if (this.explainPrimaryClasses.includes('2º')) {

                    subjsDetailsArray.push(this.defaultGetDetailsForClasses(previousSubjectDetails, nivel, '2º', this.selectedSubjectsClass2))

                }
                if (this.explainPrimaryClasses.includes('3º')) {

                    subjsDetailsArray.push(this.defaultGetDetailsForClasses(previousSubjectDetails, nivel, '3º', this.selectedSubjectsClass3))

                }
                if (this.explainPrimaryClasses.includes('4º')) {

                    subjsDetailsArray.push(this.defaultGetDetailsForClasses(previousSubjectDetails, nivel, '4º', this.selectedSubjectsClass4))

                }
                if (this.explainPrimaryClasses.includes('5º')) {

                    subjsDetailsArray.push(this.defaultGetDetailsForClasses(previousSubjectDetails, nivel, '5º', this.selectedSubjectsClass5))

                }
                if (this.explainPrimaryClasses.includes('6º')) {

                    subjsDetailsArray.push(this.defaultGetDetailsForClasses(previousSubjectDetails, nivel, '6º', this.selectedSubjectsClass6))

                }
                if (this.explainPrimaryClasses.includes('7º')) {

                    subjsDetailsArray.push(this.defaultGetDetailsForClasses(previousSubjectDetails, nivel, '7º', this.selectedSubjectsClass7))

                }

                return subjsDetailsArray
            },

            secundaryExplainDetails(previousSubjectDetails, newArray) {
                let subjsDetailsArray = newArray
                const nivel = 'Secundário'

                if (this.explainSecundaryClasses.includes('8º')) {

                    subjsDetailsArray.push(this.defaultGetDetailsForClasses(previousSubjectDetails, nivel, '8º', this.selectedSubjectsClass8))

                }
                if (this.explainSecundaryClasses.includes('9º')) {

                    subjsDetailsArray.push(this.defaultGetDetailsForClasses(previousSubjectDetails, nivel, '9º', this.selectedSubjectsClass9))

                }
                if (this.explainSecundaryClasses.includes('10º')) {

                    subjsDetailsArray.push(this.defaultGetDetailsForClasses(previousSubjectDetails, nivel, '10º', this.selectedSubjectsClass10))

                }
                if (this.explainSecundaryClasses.includes('11º')) {

                    subjsDetailsArray.push(this.defaultGetDetailsForClasses(previousSubjectDetails, nivel, '11º', this.selectedSubjectsClass11))

                }
                if (this.explainSecundaryClasses.includes('12º')) {

                    subjsDetailsArray.push(this.defaultGetDetailsForClasses(previousSubjectDetails, nivel, '12º', this.selectedSubjectsClass12))

                }


                return subjsDetailsArray
            },

            tecnicalExplainDetails(previousSubjectDetails, newArray) {
                let subjsDetailsArray = newArray
                const nivel = 'Técnico'

                if (this.explainTecnicalClasses.includes('Elementar')) {

                    subjsDetailsArray.push(this.defaultGetDetailsForClasses(previousSubjectDetails, nivel, 'Elementar', this.selectedElementarTecnicalSubjects))

                }

                if (this.explainTecnicalClasses.includes('Básico')) {

                    subjsDetailsArray.push(this.defaultGetDetailsForClasses(previousSubjectDetails, nivel, 'Básico', this.selectedBasicTecnicalSubjects))

                }

                if (this.explainTecnicalClasses.includes('Médio')) {

                    subjsDetailsArray.push(this.defaultGetDetailsForClasses(previousSubjectDetails, nivel, 'Médio', this.selectedMediumTecnicalSubjects))

                }


                return subjsDetailsArray
            },

            masterExplainDetails(previousSubjectDetails, newArray) {
                let subjsDetailsArray = newArray
                const nivel = 'Superior'

                if (this.explainLevels.includes('Superior')) {

                    subjsDetailsArray.push(this.defaultGetDetailsForClasses(previousSubjectDetails, nivel, 'Superior', this.selectedMasterSubjects))

                }

                return subjsDetailsArray
            },

            otherExplainDetails(previousSubjectDetails, newArray) {
                let subjsDetailsArray = newArray
                const nivel = 'Outro'

                if (this.explainLevels.includes('Outro')) {

                    subjsDetailsArray.push(this.defaultGetDetailsForClasses(previousSubjectDetails, nivel, 'Outro', this.selectedOtherSubjects))

                }

                return subjsDetailsArray
            },

            // Busca detalhes da disciplina explicada.
            // Todos os metodos chamam esse faz de forma generica.
            defaultGetDetailsForClasses(previousSubjectDetails, nivel, classe, disciplinas) {

                const today = new Date()
                let objectClass = previousSubjectDetails.find((subject) => {
                    return subject.classe === classe
                })

                if (objectClass) {
                    objectClass.subjects = disciplinas
                    objectClass.updated_at = today
                }
                else {
                    objectClass = {
                        classe: classe,
                        level: nivel,
                        subjects: disciplinas,
                        created_at: today,
                        updated_at: today,
                    }
                }
                return objectClass
            },

            saveNewSubjectsToDB() {

                let vm = this
                this.selectedUserSubjects.forEach(function (selectSub) {

                    // verificar se eh uma nova disciplina
//                    if (selectSub)
//                    vm.$store.dispatch('saveSubject', {
//                        name: selectSub,
//                        creatorId: vm.userAuth.id,
//                        created_at: new Date(),
//                        confirmed: false
//                    })
                })


            },

            onCancel() {
                this.dialog = false
                this.inicializeUserInfo()
                this.$store.dispatch('setLoading', false)
                this.canceled = true
            },

            onDismissed() {
                this.$store.dispatch('clearError')
                this.$store.dispatch('setLoading', false)
            },

            inicializeUserInfo() {

                if (!this.isUserAuth) return //se nao estiver autenticado nao precisamos de inicializar

                const explicador = this.isUserAuth ? this.userAuth.Explicador : {}

                if (!explicador) return //Se ele ainda nao tiver registado as disciplinas, nao inicializamos

                this.explainLevels = explicador.explainLevels
                this.explainPrimaryClasses = explicador.explainPrimaryClasses
                this.explainSecundaryClasses = explicador.explainSecundaryClasses
                this.explainTecnicalClasses = explicador.explainTecnicalClasses

                if (this.explainLevels.includes('Primário')) {

                    this.inicializePrimarySubjects(explicador.subjectDetails)

                }
                if (this.explainLevels.includes('Secundário')) {

                    this.inicializeSecundarySubjects(explicador.subjectDetails)

                }
                if (this.explainLevels.includes('Técnico')) {

                    this.inicializeTecnicalSubjects(explicador.subjectDetails)

                }
                if (this.explainLevels.includes('Superior')) {

                    this.inicializeMasterSubjects(explicador.subjectDetails)

                }
                if (this.explainLevels.includes('Outro')) {

                    this.inicializeOtherSubjects(explicador.subjectDetails)

                }


            },

            resetPrimary()
            {
                this.selectedSubjectsClass1 = []
                this.selectedSubjectsClass2 = []
                this.selectedSubjectsClass3 = []
                this.selectedSubjectsClass4 = []
                this.selectedSubjectsClass5 = []
                this.selectedSubjectsClass6 = []
                this.selectedSubjectsClass7 = []
                this.explainPrimaryClasses = []

            },
            resetSecundary()
            {
                this.selectedSubjectsClass8 = []
                this.selectedSubjectsClass9 = []
                this.selectedSubjectsClass10 = []
                this.selectedSubjectsClass11 = []
                this.selectedSubjectsClass12 = []
                this.explainSecundaryClasses = []

            },
            resetTecnical()
            {
                this.selectedElementarTecnicalSubjects = []
                this.selectedBasicTecnicalSubjects = []
                this.selectedMediumTecnicalSubjects = []
                this.explainTecnicalClasses = []
            },
            resetMaster()
            {
                this.selectedMasterSubjects = []
            },
            resetOther()
            {
                this.selectedOtherSubjects = []
            },

            //Inicializa as disciplinas da primaria
            inicializePrimarySubjects(subjectDetails)
            {
                subjectDetails.forEach((explainedSubject) => {

                    // O else estraga tudo, pois quando nao eh aquela disciplina atribui [], temos de ter if para apenas enviar se for, se nao for nao enviar nada.
                    this.selectedSubjectsClass1 = (explainedSubject.classe === '1º') ? explainedSubject.subjects : this.selectedSubjectsClass1
                    this.selectedSubjectsClass2 = (explainedSubject.classe === '2º') ? explainedSubject.subjects : this.selectedSubjectsClass2
                    this.selectedSubjectsClass3 = (explainedSubject.classe === '3º') ? explainedSubject.subjects : this.selectedSubjectsClass3
                    this.selectedSubjectsClass4 = (explainedSubject.classe === '4º') ? explainedSubject.subjects : this.selectedSubjectsClass4
                    this.selectedSubjectsClass5 = (explainedSubject.classe === '5º') ? explainedSubject.subjects : this.selectedSubjectsClass5
                    this.selectedSubjectsClass6 = (explainedSubject.classe === '6º') ? explainedSubject.subjects : this.selectedSubjectsClass6
                    this.selectedSubjectsClass7 = (explainedSubject.classe === '7º') ? explainedSubject.subjects : this.selectedSubjectsClass7

                })

            },

            //Inicializa as disciplinas da secundaria
            inicializeSecundarySubjects(subjectDetails)
            {

                subjectDetails.forEach((explainedSubject) => {

                    this.selectedSubjectsClass8 = explainedSubject.classe === '8º' ? explainedSubject.subjects : this.selectedSubjectsClass8
                    this.selectedSubjectsClass9 = explainedSubject.classe === '9º' ? explainedSubject.subjects : this.selectedSubjectsClass9
                    this.selectedSubjectsClass10 = explainedSubject.classe === '10º' ? explainedSubject.subjects : this.selectedSubjectsClass10
                    this.selectedSubjectsClass11 = explainedSubject.classe === '11º' ? explainedSubject.subjects : this.selectedSubjectsClass11
                    this.selectedSubjectsClass12 = explainedSubject.classe === '12º' ? explainedSubject.subjects : this.selectedSubjectsClass12

                })

            },

            //Inicializa as disciplinas do nivel Tecnico que o user ja possuia
            inicializeTecnicalSubjects(subjectDetails) {

                subjectDetails.forEach((explainedSubject) => {

                    this.selectedElementarTecnicalSubjects = explainedSubject.classe === 'Elementar' ? explainedSubject.subjects : this.selectedElementarTecnicalSubjects
                    this.selectedBasicTecnicalSubjects = explainedSubject.classe === 'Básico' ? explainedSubject.subjects : this.selectedBasicTecnicalSubjects
                    this.selectedMediumTecnicalSubjects = explainedSubject.classe === 'Médio' ? explainedSubject.subjects : this.selectedMediumTecnicalSubjects

                })

            },

            //Iniciliza as disciplinas do nivel Superior
            inicializeMasterSubjects(subjectDetails) {

                subjectDetails.forEach((explainedSubject) => {

                    if (explainedSubject.level === 'Superior')
                    {
                        this.selectedMasterSubjects = explainedSubject.subjects
                        return
                    }

                })

            },

            inicializeOtherSubjects(subjectDetails) {

                subjectDetails.forEach((explainedSubject) => {

                    if (explainedSubject.level === 'Outro')
                    {
                        this.selectedOtherSubjects = explainedSubject.subjects
                        return
                    }

                })

            },

            onHelp () {
                this.dialog = false
                this.$router.push('/help')
            }
        }
    }
</script>
<style>
    .dialog {
        margin: 0!important;
    }
</style>


