<template>

    <div style="width: 100%">

        <!--Seccao da Localizacao-->

            <v-flex xs12 class="pb-2">
                <div class="cabecalho-profile">
                    Localização

                    <explicador-edit-location-dialog v-if="isMyProfile" :user="user" style="float: right;"></explicador-edit-location-dialog>
                </div>

                <div style="margin: 30px"></div>

                <v-container class="pl-2">
                    <v-layout row>
                        <v-flex xs4>
                            <span class="mr-3"><v-icon>location_on</v-icon></span>
                            <span class="title">Provincia:</span>
                            <br>
                            <br>
                            <span class="mr-3"><v-icon>location_on</v-icon></span>
                            <span v-if="isUser && hasSomeLocation &&  user.Locations.isFromMaputoCity">Cidade:</span>
                            <span v-else-if="isUser && hasSomeLocation &&  user.Locations.isFromMaputoProvince">Localizacao:</span>
                            <span v-else>Distrito:</span>
                            <br>
                            <br>
                            <span class="mr-3"><v-icon>location_on</v-icon></span>
                            <span v-if="isUser && hasSomeLocation &&  (user.Locations.isFromMaputoCity || user.Locations.isFromMaputoProvince)">Bairro:</span>
                            <span v-else>Localidade</span>
                            <br>
                            <br>
                            <span class="mr-3" v-if="isUser && hasSomeLocation &&  (user.Locations.isFromMaputoCity || user.Locations.isFromMaputoProvince)">
                                <v-icon>location_on</v-icon>
                            </span>
                            <span v-if="isUser && hasSomeLocation &&  user.Locations.isFromMaputoCity">Distrito Municipal:</span>
                            <span v-else-if="isUser && hasSomeLocation &&  user.Locations.isFromMaputoProvince">Posto administrativo:</span>

                        </v-flex>

                        <v-flex xs8 class="pl-3" style="border-left: 2px solid orange; height: 280px">

                            <span>{{ userProvince }}</span>
                            <br>
                            <br>
                            <span v-if="isUser && hasSomeLocation &&  user.Locations.isFromMaputoCity">Cidade de Maputo</span>
                            <span v-else-if="isUser && hasSomeLocation &&  user.Locations.isFromMaputoProvince">Matola</span>
                            <span v-else>{{ userDistrict }}</span>
                            <br>
                            <br>
                            <span v-if="isUser && hasSomeLocation &&  (user.Locations.isFromMaputoCity || user.Locations.isFromMaputoProvince)">{{ userBairro }}</span>
                            <span v-else>{{ userLocality }}</span>
                            <br>
                            <br>
                            <span v-if="isUser && hasSomeLocation &&  user.Locations.isFromMaputoCity">{{ userMunicipalDistrict }}</span>
                            <span v-else-if="isUser && hasSomeLocation &&  user.Locations.isFromMaputoProvince">{{ userPost }}</span>

                        </v-flex>

                    </v-layout>
                </v-container>

            </v-flex>

    </div>

</template>

<script>
    export default {
        props: ['user', 'name'],
        data: () => ({
            hidden: false,
            nameU: ''
        }),
        created () {
        },
        computed: {
            options () { //usado para fazer o scroll
                return {
                    duration: 1000,
                    offset: 0,
                    easing: 'easeInQuint'
                }
            },
            userAuth () {
                return this.$store.getters.userAuth
            },
            isUserAuth () {
                return this.userAuth !== undefined && this.userAuth !== null
            },
            isUser () {
                return this.user !== undefined && this.user !== null
            },
            isMyProfile () {
                return this.isUserAuth && this.isUser && this.user.id === this.userAuth.id
            },

            hasSomeLocation () {

                if ( !this.isUser ) return false

                return this.user.Locations

            },

            userLocality() {

                const negative = 'Localidade Não registada'

                if (!this.hasSomeLocation) return negative

                let userL = this.user.Locations.locality

                return userL !== null && userL && userL !== '' ? userL : negative

            },

            userBairro() {

                const negative = 'Bairro Não registado'

                if (!this.hasSomeLocation) return negative

                let userBairro = this.user.Locations.bairro

                return userBairro !== null && userBairro && userBairro !== '' ? userBairro : negative

            },

            userPost() {

                const negative = 'Posto Administrativo Não registado'

                if (!this.hasSomeLocation) return negative

                let userPost = this.user.Locations.post //Posto administrativo

                return userPost !== null && userPost && userPost !== '' ? userPost : negative

            },

            userMunicipalDistrict() {

                const negative = 'Distrito Municipal Não registado'

                if (!this.hasSomeLocation) return negative

                let userMD = this.user.Locations.municipalDistrict

                return userMD !== null && userMD && userMD !== '' ? userMD : negative

            },

            userDistrict() {

                const negative = 'Distrito Não registado'

                if (!this.hasSomeLocation) return negative

                let userDistrict = this.user.Locations.district

                return userDistrict !== null && userDistrict && userDistrict !== '' ? userDistrict : negative

            },

            userProvince () {

                const negative = 'Provincia Não registada'

                if (!this.hasSomeLocation) return negative

                let userProv = this.user.Locations.province

                return userProv !== null && userProv && userProv !== '' ? userProv : negative

            },

            userCity () {

                const negative = 'Cidade Não registada'

                if (!this.hasSomeLocation) return negative

                let userCity = this.user.Locations.city

                return userCity !== null && userCity !== undefined && userCity !== '' ? userCity : negative

            },

        },
        methods: {

            scrollTo (top) {

                window.scroll({
                    top: top,
                    left: 10,
                    behavior: 'smooth'
                });

                // Scroll to a certain element
//                document.querySelector(el).scrollIntoView({
//                    behavior: 'smooth'
//                });

            },
            click () {
                alert('cliou')
            }

        },
    }
</script>
<style scoped>
    .explicador-bar-fixed {
        z-index: 0;
    }
    .explicador-edit-item {
        float: right;
        cursor: pointer
    }
    .icon {
        font-size: 18px;
    }
    .img-radius {
        width: 50px;
        height: 50px!important;
    }
    .container {
        padding: 0px;
    }


</style>


