<template>
    <v-dialog v-model="dialog" width="350px">
        <!--Espaco com o botao-->
        <v-btn
                flat=""
                color="orange"
                round=""
                class=""
                outline=""
                @click.stop="dialog = !dialog"
                slot="activator"
        >
            Ver contacto do explicador
            <v-icon class="ml-3" large="">phone</v-icon>
        </v-btn>

        <v-card>
            <v-form v-model="valid" @submit.prevent="onLogin">
                <v-card-title
                    class="lighten-4 py-4 title"
                >
                    Sua identificação primeiro

                    <v-icon class="ml-3">phone</v-icon>
                </v-card-title>
                <v-divider></v-divider>
                <v-container grid-list-sm class="pa-4 mb-2">
                    <v-layout row v-if="error">
                        <v-flex xs12>
                            <app-alert @dismissed="onDismissed" :text="error.message"></app-alert>
                        </v-flex>
                    </v-layout>

                    <v-layout row class="grey pa-2 mb-3 white--text" style="border-radius: 10px">
                        <v-flex xs10 offset-xs1>
                            <small>Voçê não esta autenticado, por isso precisamos dos seus contactos também, iremos te informar quando o explicador responder</small>
                        </v-flex>
                    </v-layout>
                    <!--Campos do form-->
                    <v-layout row wrap class="">

                        <!--Nome-->
                        <v-flex xs12>
                            <v-text-field
                                    label="Seu Nome"
                                    prepend-icon="person"
                                    v-model="name"
                                    @keyup.enter="onLogin"
                                    :rules="nameRules"
                                    type="email"
                                    ></v-text-field>
                        </v-flex>

                        <!--Email-->
                        <v-flex xs12>
                            <v-text-field
                                    name="email"
                                    label="Seu email"
                                    prepend-icon="mail"
                                    v-model="email"
                                    @keyup.enter="onLogin"
                                    :rules="emailRules"
                                    type="email"
                                    required></v-text-field>
                        </v-flex>

                        <!--Telefone-->
                        <v-flex xs12>
                            <v-text-field
                                    label="Seu Número de Telefone"
                                    prepend-icon="phone"
                                    v-model="telphone"
                                    :rules="telefoneRules"
                                    @keyup.enter="onLogin"
                                    :counter="9"
                                    type="number"
                                    required></v-text-field>
                        </v-flex>



                    </v-layout>
                </v-container>

                <v-divider></v-divider>

                <!--Botoes da dialog-->
                <v-card-actions>

                    <v-spacer></v-spacer>
                    <v-btn flat color="primary" @click="dialog = false">Cancelar</v-btn>
                    <v-btn
                        flat
                        type="submit"
                        class="green--text"
                        :disabled="loading || !valid"
                        :loading="loading"
                    >
                        Enviar

                        <span slot="loader" class="custom-loader">
                            <v-icon light>cached</v-icon>
                        </span>
                    </v-btn>
                </v-card-actions>
            </v-form>
        </v-card>
    </v-dialog>
</template>
<script>
    import { eventBus } from '../../main'
    export default {
        props: ['user'],

        data () {
            return {
                dialog: false,
                valid: false,
                email: '',
                name: '',
                telphone: '',
                nameRules: [
                    v => !!v || 'O Nome é obrigatório.',
                    v => /^[a-zA-Zçããõíáóúéõ ]{2,30}$/.test(v) || 'Insira um nome válido.',
                    v => v.length <= 30 || 'O Nome deve ter menos de characters'
                ],
                emailRules: [
                    v => !!v || 'O E-mail é obrigatório.',
                    v => /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v) || 'Insira um email válido.',
                    v => v.length <= 40 || 'O Email deve ter menos de 40 characters'
                ],
                telefoneRules: [
                    v => !!v || 'O Número do telefone é obrigatório.',
                    v => /^[0-9]*$/.test(v) || 'Insira um Número do telefone válido.',
                    v => /^[0-9]{9}$/.test(v) || 'O numero de telefone deve ter 9 digitos.',
                    v => v.length <= 9 || 'O seu numero de telefone deve ter 9 digitos',
                ],
                loginDialog: false, // Controlador para ver se as operacoes estao a ser feitas aqui ou nao
            }
        },
        computed: {
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            },
            isUserAuth () {
                return this.$store.getters.isUserAuth
            },
            didUserConfirm () {
                return this.$store.getters.didUserConfirm
            },

            getNameRules () {
                return this.$store.getters.getNameRules
            },

            getEmailRules () {
                return this.$store.getters.getEmailRules
            },
            isEmailValid () {
                return this.email !== '' && this.email.toString().length > 10
            },

            isTelfoneValid () {
                return this.telphone !== '' && this.telphone.toString().length === 9 && !isNaN(this.telphone)
            },

        },
        watch: {
            didUserConfirm (value) {

                if (this.isUserAuth && value) {
                    this.$store.dispatch('logOutUser')
                    this.dialog = !this.dialog
                    this.$store.dispatch('userConfirmation', false) //Resetando a confirmacao do user
                }

                this.loginDialog = false

            },
            dialog()
            {
                this.$store.dispatch('setLoading', false)
            }
        },
        methods: {
            onLogin () {

                if (this.name !== '' && this.isEmailValid && this.isTelfoneValid) {

                    let user = this.user;
                    let object = {
                        explicadorId: user.id,
                        userRequesterId: '',
                        explicadorName: user.name ?  user.name : '',
                        explicadorEmail: user.email,
                        userRequesterName: this.name,
                        userRequesterEmail: this.email,
                        userRequesterTelefone: this.telphone,
                        created_at: new Date(),
                        updated_at: new Date(),
                        permited: true,
                    }

                    //Emitido o evento, para que o ficheiro: dialog/OtherProfileShowContactDialog.vue
                    //Trate da gravacao dos dados
                    eventBus.$emit('saveContactRequest', object)
                    this.dialog = false
                }
                else
                {
                    let fields = this.isTelfoneValid ? '' : 'Telefone'
                    let text = "Dados não enviados, por favor preencha os campos " + fields + " correctamente"
                    let color = 'red'
                    eventBus.$emit('showSnackbar', text, color, 5000);
                }

            },
            onDismissed () {
                this.$store.dispatch('clearError')
            },


        }
    }
</script>


