<template>
    <v-dialog v-model="dialog" :width="width" style="display: none">
        <v-btn
                fab
                bottom
                right
                fixed
                color="white"
                title="Enviar Sugestoes"
                slot="activator"
                @click.stop="mostrarDialog"
        >
            <v-icon>mail_outline</v-icon>
        </v-btn>
        <v-card>
            <v-card-title class="white--text title" :class="color">
                {{ dialogData.title }}
            </v-card-title>
            <v-container grid-list-sm class="pa-4">
                <v-layout row wrap class="mt-0">
                    <v-flex xs12>
                        <p> {{ dialogData.message }} </p>
                    </v-flex>
                </v-layout>
            </v-container>
            <v-divider class="mb-2"></v-divider>
            <v-card-actions class="pt-0">
                <v-spacer></v-spacer>
                <v-btn flat color="primary" @click="dialog = false">{{ dialogData.btn1 }}</v-btn>
                <v-btn flat @click="userConfirmation" class="green--text">{{ dialogData.btn2 }}</v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>
<script>
    export default {
        data () {
            return {
                dialog: true,
                dialogData: {}
            }
        },
        created () {
            this.dialog = this.dialog && this.showConfirmDialog
        },
        computed: {
            showConfirmDialogData () {
                return this.$store.getters.globalConfirmDialogDetails
            },
            color () {
                let cd = this.dialogData
                return cd.color ? cd.color : 'blue'
            },
            width () {
                return this.dialogData.width ? this.dialogData.width : '400px'
            },
            showConfirmDialog () { //propriedade que mostra ou nao a dialog
                return this.showConfirmDialogData.show //redundacia para evitar o erro que lanca no console
            },
        },
        methods: {

            mostrarDialog () {
                this.dialog = !this.dialog
            },
            userConfirmation () {

                this.$store.dispatch('userConfirmation', true)
                this.dialog = !this.dialog

            }
        },
        watch: {

//          Controlando a variavel computada para que ao se fechar a dialog, as propriedades que controlam a dialog sejam resetadas
            showConfirmDialog (value) {

                this.dialogData = this.showConfirmDialogData
                this.dialog = value
            },
            dialog (value) {
                this.$store.dispatch('setLoading', false)
                this.$store.dispatch('togleConfirmDialog', { show: value })
            }
        }
    }
</script>


