<template>
    <v-dialog
            v-model="dialog"
            fullscreen
            transition="dialog-bottom-transition"
            :overlay="false"
            scrollable
    >
        <v-card tile>
            <v-toolbar card dark color="primary">
                <v-btn icon @click.native="dialog = false" dark>
                    <v-icon>close</v-icon>
                </v-btn>
                <v-toolbar-title>Ajuda</v-toolbar-title>
                <v-spacer></v-spacer>
                <v-toolbar-items>
                    <v-btn dark flat @click.native="dialog = false">Fechar</v-btn>
                </v-toolbar-items>
                <v-menu bottom right offset-y>
                    <v-btn slot="activator" dark icon>
                        <v-icon>more_vert</v-icon>
                    </v-btn>
                    <v-list>
                        <v-list-tile router to="/help">
                            <v-list-tile-title>Ver Ajuda geral</v-list-tile-title>
                        </v-list-tile>
                    </v-list>
                </v-menu>
            </v-toolbar>
            <v-card-text>


                <v-list three-line subheader>
                    <v-subheader>Ações sobre o explicador</v-subheader>

                    <v-list-tile avatar>
                        <v-list-tile-content><v-list-tile-sub-title>
                                Olá, leia o que significam as opções asseguir sobre o explicador
                            </v-list-tile-sub-title>
                        </v-list-tile-content>
                    </v-list-tile>

                    <!--Informacao sobre ver perfil-->
                    <v-list-tile avatar>
                        <v-list-tile-content>
                            <v-list-tile-title>Ver Perfil</v-list-tile-title>
                            <v-list-tile-sub-title>
                                Quando voçê clica no <b>ver perfil</b> sobre um certo explicador, será levado para uma página de perfil
                                desse explicador, onde poderá ver: <b>Os dados pessoais, académicos, as disciplinas, localização,...</b>
                                do explicador que voçê clicou.
                            </v-list-tile-sub-title>
                        </v-list-tile-content>
                    </v-list-tile>

                    <!--Informacao sobre disciplinas-->
                    <v-list-tile avatar>
                        <v-list-tile-content>
                            <v-list-tile-title>Disciplinas</v-list-tile-title>
                            <v-list-tile-sub-title>
                                Ao clicar nessa opção, serão listadas todas as disciplinas do explicador,
                                mas também podes ver as mesmas no perfil do explicador.
                            </v-list-tile-sub-title>
                        </v-list-tile-content>
                    </v-list-tile>

                    <!--Informacao sobre Marcar-->
                    <v-list-tile avatar>
                        <v-list-tile-content>
                            <v-list-tile-title>Marcar</v-list-tile-title>
                            <v-list-tile-sub-title>
                                Marcar explicador, é adicionar o explicador na lista dos seus explicadores preferidos
                                Mas só pode <b>Marcar</b> um explicador se voçê estiver autenticado. (Opção não disponivel, ainda estamos a implementar essa funcionalidade).
                            </v-list-tile-sub-title>
                        </v-list-tile-content>
                    </v-list-tile>
                </v-list>
                <v-divider></v-divider>

                <v-list>
                    <v-list-tile avatar>
                        <v-list-tile-content>
                            <v-list-tile-sub-title>
                            Essa ajuda sempre estará disponivel para si, consulte-a sempre que precisares.
                            </v-list-tile-sub-title>
                        </v-list-tile-content>
                    </v-list-tile>

                    <v-list-tile avatar>
                        <v-list-tile-content>
                            <v-list-tile-sub-title>
                                Para mais informações (dúvidas, sugestões,...) envie-nos um email para <b>explicadores.mz@gmail.com</b>
                            </v-list-tile-sub-title>
                        </v-list-tile-content>
                    </v-list-tile>
                </v-list>
            </v-card-text>

            <div style="flex: 1 1 auto;"/>
        </v-card>
    </v-dialog>
</template>
<script>
    export default {
        data () {
            return {
                dialog: false,
                notifications: false,
                sound: true,
                widgets: false,
                select: [
                    { text: 'State 1' },
                    { text: 'State 2' },
                    { text: 'State 3' },
                    { text: 'State 4' },
                    { text: 'State 5' },
                    { text: 'State 6' },
                    { text: 'State 7' }
                ]
            }
        },
        computed: {
            showHelpExplicador () {
                return this.$store.getters.getShowHelpExplicador
            },
        },
        watch: {
            showHelpExplicador (value) {
                if (value && !this.dialog) {
                    this.dialog = value
                }
            },
            dialog (value) {
                if (!value) {
                    this.$store.dispatch('setShowHelpExplicador', false)
                }
            }
        }
    }
</script>


