<template>
    <div>
        <other-profile :user="user" :name="name"></other-profile>
    </div>
</template>
<script>

    export default {
        data () {
            return {
                showAlert: true,
                searchSubjects: true,
            }
        },
        computed: {
            loading () {
                return this.$store.getters.loading
            },
            allUsers () {
                return this.$store.getters.getUsers
            },
            user () {
                return this.$store.getters.user
            },
            isUser () {
                return this.user !== null && this.user !== undefined
            },
            isMyProfile () {
                return this.isUserAuth && this.isUser && this.userAuth.id === this.user.id
            },
            userAuth () {
                return this.$store.getters.userAuth
            },
            isUserAuth () {
                return this.userAuth !== null && this.userAuth !== undefined
            },
            name () {
                return this.isUser && this.user.name !== '' && this.user.name !== null ? this.user.name : 'Sem Nome'
            },
            mostrarAlert () {
                let alert = true

                alert = this.isUser && this.name === 'Sem Nome'

                return alert

            },
        },
        watch: {
            allUsers (value) {
                this.$store.dispatch('setProfileUser', this.$route.params.profile)
            },
            loading (value) {

                if (!value) { //busca os dados do perfil quando ele terminar de ler
                    this.$store.dispatch('setProfileUser', this.$route.params.profile)
                }
            }
        },
        beforeCreate () {
            this.$store.dispatch('setProfileUser', this.$route.params.profile)
            this.$Progress.start()
            this.$Progress.set(80)
        },
        methods: {
            onDismissed () {
                this.showAlert = false
                this.$store.dispatch('clearError')
                this.$store.dispatch('setLoading', false)
            },
        },
        mounted () {
//            alert('componente criado.')
//            console.log('rotas param: ', )
//            this.$Progress.finish()
        },
    }
</script>


