export default (str) => {
//Filtro para o nome no card explicador
    let finalStr = str

    if (str.length > 19)
    {
        let strArr = str.split(' ');
        finalStr = strArr[0]

        strArr.forEach((s, i) => {
            if (finalStr.length < 19 && i > 0 && i !== strArr.length-1)
            {
                finalStr += ' ' + strArr[i].charAt(0).toUpperCase() + '.'
            }
        })

        finalStr += ' ' + strArr[strArr.length-1] //pegando apelido
    }

    return finalStr

}


