<template>
    <v-dialog v-model="dialog" width="350px">
        <v-list-tile
                title="Disciplinas que ele explica"
                slot="activator"
                @click.stop="mostrarDialog"
                >
            <v-list-tile-title>{{ btnText }}</v-list-tile-title>
        </v-list-tile>

        <v-card>
            <!--Titulo da dialog-->
            <v-card-title
               class="blue white--text py-4 title">
                Disciplinas do {{ user.name }}
            </v-card-title>

            <!--Corpo  da dialog-->
            <v-container grid-list-sm class="pa-4 mb-5">

                <!--Espaco para o reposrte dos erros-->
                <v-layout row v-if="error">
                    <v-flex xs12>
                        <app-alert @dismissed="onDismissed" :text="error.message"></app-alert>
                    </v-flex>
                </v-layout>

                <v-layout row wrap class="mt-0">

                    <div v-if="!hasSubjects" class="text-xs-center" style="width: 100%">

                        <span class="red--text">{{ user.name }}, ainda não registou as disciplinas que explica</span>
                        <br>
                        <br>
                        <br>
                        <v-btn block ico flat="" v-if="isUserAuth">Perguntar que disciplina explica</v-btn>
                    </div>

                    <v-list v-else>
                        <template v-for="(subject, i) in subjects">
                            <v-list-tile @click="">
                                <v-list-tile-action>
                                    <v-icon>library_books</v-icon>
                                </v-list-tile-action>
                                <v-list-tile-content>
                                    <v-list-tile-title>{{ subject.name }}</v-list-tile-title>
                                </v-list-tile-content>
                            </v-list-tile>

                            <v-divider inset v-if="i !== subjects.length -1"></v-divider>
                        </template>
                    </v-list>

                </v-layout>
            </v-container>

            <v-divider class="mb-2"></v-divider>

            <v-card-actions class="pt-0">
                <v-btn flat color="primary" @click="onHelp">Ajuda</v-btn>
                <v-spacer></v-spacer>

                <v-btn
                    flat
                    @click="dialog = !dialog"
                    class="blue--text"
                    :disabled="loading"
                    :loading="loading">
                    Fechar
                    <span slot="loader" class="custom-loader">
                        <v-icon light>cached</v-icon>
                    </span>
                </v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>
<script>
    import { eventBus } from '../../main'
    export default {
        created () {
        },
        props: ['user', 'btnText'],
        data () {
            return {
                dialog: false,
            }
        },
        computed: {
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            },
            isUserAuth () {
                return this.$store.getters.isUserAuth
            },
            userAuth () {
                return this.$store.getters.userAuth
            },
            hasSubjects () {
                return this.user && this.user.Educations && this.user.Educations.subjects.length > 0
            },
            subjects () {

                let negative = []
                if (!this.hasSubjects) return negative

                return this.user.Educations.subjects
            }
        },
        methods: {

            onDismissed () {
                this.$store.dispatch('clearError')
            },
            onHelp () {
                this.dialog = !this.dialog
                this.$router.push('/help')
            },
            mostrarDialog () {
                this.dialog = !this.dialog
            },

        }
    }
</script>


