<template>
    <v-dialog v-model="dialog" persistent max-width="500px">
        <!--Botao de activacao-->
        <v-btn
                small=""
                slot="activator"
                :color="isMobile ? '' : 'blue'"
                :flat="isMobile"
                title="Editar os meus dados Pessoais"
                @click.stop="dialog = !dialog"
        >
            Editar
            <v-icon>edit</v-icon>
        </v-btn>

        <v-card>
            <!--Cabecalho da dialog-->
            <v-card-title class="headline blue white--text">
                <v-icon large="" dark>person</v-icon> Edite os seus dados Pessoais
                <v-spacer></v-spacer>
                <v-btn title="Fechar" @click="dialog = !dialog" class="mb-0 mt-0" icon=""><v-icon>close</v-icon></v-btn>
            </v-card-title>

            <!--Separador-->
            <v-divider></v-divider>

            <v-container grid-list-sm class="pb-4 mb-5">
                <v-layout row v-if="error">
                    <v-flex xs12>
                        <app-alert @dismissed="onDismissed" :text="error.message"></app-alert>
                    </v-flex>
                </v-layout>

                <!--Corpo da dialog-->
                <v-card-text>
                    <v-container grid-list-md>
                        <v-layout wrap>
                            <v-flex xs12>
                                <v-text-field
                                        label="Seu Nome"
                                        v-model="name"
                                        type="text"
                                        :rules="nameRules"
                                        prepend-icon="person"
                                        required
                                ></v-text-field>
                            </v-flex>
                            <!--Campo descricao-->
                            <v-flex xs12>
                                <v-text-field
                                        prepend-icon="face"
                                        v-model="bio"
                                        hint="Palavras convincentes para que te procurem"
                                        placeholder="Fale um pouco de si">
                                </v-text-field>
                            </v-flex>

                            <v-flex xs12>
                                <v-text-field
                                        type="number"
                                        prepend-icon="phone"
                                        placeholder="841234567"
                                        hint="Os dados de contacto devem ser válidos, para que possamos te manter informado quando te procurarem"
                                        v-model="telfone"
                                        :rules="telfoneRules"
                                        :counter="9"
                                ></v-text-field>
                            </v-flex>
                            <v-flex xs12>
                                <v-radio-group hint="Seu sexo"  v-model="gender" row>
                                    <v-radio label="Masculino" value="Masculino" ></v-radio>
                                    <v-radio label="Feminino" value="Feminino"></v-radio>
                                </v-radio-group>

                            </v-flex>


                        </v-layout>
                    </v-container>
                    <small>*indica os campos obrigatorios</small>
                </v-card-text>

            </v-container>

            <v-divider></v-divider>

            <!--Botoes das acoes-->
            <v-card-actions>
                <v-btn flat color="primary" @click="onHelp">Ajuda</v-btn>
                <v-spacer></v-spacer>
                <v-btn flat color="primary" @click="dialog = false">Fechar</v-btn>
                <v-btn
                    flat
                    @click="onSave"
                    class="green--text"
                    :disabled="loading || !dataValid"
                    :loading="loading"
                >
                    Salvar

                    <span slot="loader" class="custom-loader">
                        <v-icon light>cached</v-icon>
                    </span>
                </v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>
<script>
    import { eventBus } from '../../main'
    export default {
        props: ['user', 'isMobile'],
        data () {
            return {
                dialog: false,
                name: '',
                nameRules: [
                    (v) => !!v || 'O seu nome é necessário ',
                    (v) => v && v.length >= 2 || 'O seu nome nao pode ter menos de 2 caracteres'
                ],
                email: '',
                gender: '',
                bio: '',
                telfone: '',
                telfoneRules: [
                    (v) => v && v.length <= 9 || 'O seu numero de celular deve ter 9 digitos',
                ],
                showPersonalProcess: false,

            }
        },
        computed: {
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            },
            userAuth () {
                return this.$store.getters.userAuth
            },
            isUserAuth () {
                return this.userAuth !== null && this.userAuth !== undefined
            },
            faculties () {
                return this.$store.getters.getFaculties
            },
            facultyNames () {
                return this.$store.getters.getFacultieNames
            },

            departments () {
                return this.$store.getters.getDepartments
            },
            departmentNames () {
                return this.$store.getters.getDepartmentNames
            },

            courses () {
                return this.$store.getters.getCourses
            },
            courseNames () {
                return this.$store.getters.getCourseNames
            },

            subjects () {
                return this.$store.getters.getSubjects
            },

            isUser () {
                return this.user !== null && this.user !== undefined
            },
            isMyProfile () {
                return this.userIsAuth && this.isUser && this.user.id === this.userAuth.id
            },
            dataValid ()
            {
                return (this.name && this.email && this.telfone)
                    && (this.gender || this.bio || true)
            }
        },
        watch: {

            loading (value) {

                if (!value && !this.error && this.showPersonalProcess) {

                    let text = "Os seus dados Pessoais foram actualizados com sucesso!"
                    let color = 'green'
                    eventBus.$emit('showSnackbar', text, color, 5000); //Esse evento eh tratado no ficheiro App.vue, eh la onde foi montado o componente snackbar
                    this.showPersonalProcess = false
                    this.dialog = false
                }
            },
            dialog (value)
            {
                if (value) {
                    this.inicializeUserInfo()
                }
                else //Entao a dialog esta a fechar
                {
                    this.$store.dispatch('updateTheUserInArray', this.userAuth)
                    this.$store.dispatch('setLoading', false)
                }

            }

        },
        methods: {
            onSave () {

                let user = this.userAuth
                user.name = this.name ? this.name : ''
                user.email = this.email ? this.email : ''
                user.gender = this.gender ? this.gender : ''
                user.bio = this.bio ? this.bio : ''
                user.telfone = this.telfone ? this.telfone : ''

                this.showPersonalProcess = true

                alert('Gravando dados pessoais')
                this.$store.dispatch('updateOnlineUserData', user)

            },
            inicializeUserInfo () {

                const user = this.user
                this.name = this.isUser ? user.name : ''
                this.email = this.isUser ? user.email : ''
                this.gender = this.isUser ? user.gender : ''
                this.bio = this.isUser ? user.bio : ''
                this.telfone = this.isUser ? user.telfone : ''

            },

            onCancel () { //Este vai ser chamado antes de se começar a gravar, para ver se algum dado foi modificado...
                this.inicializeUserInfo()
                this.$store.dispatch('setLoading', false)
                this.dialog = false
F            },

            onDismissed () {
                this.$store.dispatch('clearError')
                this.$store.dispatch('setLoading', false)
            },

            onHelp () {
                this.dialog = false
                this.$router.push('/help')
            }
        }
    }
</script>


