<template>
    <v-dialog v-model="dialog" persistent max-width="500px">

        <v-btn
                small=""
                slot="activator"
                :color="isMobile ? '' : 'blue'"
                :flat="isMobile"
                title="Editar os meus dados de Localizacao"
                @click.stop="dialog = !dialog"
        >
            Editar
            <v-icon>edit</v-icon>
        </v-btn>

        <v-card>
            <!--Cabecalho da dialog-->
            <v-card-title class="headline">
                Edite os seus dados de Localização
                <v-spacer></v-spacer>
                <v-btn title="Fechar" @click="dialog = !dialog" class="mb-0 mt-0" icon=""><v-icon>close</v-icon></v-btn>
            </v-card-title>

            <!--Separador-->
            <v-divider></v-divider>

            <v-container grid-list-sm class="pa-4 mb-5">
                <v-layout row v-if="error">
                    <v-flex xs12>
                        <app-alert @dismissed="onDismissed" :text="error.message"></app-alert>
                    </v-flex>
                </v-layout>

                <v-card-text>
                    <v-container grid-list-md>
                        <v-layout wrap>
                            <v-flex xs12>
                                <v-select
                                        label="Sua Provincia"
                                        required
                                        autocomplete
                                        v-model="province"
                                        :items="provinceNames"
                                ></v-select>
                            </v-flex>
                            <v-flex xs12 v-if="isFromCity">
                                <v-select
                                        label="Seu districto Municipal"
                                        v-model="municipalDistrict"
                                        autocomplete
                                        :items="municipalDistrictNames"
                                ></v-select>
                            </v-flex>
                            <v-flex xs12 v-else-if="isFromMaputoProvince">
                                <v-select
                                        label="Seu Posto Administrativo"
                                        required
                                        v-model="post"
                                        :items="['Infulene', 'Machava', 'Matola Sede']"
                                ></v-select>
                            </v-flex>
                            <v-flex xs12 v-else>
                                <v-select
                                        label="Seu distrito"
                                        required
                                        autocomplete
                                        v-model="district"
                                        :items="districtNames"
                                ></v-select>
                            </v-flex>
                            <v-flex xs12 v-if="isFromCity">
                                <v-select
                                        :label="bairroLabel"
                                        v-model="bairro"
                                        autocomplete
                                        hint="Pesquise o nome do seu bairro"
                                        :items="bairroMCNames"
                                ></v-select>
                            </v-flex>
                            <v-flex xs12 v-else-if="isFromMaputoProvince">
                                <v-select
                                        :label="bairroLabel"
                                        v-model="bairro"
                                        autocomplete
                                        hint="Pesquise o nome do seu bairro"
                                        :items="bairroMPNames"
                                ></v-select>
                            </v-flex>
                            <v-flex xs12 v-else>
                                <v-text-field
                                        :label="bairroLabel"
                                        v-model="locality"
                                ></v-text-field>
                            </v-flex>

                        </v-layout>
                    </v-container>
                    <small>*indica os campos obrigatorios</small>
                </v-card-text>


            </v-container>

            <v-divider></v-divider>

            <v-card-actions>
                <v-btn flat color="primary" @click="onHelp">Ajuda</v-btn>
                <v-spacer></v-spacer>
                <v-btn flat color="primary" @click="onCancel">Fechar</v-btn>
                <v-btn
                    flat
                    @click="onSave"
                    class="green--text"
                    :disabled="loading"
                    :loading="loading"
                >
                    Salvar

                    <span slot="loader" class="custom-loader">
                        <v-icon light>cached</v-icon>
                    </span>
                </v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>
<script>
    import { eventBus } from '../../main'
    export default {
        props: ['user', 'isMobile'],
        data () {
            return {

                dialog: false,
                province: '',
                city: '',
                post: '',
                municipalDistrict: '',
                bairro: '',
                locality: '',
                district: '',
                showLocationProcess: false
            }
        },
        computed: {
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            },
            userAuth () {
                return this.$store.getters.userAuth
            },
            isUserAuth () {
                return this.userAuth !== null && this.userAuth !== undefined
            },

            isUser () {
                return this.user !== null && this.user !== undefined
            },
            isMyProfile () {
                return this.isUserAuth && this.isUser && this.user.id === this.userAuth.id
            },

            isFromCity () {
                return this.province === 'Maputo Cidade'
            },

            isFromMaputoProvince () {
                return this.province === 'Maputo Província'
            },
            bairroLabel () {
                let generic = 'Sua Localidade'
                if (this.province === '') return generic

                if (this.isFromCity || this.isFromMaputoProvince)
                {
                    generic = 'Seu Bairro'
                }
                return generic
            },

            provinceNames () {
                return this.$store.getters.getProvinceNames
            },

            districtNames () {
                let dists = this.$store.getters.getDistrictNames

                if (dists.length || !this.dialog)
                {
                    return dists
                }
                this.$store.dispatch('getAllDistricts')
                return this.$store.getters.getDistrictNames.sort()

            },

            bairroMCNames () {
                return this.$store.getters.getMCBairrosNames
            },

            bairroMPNames () {
                return this.$store.getters.getMPBairrosNames
            },

            municipalDistrictNames () {
                let distMun = this.$store.getters.getMunicipalDistrictNames

                if (distMun.length || !this.dialog)
                {
                    return this.$store.getters.getMunicipalDistrictNames
                }

                this.$store.dispatch('getAllMunicipalDistricts')
                return this.$store.getters.getMunicipalDistrictNames
            },

            hasSomeLocation () {

                if ( !this.isUser ) return false

                return this.user.Locations

            },

        },
        watch: {

            loading (value) {

                if (!value && !this.error && this.showLocationProcess) {

                    let text = "Os seus dados de Localizacao foram actualizados com sucesso!"
                    let color = 'green'
                    eventBus.$emit('showSnackbar', text, color, 5000); //Esse evento eh tratado no ficheiro App.vue, eh la onde foi montado o componente snackbar
                    this.showLocationProcess = false
                    this.dialog = false
                }
            },
            dialog (value)
            {
                if (value) {
                    this.inicializeUserInfo()
                }
                else
                {
                    this.$store.dispatch('updateTheUserInArray', this.userAuth)
                    this.$store.dispatch('setLoading', false)
                }

            },
        },
        methods: {
            onSave () {

                let user = this.userAuth

                user.Locations = {
                    province:               this.province,
                    city:                   this.city,
                    municipalDistrict:      this.isFromCity ? this.municipalDistrict : '',
                    district:               this.isFromCity || this.isFromMaputoProvince ? '' : this.district,
                    isFromMaputoCity:       this.isFromCity,
                    isFromMaputoProvince:   this.isFromMaputoProvince,
                    post:                   this.isFromCity ? '' : this.post,
                    bairro:                 this.isFromCity || this.isFromMaputoProvince ? this.bairro : '',
                    locality:               this.isFromCity || this.isFromMaputoProvince ? '' : this.locality,
                }

                this.showLocationProcess = true
                this.$store.dispatch('updateOnlineUserData', user)

            },
            onDismissed () {
                this.$store.dispatch('clearError')
            },
            onCancel () {
                this.inicializeUserInfo()
                this.$store.dispatch('setLoading', false)
                this.dialog = false
            },

            inicializeUserInfo () {

                if (!this.isMyProfile || !this.hasSomeLocation) return

                let uLocation = this.user.Locations

                this.province = uLocation.province && uLocation.province !== null ? uLocation.province : ''
                this.city = uLocation.city && uLocation.city !== '' ? uLocation.city : ''
                this.municipalDistrict = uLocation.municipalDistrict && uLocation.municipalDistrict !== '' ? uLocation.municipalDistrict : ''
                this.district = uLocation.district && uLocation.district !== '' ? uLocation.district : ''
                this.post = uLocation.post && uLocation.post !== '' ? uLocation.post : ''
                this.bairro = uLocation.bairro && uLocation.bairro !== '' ? uLocation.bairro : ''
                this.locality = uLocation.locality && uLocation.locality !== '' ? uLocation.locality : ''


            },

            onHelp () {
                this.dialog = false
                this.$router.push('/help')
            }
        }
    }
</script>


