<template>
    <v-layout row>
        <v-flex xs12>
            <v-layout>

                <v-card v-if="isHomePage">
                    <v-list class="text-xs-center">
                        <h4>Explicadores Populares</h4>
                    </v-list>
                    <v-list three-line>
                        <!--{{ explicadores.length }}-->
                        <template v-for="(item , i) in explicadores">

                            <v-subheader v-if="i === 0" v-text="'Explicadores ( ' + explicadores.length + ' )'"></v-subheader>
                            <v-divider v-if="i > 0" v-bind:inset="true"></v-divider>
                            <template>
                                <v-list-tile avatar @click="" router :to="'/' + item.id + '/profile'">
                                    <v-list-tile-avatar>
                                        <img v-bind:src="item.photoURL"/>
                                    </v-list-tile-avatar>
                                    <v-list-tile-content>
                                        <v-list-tile-title v-html="userInstitution(item)"></v-list-tile-title>
                                        <v-list-tile-sub-title v-html="getDescription(item)"
                                        ></v-list-tile-sub-title>
                                    </v-list-tile-content>
                                </v-list-tile>
                            </template>
                        </template>
                    </v-list>
                </v-card>
                <v-card v-else>
                    <v-list>
                        <!--Todo: Layout com a lista das discipplinas que sao mostradas, falta colocar o scroll-->
                        <div v-for="item in menuItens">

                            <!--Todo: Segundo divider que poe a linha de divisao-->
                            <v-divider v-if="item.aditional"></v-divider>
                            <template>
                                <v-list-tile @click="">
                                    <v-list-tile-action>
                                        <v-icon>{{ item.icon }}</v-icon>
                                    </v-list-tile-action>
                                    <v-list-tile-content>
                                        <v-list-tile-title>
                                            {{ item.text }}
                                        </v-list-tile-title>
                                    </v-list-tile-content>
                                </v-list-tile>
                            </template>
                        </div>
                    </v-list>
                </v-card>

            </v-layout>

        </v-flex>
    </v-layout>
</template>

<script>
    import { eventBus } from '../../main'
    export default {
        data () {
            return {
            }
        },
        computed: {
            explicadores () {
                return this.$store.getters.getUsers
            },
            isHomePage () {
                return this.$store.getters.isHomePage
            },
            menuItens () {
                return this.$store.getters.getMenuRightSide
            }
        },
        methods: {
            getDescription (user) {

                let nome = user.name === null ? 'Sem Nome' : user.name
                let name_color = user.name === null ? 'red--text text--darken-2' : 'grey--text text--darken-2'
                let bio = user.bio !== ''  ? user.bio : user.email ? user.email : user.id
                let desc = '<span class="' + name_color + '">' + nome+ '</span> — ' + bio + ''

                return desc
            },
            userInstitution (user) {
                let negative = '<span class="red--text">Sem Universidade (Escola)</span>'
//                let inst = user.Educations.institution
                return user && user.Educations && user.Educations.institution  ? user.Educations.institution : negative
            },
            closeDrawer () {
                eventBus.$emit('closeDrawer', 'right')
            }
        }
    }
</script>


