<template>
    <div>
        <v-form v-model="valid" @submit.prevent="">
            <v-dialog v-model="dialog" width="400px">

        <v-btn
            fab
            bottom
            right
            fixed
            flat
            round
            outline
            color="blue"
            title="Enviar Sugestoes"
            slot="activator"
            class="mb-4"
            @click.stop="mostrarDialog"
        >
            <v-icon>mail_outline</v-icon>

        </v-btn>

        <v-card>
            <!--Titulo da dialog-->
            <v-card-title
               class="blue white--text py-4 title">
                {{ option !== '' ? option : 'Recomendacoes' }}
            </v-card-title>

            <!--Corpo  da dialog-->
            <v-container grid-list-sm class="pa-4 mb-5">

                <!--Espaco para o reposrte dos erros-->
                <v-layout row v-if="error">
                    <v-flex xs12>
                        <app-alert @dismissed="onDismissed" :text="error.message"></app-alert>
                    </v-flex>
                </v-layout>

                <v-layout row wrap class="mt-0">
                    <v-flex xs12>
                        <v-layout  v-if="option === ''">
                            <v-flex xs12>
                                <small>Em todos os lugares existem Explicadores...</small>
                                <br>
                                <br>
                                <p>Gostarias de sugerir a plataforma a algum Explicador ou colega?</p>
                                <p>Ha duas coisas que podes fazer!</p>
                            </v-flex>
                        </v-layout>
                    </v-flex>
                    <v-flex xs12>
                        <v-layout>
                            <v-flex>
                                <v-flex xs12>
                                    <v-btn class="ma-0" style="font-size: 8pt; float: right" small="" flat="" @click="dialogInfo = !dialogInfo"><v-icon style="font-size: 10pt">help</v-icon> Saiba mais</v-btn>
                                </v-flex>
                            </v-flex>
                        </v-layout>
                        <v-layout>
                            <v-flex xs6>
                                <v-btn :flat="option != 'Recomendar'" @click="option = 'Recomendar'" block=""> Recomendar</v-btn>
                            </v-flex>
                            <v-flex xs6>
                                <v-btn :flat="option != 'Criar conta'" @click="option = 'Criar conta'" block="">Criar conta</v-btn>
                            </v-flex>
                        </v-layout>

                    </v-flex>

                    <template v-if="option !== ''">
                        <v-flex class="mt-5" xs12>
                            <v-text-field
                                    name="email"
                                    :label="'Email de quem desejas lhe ' + option"
                                    v-model="email"
                                    @keyup.enter="onSend"
                                    :rules="emailRules"
                                    type="email"
                                    required></v-text-field>
                        </v-flex>


                        <v-flex xs12 >
                            <v-text-field
                                    @keyup.enter="onSend"
                                    label="Escreva a mensagem que deseja enviar"
                                    hint="Escreva a mensagem que quer lhe enviar, ou deixe em branco."
                                    :rules="[(v) => (v.length > 50 || v.length == 0)  || 'Mínimo 50 caracteres']"
                                    :counter="50"
                                    v-model="message"
                            ></v-text-field>
                        </v-flex>
                    </template>

                </v-layout>

            </v-container>

            <v-divider class="mb-2"></v-divider>
            <v-card-actions class="pt-0">

                <v-spacer></v-spacer>
                <v-btn flat color="primary" @click="dialog = false">Cancelar</v-btn>
                <v-btn
                    flat
                    @click="onSend"
                    class="green--text"
                    :disabled="loading || !emailValid"
                    :loading="loading">
                    Enviar
                    <span slot="loader" class="custom-loader">
                        <v-icon light>cached</v-icon>
                    </span>
                </v-btn>
            </v-card-actions>
        </v-card>
        </v-dialog>
        </v-form>

        <!--Dialog com explicacao da diferenca-->
        <template>
            <v-layout row justify-center>
                <v-dialog v-model="dialogInfo" width="600px">
                    <v-card>
                        <v-card-title>
                            <span class="headline">Recomendação e criação de conta.</span>
                        </v-card-title>
                        <v-card-text class="">
                            <span class="">Qual é a diferença entre as opções recomendar e criar conta?</span>
                            <br>
                            <br>
                            <br>
                            <b>Recomendar explicador.co.mz</b><br><br>

                            Recomendar a plataforma para alguém.<br><br>
                            Ao recomendar a plataforma para alguém, permites que a pessoa saiba que o explicador.co.mz existe, se ele for um explicador pode criar conta e estar disponivel para muitas pessoas que estão a procura de explicador.<br>
                            <br>Pode recomendar a plataforma a qualquer pessoa que desejares<br><br>
                            O objectivo é que os nossos amigos, colegas, vizinhos e familiares saibam que a plataforma existe e que a qualquer momento que precisarem de explicador poderão encontrar diversas opções de explicadores capacitados aqui no <b>explicador.co.mz</b>,
                            e trabalharemos junto consigo para certificarmos de que os Explicadores que aqui se registam estão realmente os capacitados.

                            <br>
                            <br>
                            <br>
                            <br>
                            <b>Criar conta</b><br><br>

                            Criar conta para um explicador interressado em se registar na plataforma de forma simples, rapida e segura.<br><br>

                            <p>Ao criar a conta para um explicador, de  seguida enviamos para ele dois emails:</p>
                            <p>No primeiro informando que ele ja possui uma conta no <b>explicador.co.mz</b>, e que ja pode entrar  usando a conta recentemente criada.</p>
                            <p>No segundo email, enviamos o link de alteração de senha. Para que logo que entrar na plataforma use a senha que ele mesmo definiu.</p>

                            <p>Mas o explicador com a conta recemente criada, é dado apenas <b>15 dias</b> para entrar na plataforma, passados os 15 dias,
                                a conta será permanentemente removida. Mas ele pode a qualquer momento se registar novamente.</p>


                        </v-card-text>
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="primary darken-1" flat="flat" @click="dialogInfo = false">Fechar</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            </v-layout>
        </template>

    </div>
</template>
<script>
    import { eventBus } from '../../main'
    export default {
        data () {
            return {
                dialog: false,
                dialogInfo: false,
                option: '',
                valid: false,
                message: '',
                email: '',
                imageUrl: '',
                image: null,
                defaultPassword: '201620172018',
                emailRules: [
                    v => !!v || 'O E-mail é obrigatório.',
                    v => /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v) || 'Insira um email válido.',
                    v => v.length <= 40 || 'O Email deve ter menos de 40 characters'
                ],
            }
        },
        computed: {
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            },
            isUserAuth () {
                return this.$store.getters.isUserAuth
            },
            userAuth () {
                return this.$store.getters.userAuth
            },

            users () {
                return this.$store.getters.getUsers
            },

            emailValid() {
                return /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(this.email)
            },
        },
        watch: {

            loading (value) {

                if (!value && !this.error && this.dialog) {

                    let text = this.option === 'Recomendar' ? "A recomendacao foi enviada com sucesso, Obrigado...."
                        : 'A conta do ' + this.email +' foi criada com sucesso,  e ja lhe enviamos um email com ' +
                        'os dados para ele poder fazer login na nova conta. Obrigado'
                    let color = 'green'
                    eventBus.$emit('showSnackbar', text, color); //Esse evento eh tratado no ficheiro App.vue, eh la onde foi montado o componente snackbar
                    this.message = ''
                    this.imageUrl = ''
                }
            },
            dialog ()
            {
                this.$store.dispatch('setLoading', false)
            }


        },

        methods: {
            onSend () {

                if (this.email <= 0) {
                    alert('Insira o email para o qual desejas enviar')
                    return
                }

                if (this.emailExists ())
                {
                    let text = "O proprietario deste email ja possui conta. Se ele não consegue Entrar, pode recuperar a senha na tela de Login."
                    let color = 'red'
                    eventBus.$emit('showSnackbar', text, color,15000); //Esse evento eh tratado no ficheiro App.vue, eh la onde foi montado o componente
                    return
                    return
                }

                alert('passou nao validou...')
                return
                if (this.message.length > 0 && this.message.length  < 50) {

                    let text = "Por favor, na sua mensagem deve se identificar e colocar um pequeno resumo da sua recomendacao para que ao receber o email, entenda o objectivo! Ou deixe em branco."
                    let color = 'red'
                    eventBus.$emit('showSnackbar', text, color,15000); //Esse evento eh tratado no ficheiro App.vue, eh la onde foi montado o componente
                    return

                }
                if (this.message.length  > 400) {

                    let text = "Por favor, Insira uma mensagem valida entes de enviar!"
                    let color = 'red'
                    eventBus.$emit('showSnackbar', text, color,5000); //Esse evento eh tratado no ficheiro App.vue, eh la onde foi montado o componente
                    return

                }

                //Gravar a recomendacao
                this.$store.dispatch('sendRecomendation', {creatorId: this.userAuth.id, sugestedEmail: this.email, message: this.message, created_at: new Date()})

                this.sendMail()

                if (this.option === 'Criar conta')
                {

                    const object = {email: this.email, password: this.defaultPassword, recomended: false, recomendedBy: this.userAuth.id, created_at: new Date()}

                    this.$store.dispatch('createUserRecomendend',object )

                }
            },
            sendMail ()
            {
                let subject = (this.option === 'Recomendar') ? 'Te recomendo acessar esta plataforma' : 'Sua nova conta do Explicador'

                let message = this.message !== '' ? this.message :
                        (this.option === 'Recomendar') ? 'Olá, eu sou o ' + this.userAuth.name +', Encontrei uma plataforma online de explicadores, te ' +
                    'recomendo criares a sua conta, acesse wwww.explicador.co.mz de preferencia pelo computador assim que puderes, nesta ' +
                    'plataforma podes procurar explicadores ou Podes se tornar explicador. Obrigado. '

                    : 'Olá ' + this.email+', conheces o ' + this.userAuth.name + '? Ele acaba de criar uma conta para si no explicador.co.mz, se não tiveres solicitado a criação ' +
                      'de conta, pode ignorar este email. Mas se tiveres solicitado gostariamos de lhe informar que a sua conta ja foi criada. Entre ja use este email: ' +
                      '' + this.email +', e defina a sua senha secreta no email que iremos te enviar. Tens 15 dias para fazer o seu primeiro Login na sua nova conta do explicador. Obrigado.'

                let emailObject = {
                    message: message,
                    subject: subject,
                    to: this.email,
                }

                this.$store.dispatch('sendMail', emailObject)
            },
            onDismissed () {
                this.$store.dispatch('clearError')
            },

            emailExists ()
            {
                return this.users.find((user) => {
                    return user.email === this.email
                })
            },

            onHelp () {
                this.dialog = !this.dialog
                this.$router.push('/help')
            },
            mostrarDialog () {
                this.dialog = !this.dialog
            },
            onPickFile ()  {
                this.$refs.fileInput.click()
            },
            onFilePicked (event) {
                const files = event.target.files
                let filename = files[0].name
                if (filename.lastIndexOf('.') <= 0)
                {
                    return alert('Por favor adicione um ficheiro valido!')
                }
                const fileReader = new FileReader()
                fileReader.addEventListener('load', () => {
                    this.imageUrl = fileReader.result
                })
                fileReader.readAsDataURL(files[0])
                this.image = files[0]
            }
        }
    }
</script>


