<template>
    <v-container fluid grid-list-md>

        <!--==============================================-->
        <!--========== Botoes (opcoes) de filtro por niveis=========-->
        <!--==============================================-->
        <v-layout v-if="device.width > 500" class="ma-0 white" xs5 :wrap="device.name === 'xs'">
            <v-flex xs6 sm12 >
                <v-btn
                    block
                    @click.prevent.stop="onSelectNivel('Primário')"
                    :color="selectedNivel === 'Primário' ? 'blue' : 'white' "
                    title="Explicadores do nivel Primario">
                    Nivel Primário
                </v-btn>
            </v-flex>
            <v-flex xs6 sm12>
                <v-btn
                    block
                    @click.prevent.stop="onSelectNivel('Secundário')"
                    :color="selectedNivel === 'Secundário' ? 'blue' : 'white' "
                    title="Explicadores do nivel Secundário">
                    Nivel Secundário
                </v-btn>
            </v-flex>
            <v-flex xs6 sm12>
                <v-btn
                    block
                    @click="onSelectNivel('Superior')"
                    :color="selectedNivel === 'Superior' ? 'blue' : 'white' "
                    title="Explicadores do nivel Superior">
                    Nivel Superior
                </v-btn>
            </v-flex>
            <v-flex xs6 sm12>
                <v-layout :wrap="device.name === 'xs'">
                    <v-flex xs12 sm6 class="ml-1">
                        <v-btn
                            block
                            @click.prevent.stop="onSelectNivel('Técnico')"
                            :color="selectedNivel === 'Técnico' ? 'blue' : 'white' "
                            title="Explicadores do nivel Técnico">
                            Nivel Técnico
                        </v-btn>
                    </v-flex>
                    <v-flex xs12 sm6>
                        <v-btn
                            block
                            @click.prevent.stop="onSelectNivel('Outro')"
                            :color="(selectedNivel === '' || selectedNivel === 'Outro') ? 'blue' : 'white' "
                            title="Explicadores de  Outros niveis">
                            Outros
                        </v-btn>
                    </v-flex>
                </v-layout>
            </v-flex>
        </v-layout>
        <!--==============Fim de opcoes de filtro======-->


        <!--====================================================-->
        <!--================== Campo de pesquisa ===============-->
        <!--====================================================-->
        <v-layout v-else="" class="ma-0 elevation-0 explicador-bar-fixed"
            style="margin: -8px 8px!important;
            width: 96%">
            <v-flex xs12 sm12 class="elevation-0">
                <v-text-field
                        v-model="searchedData"
                        light
                        solo
                        id="pesquisa"
                        prepend-icon="search"
                        append-icon="close"
                        placeholder="Pesquise pela Provincia, Cursos, Universidade, Disciplinas, Bairros . . ."
                        @click=""
                ></v-text-field>
            </v-flex>
        </v-layout>
        <!--=========== Fim do campo de pesquisa ===============-->

        <!--====================================================-->
        <!--==============Listagem dos explicadores=============-->
        <!--====================================================-->
        <v-layout
            wrap v-if="searchedUsers.length"
            :class="{'mt-5' : device.width < 500}">


            <template v-for="user in searchedUsers" v-if="user.name">

                <!--<p>Ha Users, {{ user.Explicador }}</p>-->
                <!--Apenas os explicadores que tem nomes, irao aparecer.-->
                <v-flex  xs12 sm4 md3>

                    <explicador
                        :user="user"
                        :filterType="filterType"
                        :filterName="filterName"
                        :searchedData="searchedData"
                        :selectedNivel="selectedNivel">
                    </explicador>

                </v-flex>
            </template>

        </v-layout>
        <!--====================================================-->

        <v-container  v-else-if="isLoading" fill-height style="height: 450px">
            <v-layout justify-center align-center >
                <v-flex xs12 offset-xs5>
                    <v-progress-circular indeterminate :size="60" color="blue"></v-progress-circular>
                </v-flex>
            </v-layout>
        </v-container>

        <!--Mensagens de feedback caso os explicadores nao sejam encontrados-->
        <v-layout v-else>

            <v-flex v-if="searchedData === '' &&  selectedNivel !== '' && filterType === '' && filterName === ''" xs12 class="text-xs-center blue--text">
                <v-container fill-height style="height: 450px">
                    <v-layout justify-center align-center >
                        <v-flex xs12>
                            <v-progress-circular indeterminate :size="60" color="blue"></v-progress-circular>
                        </v-flex>
                    </v-layout>
                </v-container>
            </v-flex>

            <v-flex v-else="" xs12 class="text-xs-center red--text">
                <div style="height: 200px"></div>
                <h3 v-if="filterType === '' && filterName === ''"><v-icon>search</v-icon>
                    Nenhum explicador do nivel {{ selectedNivel }} foi encontrado ...
                </h3>


                <div v-else=''>
                    <h3
                       v-if="'DisciplinasCursosUniversidadesFaculdades'.includes(filterType)">
                        <v-icon>search</v-icon>
                        Nenhum explicador {{ searchedData ? '"' +searchedData+'"' : '' }} do nivel {{ selectedNivel }} encontrado de ...
                    </h3>

                    <h3 v-else="">
                        <v-icon>search</v-icon>
                        Nenhum explicador {{ searchedData ? '"' +searchedData+'"' : '' }} do nivel {{ selectedNivel }} encontrado em ...
                    </h3>

                    <h3>{{ filterType }} |  {{ filterName }} </h3>
                    <v-btn
                        round=""
                        flat=""
                        title="Clique aqui para enviar sugestão de registo ao explicador">
                        Conhece Algum explicador nessa área?
                    </v-btn>
                    <br>
                    <v-btn
                            round=""
                            flat=""
                            @click="onCloseFilter"
                    >
                        Voltar
                    </v-btn>
                </div>


            </v-flex>
        </v-layout>

        <explicador-help-dialog></explicador-help-dialog>

        <!--==============================================================================-->
        <!--======Dialog (01) para selecao do nivel  dos explicadores a serem visualizados=====-->
        <!--==============================================================================-->
        <v-dialog v-model="nivelDialog" v-if="$vuetify.breakpoint.width > 400" persistent="" maxWidth="500px">
            <v-card>
                <v-card-title>
                    <div>
                    <img width="20" style="background: #1565c0" src="https://user-images.githubusercontent.com/18400142/37566968-cfca5484-2ac8-11e8-89a2-3cec6e124af4.png" alt="Mindd"/>
                    </div>
                    <span class="ma-1">Temos explicadores de todos os níveis</span>
                    <v-spacer></v-spacer>
                    <v-menu bottom left class="ma-0 pa-0">
                        <v-btn icon slot="activator">
                            <v-icon>more_vert</v-icon>
                        </v-btn>
                        <v-list>
                            <v-list-tile @click="" title="Voltar para a pagina de apresentação">
                                <v-list-tile-title>Voltar</v-list-tile-title>
                            </v-list-tile>
                        </v-list>
                    </v-menu>
                </v-card-title>
                <v-card-text class="mb-">
                    <v-layout class="mb-5 text-xs-center">
                        <v-flex>

                            <p>Olá, os <b>Explicadores</b> estão a espera de si!</p>

                            <span class="headline">Procura Explicador de que nivel?</span>
                        </v-flex>
                    </v-layout>
                    <v-layout class="white--text mb-5">
                        <!--Btn Primario-->
                        <v-flex
                            xs12 sm5
                            @click="onSelectNivel('Primário')"
                            class="azul-principal ma-1 text-xs-center pt-4 elevation-1"
                            style="cursor: pointer;"
                            :style="{height: height}">
                            <!--<img width="30" src="https://user-images.githubusercontent.com/18400142/37566968-cfca5484-2ac8-11e8-89a2-3cec6e124af4.png" alt="Mindd"/>-->
                            Primario
                        </v-flex>

                        <!--Btn Secundario-->
                        <v-flex
                            xs12 sm5
                            @click="onSelectNivel('Secundário')"
                            class="azul-principal ma-1 text-xs-center pt-4 elevation-1"
                            style="cursor: pointer;"
                            :style="{height: height}" >
                            Secundário
                        </v-flex>

                        <!--Brn Universitario-->
                        <v-flex
                            xs12 sm5
                            @click="onSelectNivel('Superior')"
                            class="azul-principal ma-1 text-xs-center pt-4 elevation-1"
                            style="cursor: pointer;"
                            :style="{height: height}">
                            Universitário
                        </v-flex>

                        <!--Btn Tecnico-->
                        <v-flex
                            xs12 sm5
                            @click="onSelectNivel('Técnico')"
                            class="azul-principal ma-1 text-xs-center pt-4 elevation-1"
                            style="cursor: pointer;"
                            :style="{height: height}">
                            Técnico
                        </v-flex>

                        <!--Btn Outro-->
                        <v-flex
                            xs12 sm5
                            @click="onSelectNivel('Outro')"
                            class="azul-principal ma-1 text-xs-center pt-4 elevation-1"
                            style="cursor: pointer;"
                            :style="{height: height}">
                            Outro
                        </v-flex>
                    </v-layout>

                    <v-layout class="mt- text-sm-right">
                        <v-flex>

                            <p>
                                <b>Desejas um explicador a domicílio?</b>
                            </p>

                            <p>Envie-nos um email para <b>info@explicador.co.mz</b> com mais detalhes sobre o tipo de explicador que precisas.</p>
                        </v-flex>
                    </v-layout>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <!--<v-btn @click.stop="dialog=false">Fechar</v-btn>-->
                    <!--<v-btn round color="primary" @click.stop="dialog=false">Secundário</v-btn>-->
                    <!--<v-btn round color="primary" @click.stop="dialog=false">Universitário</v-btn>-->
                    <!--<v-btn round color="primary" @click.stop="dialog=false">Técnico</v-btn>-->
                    <!--<v-btn round color="primary" @click.stop="dialog=false">Outros</v-btn>-->
                </v-card-actions>
            </v-card>
        </v-dialog>

        <!--==============================================================================-->
        <!--=============================== Fim da dialog (01)============================-->
        <!--==============================================================================-->

    </v-container>
</template>

<script>
    import { eventBus } from '../main'
    export default {
        beforeCreate () {
            this.loading = true
            this.$Progress.start()
        },
        mounted () {

            if (this.users.length > 0)
            {
                this.$Progress.finish()
            }

            eventBus.$on('searchForExplicador',  (nameOrUniversity) => {

                this.searchedData = nameOrUniversity

            })

            eventBus.$on('mobileSelectNivelEvent',  (nivel) => {

                this.onSelectNivel(nivel)

            })

            //Metodo chamado no MainMenuLeft.vue e SearchResult.vue
            //Recebe item: o que deve ser procurado ex: 'maxaque c'
            //Recebe filterType: O tipo de filtro ex: 'Bairro'
            eventBus.$on('filterExplicadoresBy',  (item, filterType) => {

                this.filterType = filterType
                this.filterName = item

            })

            //Evento emitido no MenuLeft.vue e App.vue, este foi emitido duas
            //Remove o filtro que tiver sido aplicado, ou seja,
            //Se o user tiver escolhido visualizar os explicadores pelo Bairro,
            //Este eleminina o filtro e volta os dados para default
            eventBus.$on('resetExplicadoresFilter', () => {

                this.filterType = ''
                this.filterName = ''

            })

            this.canShowNivelDialog() //Este  vai verificar se mostramos ou nao a dialog

            let search_icon = document.getElementsByClassName('input-group__append-icon')
            search_icon[search_icon.length -1].setAttribute("style", "display: none;");

            let vm = this
            search_icon[search_icon.length -1].addEventListener('click', function () {
                vm.searchedData = ''
            })
            this.loading = false
        },
        data () {
            return {
                searchedData: '',
                filterType: '',
                loading: false,
                filterName: '',
                selectedNivel: 'Secundário',
                nivelDialog: false,
                closeDialogDefinitively: false,
                height: '90px',
                sortedUsers: []
            }
        },
        computed: {
            isLoading ()
            {
                return this.loading || this.$store.getters.loading
            },
            device ()
            {
                return this.$vuetify.breakpoint
            },
            users () {
                return this.$store.getters.getUsers
            },
            userAuth () {
                return this.$store.getters.userAuth
            },
            isUserAuth () {
                return this.userAuth !== null && this.userAuth !== undefined
            },
            userGuest () {
                return this.$store.getters.userGuest
            },
            isUserGuest () {
                return this.userGuest !== null && this.userGuest !== undefined
            },
            areUsers () {
                return this.users.length > 0
            },
            searchedUsers () {

                if (!this.areUsers)  return []

                this.sortedUsers = this.users.sort(function(a, b){return 0.5 - Math.random()});

                if (this.filterType === '' && this.filterName === '') {

                    return this.sortedUsers.filter(u => {
                        let array_result =  (u.name && u.name.toLowerCase().includes(this.searchedData.toLowerCase())
                                || this.searchByEducationAndLocation(u, this.searchedData)) //Pesquisa por quase tudo, veja o metodo
                                && this.doesHeExplainsLavel(u, this.selectedNivel) //Filtra apenas explicadores do nivel seleecionado (Primario, Secundario,...)


                        return array_result
                    })

                }
                else
                {
                    let array_result = []

                    if (this.filterType === '')
                    {
                        array_result = this.filterBySubject
                    }
                    else
                    {
                        array_result = this.filterAdvanced
                    }

                    return array_result
                }

            },
//          Dependendo da opcao que se clica, mostramos os explicadores correspondentes e eh nesse onde se fara pesquisa.
            filterAdvanced ()
            {
                return  this.filterType === 'Províncias' ? this.filterByProvince  :
                        this.filterType === 'Cidades' ? this.filterByCity  :
                        this.filterType === 'Distritos' ? this.filterByDistrict  :
                        this.filterType === 'Dist. Municipais' ? this.filterByMunicipalDistrict  :
                        this.filterType === 'Postos Administrativos' ? this.filterByPost  :
                        this.filterType === 'Bairros' ? this.filterByBairro  :
                        this.filterType === 'Disciplinas' ? this.filterBySubject  :
                        this.filterType === 'Cursos' ? this.filterByCourse :
                        this.filterType === 'Faculdades' ? this.filterByFaculty :
                        this.filterType === 'Universidades' ? this.filterByUniversity :
                        this.filterType === 'Departamentos' ? this.filterByDepartment :
                        this.filterType === 'Escolas' ? this.filterBySchool :
                        this.filterType === 'Classes' ? this.filterByExplainClass : []

            },

            filterByProvince () {

                return this.users.filter(u => {
                    return (this.hasName(u) && u.name.toLowerCase().includes(this.searchedData.toLowerCase())
                        || this.searchByEducationAndLocation(u, this.searchedData)) //Pesquisa por quase tudo, veja o metodo
                        && (this.hasLocation(u) && u.Locations.province && u.Locations.province === this.filterName)
                        && this.doesHeExplainsLavel(u, this.selectedNivel) //Filtra apenas explicadores do nivel seleecionado
                })

            },

            filterByCity () {

                return this.users.filter(u => {
                    return (this.hasName(u) && u.name.toLowerCase().includes(this.searchedData.toLowerCase())
                        || this.searchByEducationAndLocation(u, this.searchedData)) //Pesquisa por quase tudo, veja o metodo
                        && (this.hasLocation(u) && u.Locations.city && u.Locations.city === this.filterName)
                        && this.doesHeExplainsLavel(u, this.selectedNivel) //Filtra apenas explicadores do nivel seleecionado
                })

            },

            filterByDistrict () {

                return this.users.filter(u => {
                    return (this.hasName(u) && u.name.toLowerCase().includes(this.searchedData.toLowerCase())
                        || this.searchByEducationAndLocation(u, this.searchedData)) //Pesquisa por quase tudo, veja o metodo
                        && (this.hasLocation(u) && u.Locations.district && u.Locations.district === this.filterName)
                        && this.doesHeExplainsLavel(u, this.selectedNivel) //Filtra apenas explicadores do nivel seleecionado (Primario, Secundario,...)
                })

            },

            filterByMunicipalDistrict () {

                return this.users.filter(u => {
                    return (this.hasName(u) && u.name.toLowerCase().includes(this.searchedData.toLowerCase())
                        || this.searchByEducationAndLocation(u, this.searchedData)) //Pesquisa por quase tudo, veja o metodo
                        && (this.hasLocation(u) && u.Locations.municipalDistrict && u.Locations.municipalDistrict === this.filterName)
                        && this.doesHeExplainsLavel(u, this.selectedNivel) //Filtra apenas explicadores do nivel seleecionado
                        && this.doesHeExplainsLavel(u, this.selectedNivel) //Filtra apenas explicadores do nivel seleecionado (Primario, Secundario,...)
                })

            },

            filterByPost () {

                return this.users.filter(u => {
                    return (this.hasName(u) && u.name.toLowerCase().includes(this.searchedData.toLowerCase())
                        || this.searchByEducationAndLocation(u, this.searchedData)) //Pesquisa por quase tudo, veja o metodo
                        && (this.hasLocation(u) && u.Locations.post && u.Locations.post === this.filterName)
                        && this.doesHeExplainsLavel(u, this.selectedNivel) //Filtra apenas explicadores do nivel seleecionado (Primario, Secundario,...)
                })

            },

            filterByBairro () {

                return this.users.filter(u => {
                    return (this.hasName(u) && u.name.toLowerCase().includes(this.searchedData.toLowerCase())
                        || this.searchByEducationAndLocation(u, this.searchedData)) //Pesquisa por quase tudo, veja o metodo
                        && (this.hasLocation(u) && u.Locations.bairro && u.Locations.bairro === this.filterName)
                        && this.doesHeExplainsLavel(u, this.selectedNivel) //Filtra apenas explicadores do nivel seleecionado (Primario, Secundario,...)
                })

            },

            filterBySubject () {

                return this.users.filter(u => {
                    return (
                            this.hasName(u) && u.name.toLowerCase().includes(this.searchedData.toLowerCase())
                            || this.searchByEducationAndLocation(u, this.searchedData)
                        ) //Pesquisa por quase tudo, veja o metodo
                        &&
                        (
                            this.hasSubjects(u)
                            && this.hasSubject(u, this.filterName) //Filtra apenas explicadores do nivel seleecionado (Primario, Secundario,...)
                        )
                })
            },

            filterByCourse () {

                return this.users.filter(u => {
                    return (this.hasName(u) && u.name.toLowerCase().includes(this.searchedData.toLowerCase())
                        || this.searchByEducationAndLocation(u, this.searchedData)) //Pesquisa por quase tudo, veja o metodo
                        && (this.hasEducation(u) && u.Educations.course === this.filterName)
                        && this.doesHeExplainsLavel(u, this.selectedNivel) //Filtra apenas explicadores do nivel seleecionado (Primario, Secundario,...)

                })

            },

            filterByFaculty () {

                return this.users.filter(u => {
                    return (this.hasName(u) && u.name.toLowerCase().includes(this.searchedData.toLowerCase())
                        || this.searchByEducationAndLocation(u, this.searchedData)) //Pesquisa por quase tudo, veja o metodo
                        && (this.hasEducation(u) && u.Educations.faculty === this.filterName)
                        && this.doesHeExplainsLavel(u, this.selectedNivel) //Filtra apenas explicadores do nivel seleecionado (Primario, Secundario,...)

                })

            },

            filterByUniversity () {

                return this.users.filter(u => {
                    return (this.hasName(u) && u.name.toLowerCase().includes(this.searchedData.toLowerCase())
                        || this.searchByEducationAndLocation(u, this.searchedData)) //Pesquisa por quase tudo, veja o metodo
                        && (this.hasEducation(u) && u.Educations.institution === this.filterName)
                        && this.doesHeExplainsLavel(u, this.selectedNivel) //Filtra apenas explicadores do nivel seleecionado (Primario, Secundario,...)

                })

            },

            filterByDepartment () {

                return this.users.filter(u => {
                    return (this.hasName(u) && u.name.toLowerCase().includes(this.searchedData.toLowerCase())
                        || this.searchByEducationAndLocation(u, this.searchedData)) //Pesquisa por quase tudo, veja o metodo
                        && (this.hasEducation(u) && u.Educations.department && u.Educations.department === this.filterName)
                        && this.doesHeExplainsLavel(u, this.selectedNivel) //Filtra apenas explicadores do nivel seleecionado (Primario, Secundario,...)

                })

            },

            filterBySchool () {

                return this.users.filter(u => {
                    return (this.hasName(u) && u.name.toLowerCase().includes(this.searchedData.toLowerCase())
                        || this.searchByEducationAndLocation(u, this.searchedData)) //Pesquisa por quase tudo, veja o metodo
                        && (this.hasEducation(u) && u.Educations.userSchool && u.Educations.userSchool === this.filterName)
                        && this.doesHeExplainsLavel(u, this.selectedNivel) //Filtra apenas explicadores do nivel seleecionado (Primario, Secundario,...)

                })

            },

            filterByExplainClass () {


                return this.users.filter(u => {

                    let classes = (this.selectedNivel === 'Primário' && u.Explicador) ? u.Explicador.explainPrimaryClasses
                        : (this.selectedNivel === 'Secundário' && u.Explicador) ? u.Explicador.explainSecundaryClasses : []

                    return (this.hasName(u) && u.name.toLowerCase().includes(this.searchedData.toLowerCase())
                        || this.searchByEducationAndLocation(u, this.searchedData)) //Pesquisa por quase tudo, veja o metodo
                        && (u.Explicador && classes.includes(this.filterName))
                        && this.doesHeExplainsLavel(u, this.selectedNivel) //Filtra apenas explicadores do nivel seleecionado (Primario, Secundario,...)

                })

            }
        },
        watch: {
            users (value) {
                this.$Progress.finish()
//                if (value.length > 0)
//                {
//                    this.$store.dispatch('resizeAllUserPhotosAndStoreInAPI')
//                }

            },
            closeDialogDefinitively (value)
            {
                if (value)
                {
                    this.nivelDialog = false
                    this.closeDialogDefinitively = true
                }

            },
            nivelDialog (value)
            {
                if (!value)
                {
                    this.closeDialogDefinitively = true
                }
            },
            searchedUsers (value)
            {
                //Mostra os explicadores encontrado na pesquisa.
                eventBus.$emit ('advancedSearchResult', value) //evento tratado no ficheiro App.vue

                if (value.length > 0)
                {
                    this.$store.dispatch('setLoading', false)
//                  Evento trato no MainMenuLeft.vue
                    eventBus.$emit('totalExplicadoresFilter', this.users, this.selectedNivel);
                }
            },
            searchedData (value)
            {
                let search_icon = document.getElementsByClassName('input-group__append-icon')
                if (value)
                {
                    let vm = this
                    search_icon[search_icon.length -1].setAttribute("style", "display: block;");
                }
                else
                {
                    search_icon[search_icon.length -1].setAttribute("style", "display: none;");
                }
            }

        },
        methods: {

            onCloseFilter () {
                // Reseta os criterios de pesquisa de dados na pagina, para o default
                // Este evento eh tratado nos metodos created () dos Componentes Home.vue e MainMenuLeft.vue
                eventBus.$emit('resetExplicadoresFilter');

            },
            onSelectNivel (nivel)
            {

                this.selectedNivel = nivel
                this.closeDialogDefinitively = true
                this.firstNivelSelect(nivel)

            },

            //Grava a primeira escolha do utilizador como escolha preferida dos niveis dos explicadores que deseja visualizar
            //Visa nao mostrar sempre a dialog
            firstNivelSelect(nivel)
            {

                if (this.isUserAuth)
                {
                    let user = this.userAuth

                    user.Settings = {
                        preferedExplicadorNivels: nivel
                    }
                    this.$store.dispatch('updateOnlineUserData', user)
                }
                else
                {
                    let userGuest = this.userGuest
                    let user = this.userGuest ? this.userGuest : {}
                    user.Settings = {
                        preferedExplicadorNivels: nivel
                    }
                    this.$store.dispatch('guestUserConfig', user)
                }


            },

            //Verifica se podemos mostrar ou nao a dialog dos users aos visitantes
            canShowNivelDialog ()
            {

                let user = this.userAuth
                let userGuest = this.userGuest

                if ((user && user.Settings && user.Settings.preferedExplicadorNivels)
                    || (userGuest && userGuest.Settings && userGuest.Settings.preferedExplicadorNivels))
                {
                    this.selectedNivel = user ? user.Settings.preferedExplicadorNivels : userGuest.Settings.preferedExplicadorNivels
                    this.closeDialogDefinitively = true

                    return
                }
//                this.nivelDialog = !this.closeDialogDefinitively //para abrir a dialog
            },
            hasName (user)
            {
                return user.name
            },

            doesHeExplainsLavel (user, level)
            {
                if (level === '') return true

                return user.Explicador &&
                       user.Explicador.explainLevels &&
                       user.Explicador.explainLevels.includes(level)
            },
            hasEducation (user) {
                const userEducation = user.Educations
                return userEducation && userEducation !== null
            },

            hasLocation (user) {
                const userLocation = user.Locations
                return userLocation && userLocation !== null
            },

            // Chamando os demais metodos que fazem a pesquisa pelos dados educacionais
            searchByEducationAndLocation (u, searchedData)
            {
                return this.hasInstitution(u, searchedData)
                || this.hasSubject(u, searchedData)
                || this.hasFaculty(u, searchedData)
                || this.hasDepartment(u, searchedData)
                || this.hasCourse(u, searchedData)

                || this.searchByAllLocations(u, searchedData) //pesquisa por tudo que eh localizacao
            },

            // Chamando os demais metodos que fazem a pesquisa pelos dados educacionais
            searchByAllLocations (u, searchedData)
            {
                return this.hasProvince(u, searchedData)
                    || this.hasCity(u, searchedData)
                    || this.hasDistrict(u, searchedData)
                    || this.hasAdminstrativePost(u, searchedData)
                    || this.hasMunicipalDistrict(u, searchedData)
                    || this.hasBairro(u, searchedData)
                    || this.hasLocality(u, searchedData)

            },

//          Verificacao de dados de Localizacao
            hasProvince (user, search) {
                const userLocation = user.Locations
                return this.hasLocation(user)
                    && userLocation.province
                    && userLocation.province !== ''
                    && (userLocation.province.toLowerCase().includes(search.toLowerCase()))
            },

            hasCity (user, search) {
                const userLocation = user.Locations
                return this.hasLocation(user)
                    && userLocation.city
                    && userLocation.city !== ''
                    && (userLocation.city.toLowerCase().includes(search.toLowerCase()))
            },

            hasDistrict (user, search) {
                const userLocation = user.Locations
                return this.hasLocation(user)
                    && userLocation.district
                    && userLocation.district !== ''
                    && (userLocation.district.toLowerCase().includes(search.toLowerCase()))
            },

            hasAdminstrativePost (user, search) {
                const userLocation = user.Locations
                return this.hasLocation(user)
                    && userLocation.post
                    && userLocation.post !== ''
                    && (userLocation.post.toLowerCase().includes(search.toLowerCase()))
            },

            hasMunicipalDistrict (user, search) {
                const userLocation = user.Locations
                return this.hasLocation(user)
                    && userLocation.municipalDistrict
                    && userLocation.municipalDistrict !== ''
                    && (userLocation.municipalDistrict.toLowerCase().includes(search.toLowerCase()))
            },

            hasBairro (user, search) {
                const userLocation = user.Locations
                return this.hasLocation(user)
                    && userLocation.bairro
                    && userLocation.bairro !== ''
                    && (userLocation.bairro.toLowerCase().includes(search.toLowerCase()))
            },

            hasLocality (user, search) {
                const userLocation = user.Locations
                return this.hasLocation(user)
                    && userLocation.locality
                    && userLocation.locality !== ''
                    && (userLocation.locality.toLowerCase().includes(search.toLowerCase()))
            },

//            ======================= Educacionais ========================
//          Verificacao de dados educacionais
//            ===============================================================
            hasInstitution (user, search) {
                const userEducation = user.Educations
                return this.hasEducation(user)
                    && userEducation.institution
                    && userEducation.institution !== null
                    && (userEducation.institution.toLowerCase().includes(search.toLowerCase()))
            },
            hasSubjects (user) {

                return  user.Explicador
                        && user.Explicador.explainLevels
                        && user.Explicador.explainLevels.includes(this.selectedNivel)
                        && user.Explicador.subjectDetails
            },

            hasSubject (user, search) {

                const userSubjectsDetails = user.Explicador && user.Explicador.subjectDetails ? user.Explicador.subjectDetails : []
                let combine = user.name + '' + user.id

                return this.hasSomeSubject(userSubjectsDetails, search, combine)
            },

//            Verificando se o utilizador possui alguma disciplina
            hasSomeSubject (userSubjectsDetails, search, combine) {

                if (!userSubjectsDetails) return false //Se nao tiver disciplinas, logo fechamos aqui

                let res = null
                let combinedArray = []

                userSubjectsDetails.forEach((sdItem) => {

                    if (combinedArray.includes(combine)) return true //Verifica se o user ja foi incluido nos que explicam uma certa disciplina...

                    if (sdItem.level === this.selectedNivel && !combinedArray.includes(combine))
                    {
                        sdItem.subjects.forEach((s) => {
                            if (s.toLowerCase().includes(search.toLowerCase()))
                            {
                                res = true
                                combinedArray.push(combine)
                                return
                            }
                        })

                        if (res) return //interrompe o ciclo principal
                    }
                    res = false
                })
                return res
            },

            hasFaculty (user, search) {
                const userEducation = user.Educations
                return this.hasEducation(user)
                    && userEducation.faculty !== undefined
                    && userEducation.faculty !== null
                    && (userEducation.faculty.toLowerCase().includes(search.toLowerCase()))
            },

            hasCourse (user, search) {
                const userEducation = user.Educations
                return this.hasEducation(user)
                    && userEducation.course !== undefined
                    && userEducation.course !== null
                    && (userEducation.course.toLowerCase().includes(search.toLowerCase()))
            },

            hasDepartment (user, search) {
                const userEducation = user.Educations
                return this.hasEducation(user)
                    && userEducation.department !== undefined
                    && userEducation.department !== null
                    && (userEducation.department.toLowerCase().includes(search.toLowerCase()))
            },

        }
    }
</script>
<style scoped>
    .explicador-bar-fixed {
        position: fixed;
        z-index: 1;
        left: 0;
        width: 100%;
        /*left: 0;*/
    }
</style>


