import { store } from "../../store/index";
export default (to, from, next) => {

    const user = store.getters.userAuth

    if (user) { //Oculta os menus se estiver mostrando

        store.dispatch('togleHomePage',false)

        next ()

    } else {

        console.log('missing redirect in router/guards/setting-guard.js')

    }




}


