<template>
    <v-dialog v-model="dialog" width="350px">
        <!--Botao de activacao-->
        <v-btn
                color="red"
                block
                :class="{'ml-5' : $vuetify.breakpoint.name === 'xs'}"
                title="Remover a minha conta"
                slot="activator"
                @click.stop="mostrarDialog"
        >
            Remover a minha conta
        </v-btn>

        <v-card>
            <v-card-title
                    class="lighten-4 py-4 title"
            >
                Email e senha para a Remoção da conta
            </v-card-title>
            <v-divider></v-divider>
            <v-container grid-list-sm class="pa-4 mb-5">
                <v-layout row v-if="error">
                    <v-flex xs12>
                        <app-alert @dismissed="onDismissed" :text="error.message"></app-alert>
                    </v-flex>
                </v-layout>
                <v-layout row wrap class="mt-5">
                    <v-flex xs12>
                        <v-text-field
                                name="email"
                                label="Mail"
                                prepend-icon="mail"
                                v-model="email"
                                type="email"
                                required></v-text-field>
                    </v-flex>
                    <v-flex xs12>
                        <v-text-field
                                name="password"
                                label="Password"
                                prepend-icon="lock"
                                v-model="password"
                                type="password"
                                required></v-text-field>
                    </v-flex>
                </v-layout>
            </v-container>
            <v-divider></v-divider>
            <v-card-actions>

                <v-spacer></v-spacer>
                <v-btn flat color="primary" @click="dialog = false">Cancelar</v-btn>
                <v-btn
                        flat
                        @click="onRemoveAccount"
                        class="red--text"
                        :disabled="loading"
                        :loading="loading"
                >
                    Remover a conta

                    <span slot="loader" class="custom-loader">
                        <v-icon light>cached</v-icon>
                    </span>
                </v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>
<script>
    export default {
        data () {
            return {
                dialog: false,
                email: '',
                password: '',
                accountRemove: false, // Controlador para ver se as operacoes estao a ser feitas aqui ou nao
            }
        },
        computed: {
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            },
            isUserAuth () {
                return this.$store.getters.isUserAuth
            },
            didUserConfirm () {
                return this.$store.getters.didUserConfirm
            },
        },
        watch: {
            didUserConfirm (value) {
                this.$store.dispatch('setLoading', false)
                if (value)
                {
                    this.dialog = value //se o user confirmar, mostramos a dialog, para ele finalizar
                    this.$store.dispatch('userConfirmation', false) //Resetando a confirmacao do user
                }



                this.accountRemove = false

            }
        },
        methods: {
            onRemoveAccount () {

                this.$store.dispatch('deleteAuthUserAccount', {email: this.email, password: this.password})

            },
            onDismissed () {
                this.$store.dispatch('clearError')
            },

            /**
             *
             * Metodo que chama a dialog de confirmacao global
             *
             */
            mostrarDialog () {
                this.accountRemove = true

                const title = 'Remoção da conta'
                const message = 'Confirma a remoção da sua conta? não será possivel recuperar a sua conta depois desta acção!'
                const btn1 = 'Não'
                const btn2 = 'Sim'

                /**
                 * Primeiro param: true: significa abrir a dialog
                 * Segundo param:  title: o titulo a mostrar
                 * Terceiro param:  msg: a mensagem a mostrar
                 */
                this.$store.dispatch('togleConfirmDialog', {show: true, title: title, message: message,btn1: btn1,btn2: btn2})

            },

            registarOpt () {
                this.dialog = false
                this.$router.push('/register')
            }
        }
    }
</script>


