<template>

    <div style="width: 100%">

        <v-flex xs12 sm12>

            <v-card>

                <v-card-title class="blue darken-1">
                    <span class="headline white--text">Localização</span>

                    <v-spacer></v-spacer>
                    <div v-if="isMyProfile" class="white" style="border-radius: 100px; padding: 2px">
                        <v-progress-circular
                                :size="circularSize"
                                :width="7"
                                :rotate="360"
                                title="Percentagem de preenchimento"
                                style="float: right;cursor: pointer"
                                :value="personalInfoFillPercent"
                                :color="personalInfoFillPercent === 100 ? 'green': 'red'"
                                @click="toggle = !toggle"
                        >
                            <span v-if="personalInfoFillPercent === 100 && toggle">{{ personalInfoFillPercent }}</span>
                            <span v-else-if="personalInfoFillPercent === 100" title="Os seus dados de Localização estao actualizados!" ><v-icon>done</v-icon></span>
                            <span v-else="" title="Clique em editar, para preencher os dados">{{ personalInfoFillPercent }}</span>
                        </v-progress-circular>
                    </div>
                    <explicador-edit-location-dialog
                        v-if="isMyProfile"
                        :isMobile="true"
                        :user="user">
                    </explicador-edit-location-dialog>

                    <template v-else="">
                        <v-btn
                            fab=""
                            flat=""
                            small=""
                            outline=""
                            style="float: right;"
                        >
                            <v-icon class="white--text">more_horiz</v-icon>
                        </v-btn>
                    </template>
                </v-card-title>

                <v-list>

                    <!--Provincia-->
                    <v-list-tile @click="">
                        <v-list-tile-action>
                            <v-icon>location_on</v-icon>
                        </v-list-tile-action>
                        <v-list-tile-content>
                            <v-list-tile-title>{{ userProvince }}</v-list-tile-title>
                        </v-list-tile-content>
                        <!--<v-list-tile-action>-->
                        <!--<v-icon>chat</v-icon>-->
                        <!--</v-list-tile-action>-->
                    </v-list-tile>

                    <v-divider inset></v-divider>

                    <!--Distrito ou Cidade-->
                    <v-list-tile @click="">
                        <v-list-tile-action>
                            <v-icon>location_on</v-icon>
                        </v-list-tile-action>
                        <v-list-tile-content>

                            <v-list-tile-title v-if="isUser && hasSomeLocation &&  user.Locations.isFromMaputoCity">Cidade de Maputo</v-list-tile-title>
                            <v-list-tile-title v-else-if="isUser && hasSomeLocation &&  user.Locations.isFromMaputoProvince">Matola</v-list-tile-title>
                            <v-list-tile-title v-else>{{ userDistrict }}</v-list-tile-title>

                        </v-list-tile-content>
                        <!--<v-list-tile-action>-->
                        <!--<v-icon>chat</v-icon>-->
                        <!--</v-list-tile-action>-->
                    </v-list-tile>

                    <v-divider inset></v-divider>

                    <!--Localidade ou bairro-->
                    <v-list-tile @click="">
                        <v-list-tile-action>
                            <v-icon>location_on</v-icon>
                        </v-list-tile-action>
                        <v-list-tile-content>
                            <v-list-tile-title v-if="isUser && hasSomeLocation &&  (user.Locations.isFromMaputoCity || user.Locations.isFromMaputoProvince)">{{ userBairro }}</v-list-tile-title>
                            <v-list-tile-title v-else>{{ userLocality }}</v-list-tile-title>
                        </v-list-tile-content>
                    </v-list-tile>

                    <!--Informacao do Distrito Urbano ou posto administrativo-->
                    <v-list-tile
                        @click=""
                        v-if="isUser && hasSomeLocation &&  (user.Locations.isFromMaputoCity || user.Locations.isFromMaputoProvince)">
                        <v-list-tile-action>
                            <v-icon>location_on</v-icon>
                        </v-list-tile-action>
                        <v-list-tile-content>
                            <v-list-tile-title v-if="isUser && hasSomeLocation &&  user.Locations.isFromMaputoCity">{{ userMunicipalDistrict }}</v-list-tile-title>
                            <v-list-tile-title v-else-if="isUser && hasSomeLocation &&  user.Locations.isFromMaputoProvince">{{ userPost }}</v-list-tile-title>
                        </v-list-tile-content>
                    </v-list-tile>


                </v-list>
            </v-card>
        </v-flex>


    </div>

</template>

<script>
    export default {
        props: ['user', 'name', 'circularSize'],
        data: () => ({
            hidden: false,
            nameU: '',
            toggle: true,
        }),
        created () {
        },
        computed: {
            options () { //usado para fazer o scroll
                return {
                    duration: 1000,
                    offset: 0,
                    easing: 'easeInQuint'
                }
            },
            userAuth () {
                return this.$store.getters.userAuth
            },
            isUserAuth () {
                return this.userAuth !== undefined && this.userAuth !== null
            },
            isUser () {
                return this.user !== undefined && this.user !== null
            },
            isMyProfile () {
                return this.isUserAuth && this.isUser && this.user.id === this.userAuth.id
            },

            hasSomeLocation () {

                if ( !this.isUser ) return false

                return this.user.Locations

            },

            userLocality() {

                const negative = 'Localidade Não registada'

                if (!this.hasSomeLocation) return negative

                let userL = this.user.Locations.locality

                return userL !== null && userL && userL !== '' ? userL : negative

            },

            userBairro() {

                const negative = 'Bairro Não registado'

                if (!this.hasSomeLocation) return negative

                let userBairro = this.user.Locations.bairro

                return userBairro !== null && userBairro ? userBairro : negative

            },

            userPost() {

                const negative = 'Posto Administrativo Não registado'

                if (!this.hasSomeLocation) return negative

                let userPost = this.user.Locations.post //Posto administrativo

                return userPost !== null && userPost && userPost !== '' ? userPost : negative

            },

            userMunicipalDistrict() {

                const negative = 'Distrito Municipal Não registado'

                if (!this.hasSomeLocation) return negative

                let userMD = this.user.Locations.municipalDistrict

                return userMD !== null && userMD && userMD !== '' ? userMD : negative

            },

            userDistrict() {

                const negative = 'Distrito Não registado'

                if (!this.hasSomeLocation) return negative

                let userDistrict = this.user.Locations.district

                return userDistrict !== null && userDistrict && userDistrict !== '' ? userDistrict : negative

            },

            userProvince () {

                const negative = 'Provincia Não registada'

                if (!this.hasSomeLocation) return negative

                let userProv = this.user.Locations.province

                return userProv !== null && userProv && userProv !== '' ? userProv : negative

            },

            userCity () {

                const negative = 'Cidade Não registada'

                if (!this.hasSomeLocation) return negative

                let userCity = this.user.Locations.city

                return userCity !== null && userCity !== undefined && userCity !== '' ? userCity : negative

            },

            personalInfoFillPercent ()
            {
                let totalPercent = 0

                if (this.userProvince !== 'Provincia Não registada')
                {
                    totalPercent = 25
                }
                if (this.userMunicipalDistrict !== 'Distrito Municipal Não registado' || this.userDistrict !== 'Distrito Não registado')
                {
                    totalPercent += 25
                }
                if (this.userBairro !== 'Bairro Não registado' || this.userLocality !== 'Localidade Não registada')
                {
                    totalPercent += 50
                }

                return totalPercent
            }

        },
        methods: {

            scrollTo (top) {

                window.scroll({
                    top: top,
                    left: 10,
                    behavior: 'smooth'
                });

                // Scroll to a certain element
//                document.querySelector(el).scrollIntoView({
//                    behavior: 'smooth'
//                });

            },

        },
    }
</script>
<style scoped>
    div.card__title {
        margin-top: 0px;
        padding: 0;
        font-size: 8pt;
    }
    .list__tile__title {
        font-size: 11px!important;
    }
    .headline {
        margin-left: 1em;
    }
    .explicador-bar-fixed {
        z-index: 0;
    }
    .explicador-edit-item {
        float: right;
        cursor: pointer
    }
    .icon {
        font-size: 18px;
    }
    .img-radius {
        width: 50px;
        height: 50px!important;
    }
    .container {
        padding: 0px;
    }


</style>


