<template>

    <v-container fluid class="white text-xs-center">

        <h3 class="mt-3">A página de ajuda está em implementação ...</h3>
        <p class="mt-3">Qualquer dúvida, envie por email para: <b>info@explicador.co.mz</b></p>
        <p class="mt-3"><a href="https://docs.google.com/forms/d/e/1FAIpQLSerGzBmhk9hdPUgC9awFRP6bdNETUtfo5rUG8MuywPmqtWIVA/viewform?usp=sf_link" target="_blank">Ou clique neste link, para deixar qualquer observacao</a> </p>
        <p><v-btn to="/" class="azul-principal white--text">Voltar</v-btn></p>
        <h4 :style="$vuetify.breakpoint.mdAndUp ? 'margin-top: 280px' : 'margin-top: 180px'">Obrigado.</h4>
        <p>Enquipe do Explicador</p>
        <v-layout v-if="false">
            <!--Nav-bar esquerdo-->
            <v-flex xs2 offset-xs1 class="hidden-xs-only">
                <v-layout class="explicador-bar-fixed mt-1">
                    <v-flex xs2 offset-xs1 >
                        <v-btn block="" flat="" @click="scrollTo('#boas-vindas')">Boas vindas  </v-btn>
                        <v-btn block="" flat="" @click="scrollTo('#introducao')">Introdução  </v-btn>
                        <v-btn block="" flat="" @click="scrollTo('#criacao-conta')">Criação de conta</v-btn>
                        <v-btn block="" flat="" @click="scrollTo('#explicador')">Explicador  </v-btn>
                    </v-flex>
                </v-layout>
            </v-flex>

            <!--Informacao central-->
            <v-flex xs12 sm7>
                <v-container>
                    <v-layout>
                        <v-flex xs12>

                            <!--Boas vindas-->
                            <v-card tile id="boas-vindas">
                                <v-card-text>
                                    <v-list subheader class="text-xs-left">
                                        <p class="headline ma-4">Seja bem vindo!</p>

                                        <p class="text-xs-left ma-4">

                                            Olá, voçê é um estudante ou encarregado de educação? de que escola ou faculdade é?
                                            Esta é a sua plataforma online de Explicadores, aqui podes criar a sua conta e registar
                                            as suas disciplinas caso sejas um explicador, se fores estudante (não explicador) ou encarregado
                                            de educação, vais encontrar nessa página o criterio para procurar os melhores ou explicadores de
                                            confiança.

                                        </p>

                                        <p class="headline ma-4">Qual é o objectivo dessa plataforma?</p>

                                        <p class="text-xs-left ma-4">
                                            Modelado e desenhado principalmente para os estudantes das faculdades da <b>Universidade Eduardo
                                            Mondlane</b> o <b>Explicador</b> tem objectvio, de facilitar o encontro entre os bons estudantes
                                            desta universidade, que gostam de partilhar os seus conhecimentos, com os colegas que realmente precisam.

                                        </p>


                                        <p class="text-xs-left ma-4 nota2">
                                            No <span class="">Explicador</span> voçê vai encontrar os explicadores que precisa para tirar
                                            boas notas
                                        </p>

                                        <p class="text-xs-left ma-4 headline nota1">
                                            Então não perca tempo, crie já a sua conta.
                                        </p>

                                    </v-list>
                                </v-card-text>

                                <!--<div style="flex: 1 1 auto;"/>-->
                            </v-card>

                            <!--Introducao-->
                            <v-card tile id="introducao">
                                <v-card-text>
                                    <v-list subheader class="text-xs-left">
                                        <p class="headline ma-4">Introdução</p>

                                        <p class="text-xs-left ma-4">

                                            Como qualquer nova tecnologia ou arquitectura ha necessidade de se facilitar a compreensao da
                                            mesma, Por esse motivo, faça boa leitura dessa documentação para melhor explorar  ou fazer uso
                                            das diversas funcionalidades que a sua plataforma dispõe.

                                        </p>

                                        <p class="text-xs-left ma-4">

                                            Iremos explicar como podes criar a sua conta, confirmar o seu registo, solicitar contacto de um explicador,...

                                        </p>

                                    </v-list>
                                </v-card-text>

                                <!--<div style="flex: 1 1 auto;"/>-->
                            </v-card>

                            <v-card tile id="criacao-conta">
                                <v-card-text>
                                    <v-list subheader class="text-xs-left">
                                        <p class="headline ma-4">Criação de conta</p>

                                        <p class="text-xs-left ma-4">

                                            O processo de criação de conta é muito simples, basta seguir apenas os passos asseguir.

                                        </p>

                                        <p class="text-xs-left ma-4">

                                            Iremos explicar como podes criar a sua conta, confirmar o seu registo, solicitar contacto de um explicador,...

                                        </p>

                                    </v-list>
                                </v-card-text>

                                <!--<div style="flex: 1 1 auto;"/>-->
                            </v-card>

                            <!--Documentacao de explicador-->
                            <v-card tile id="explicador">
                                <v-card-text>
                                    <v-list three-line subheader>
                                        <v-subheader>Explicador</v-subheader>

                                        <v-list-tile avatar>
                                            <v-list-tile-content><v-list-tile-sub-title>
                                                Olá, leia o que significam as opções asseguir sobre o explicador
                                            </v-list-tile-sub-title>
                                            </v-list-tile-content>
                                        </v-list-tile>

                                        <!--Informacao sobre ver perfil-->
                                        <v-list-tile avatar>
                                            <v-list-tile-content>
                                                <v-list-tile-title>Ver Perfil</v-list-tile-title>
                                                <v-list-tile-sub-title>
                                                    Quando voçê clica no <b>ver perfil</b> sobre um certo explicador, será levado para uma página de perfil
                                                    desse explicador, onde poderá ver: <b>Os dados pessoais, académicos, as disciplinas, localização,...</b>
                                                    do explicador que voçê clicou.
                                                </v-list-tile-sub-title>
                                            </v-list-tile-content>
                                        </v-list-tile>

                                        <!--Informacao sobre disciplinas-->
                                        <v-list-tile avatar>
                                            <v-list-tile-content>
                                                <v-list-tile-title>Disciplinas</v-list-tile-title>
                                                <v-list-tile-sub-title>
                                                    Ao clicar nessa opção, serão listadas todas as disciplinas do explicador,
                                                    mas também podes ver as mesmas no perfil do explicador.
                                                </v-list-tile-sub-title>
                                            </v-list-tile-content>
                                        </v-list-tile>

                                        <!--Informacao sobre Marcar-->
                                        <v-list-tile avatar>
                                            <v-list-tile-content>
                                                <v-list-tile-title>Marcar</v-list-tile-title>
                                                <v-list-tile-sub-title>
                                                    Marcar explicador, é adicionar o explicador na lista dos seus explicadores preferidos
                                                    Mas só pode <b>Marcar</b> um explicador se voçê estiver autenticado. (Opção não disponivel, ainda estamos a implementar essa funcionalidade).
                                                </v-list-tile-sub-title>
                                            </v-list-tile-content>
                                        </v-list-tile>
                                    </v-list>
                                    <v-divider></v-divider>

                                    <v-list>
                                        <v-list-tile avatar>
                                            <v-list-tile-content>
                                                <v-list-tile-sub-title>
                                                    Essa ajuda sempre estará disponivel para si, consulte-a sempre que precisares.
                                                </v-list-tile-sub-title>
                                            </v-list-tile-content>
                                        </v-list-tile>

                                        <v-list-tile avatar>
                                            <v-list-tile-content>
                                                <v-list-tile-sub-title>
                                                    Para mais informações (dúvidas, sugestões,...) envie-nos um email para <b>explicadores.mz@gmail.com</b>
                                                </v-list-tile-sub-title>
                                            </v-list-tile-content>
                                        </v-list-tile>
                                    </v-list>
                                </v-card-text>

                                <div style="flex: 1 1 auto;"/>
                            </v-card>

                        </v-flex>
                    </v-layout>
                </v-container>
            </v-flex>


        </v-layout>

    </v-container>

</template>

<script>
    import { eventBus } from '../../main'
    export default {
        data() {
            return {

            }
        },
        methods: {

            scrollTo (el) {

//                window.scroll({
//                    top: el,
//                    left: 10,
//                    behavior: 'smooth'
//                });

                // Scroll to a certain element
                document.querySelector(el).scrollIntoView({
                    behavior: 'smooth'
                });

            }

        },
        beforeCreate() {
//            eventBus.$emit('ocultarMenus', false); //Esse evento eh tratado no ficheiro App.vue,  eh onde sao ocultados os menus
            this.$Progress.start()
            this.$Progress.set(70)


        },
        mounted () {
        }
    }
</script>
<style>

    .nota1 {
        font-size: 30pt;
        color: darkblue;
        border-left: solid darkblue medium;
        padding-left: 15px;
    }
    .nota2 {
        font-size: 12pt;
        font-weight: bold;
        color: grey;
        border-left: solid darkblue medium;
        padding-left: 15px;
    }
    .explicador-text-info {
        font-weight: 200;
        font-size: 14pt;
        color: red;
    }
</style>


