<template>

        <!--Design para mobile, tablets, ipads, computadores e outros-->
        <v-container fluid :class="{'pt-1': $vuetify.breakpoint.mdAndUp }">

            <v-layout>

                <!--Nav-bar fixo esquerdo que esconde nos dispositivos moveis-->
                <v-flex xs2 offset-xs2 class="hidden-md-and-down">
                    <v-layout class="explicador-bar-fixed">
                        <v-flex xs2 offset-xs2>
                            <v-layout row>

                                <!--Apresentacao do explicador aqui.-->
                                <v-layout>
                                    <v-flex xs12 class="white elevation-5  pb-">

                                        <!--Informacao do meu perfil-->
                                        <v-layout v-if="isMyProfile">
                                            <v-flex xs12 class="white--text green text-xs-center">

                                                <v-layout>
                                                    <!--Nome do explicador-->
                                                    <v-flex xs12 class="ma-0 pa-0">
                                                        <!--Nome do utilizador-->
                                                        <h3>Meu Perfil</h3>

                                                    </v-flex>

                                                </v-layout>

                                            </v-flex>
                                        </v-layout>

                                        <v-divider></v-divider>

                                        <!--Imagem-->
                                        <v-layout class="mt-2 pa-2">
                                            <!--Foto-->
                                            <v-flex xs3>
                                                <!--Foto de utilizador-->
                                                <img :src="img" class="img-radius"/>

                                            </v-flex>

                                            <!--Nome do explicador-->
                                            <v-flex xs9 class="ml-2 orange--text">

                                                <!--Nome do utilizador-->
                                                <h2>{{ nomeUser }}</h2>

                                            </v-flex>

                                        </v-layout>

                                        <v-divider class=""></v-divider>

                                        <!--Alguns dados academicos-->
                                        <v-layout>

                                            <v-flex class="mt-3 pa-2">
                                                <!--Instituicao do utilizador-->
                                                <h4 class="pb-2 blue--text"><v-icon>school</v-icon> {{ userInstitution }}</h4>
                                                <h4 class="pb-2 grey--text"><v-icon>school</v-icon> {{ userCourse }}</h4>
                                                <h4 class="pb-2 green--text"><v-icon>school</v-icon> {{ userDepartment }}</h4>

                                            </v-flex>

                                        </v-layout>
                                    </v-flex>
                                </v-layout>

                            </v-layout>
                            <v-layout>
                                <v-flex>

                                    <v-btn block color="white" router v-if="totalSubjects > 0" @click="scrollTo(480)" class="azul-principal--text">Disciplinas</v-btn>

                                    <!--&lt;!&ndash;Btn contacto do explicador&ndash;&gt;-->
                                    <!--<v-btn if="!isMyProfile" class="white&#45;&#45;text" color='blue' @click="scrollTo('#contact')" block="" title="Ver os contactos do explicador">-->
                                        <!--<v-icon left dark>phone</v-icon>-->
                                        <!--Ver Contactos-->
                                    <!--</v-btn>-->

                                    <v-btn block color="white" router @click="scrollTo('#dadosPessoais')" class="azul-principal--text">Informações pessoais</v-btn>

                                    <!--<v-btn v-if="isMyProfile" block color="white" router @click="scrollTo('#fotoDePerfil')" class="azul-principal&#45;&#45;text">Imagem do Perfil</v-btn>-->

                                    <v-btn block color="white" router @click="scrollTo('#dadosAcademicos')" class="azul-principal--text">Informações Académicas</v-btn>

                                    <v-btn block color="white" router @click="scrollTo('#localizacao')" class="azul-principal--text">Localizacao</v-btn>

                                    <v-btn block color="white" router v-if="totalSubjects <= 0" @click="scrollTo(1000)" class="azul-principal--text">Disciplinas</v-btn>

                                </v-flex>
                            </v-layout>

                            <!--<v-btn round outline block router >Testemunhos</v-btn>-->

                        </v-flex>
                    </v-layout>
                </v-flex>

                <!--Div que coloca espaco no dispositivo medio-->
                <v-flex md1 v-if="$vuetify.breakpoint.mdAndDown"></v-flex>

                <!--Informacao central nao fixa-->
                <v-flex  xs12 md10 lg6 class="mb-3" :class="{ 'ml-2': $vuetify.breakpoint.mdAndUp }" >

                    <!--Corpo-->
                    <v-card-text class="ma-0 pa-0 lighten-5 text-xs-center">

                        <!--Cabecalho de cima.-->
                        <template>
                            <!--Card Azul com a foto-->
                            <v-layout class="ma-0">
                                <v-flex xs12 class="blue" style="height: 220px">

                                    <h3 v-if="isMyProfile" style="position: absolute;margin: 20px 10px"
                                        class="white--text">Meu perfil</h3>

                                    <explicador-show-contact-dialog
                                        v-else-if="user"
                                        :user="user"
                                        :onExplicadorTopProfile="true"></explicador-show-contact-dialog>

                                    <v-card width="100px"
                                        :style="isMyProfile ? 'margin: 170px auto' : 'margin: 150px auto'">
                                        <a v-if="isUser" :href="user.photoURL" target="_blank" title="Clique para ver a imagem completa">
                                            <v-card-media :src="img" height="100px">

                                            </v-card-media>
                                        </a>

                                        <!--Botao para editar a foto de perfil-->
                                        <v-btn
                                            v-if="isMyProfile"
                                            class="edit-photo-icon"
                                            color="orange"
                                            icon
                                            title="Alterar foto de perfil"
                                            @click.prevent.stop="showDialog = !showDialog">
                                            <v-icon>edit</v-icon>
                                        </v-btn>
                                        <profile-photo-upload v-show="false" :user="user" @closing="fechandoDialog" :showDialog="showDialog"></profile-photo-upload>
                                    </v-card>


                                </v-flex>


                            </v-layout>

                            <!--Card de baixo-->
                            <v-layout class="red ma-0">

                                <v-flex xs12 class="white" style="height: 230px">

                                    <div style="margin-top: 80px"></div>
                                    <h3> {{ nomeUser }}</h3>
                                    <h4> {{ userCourseOrClass }}</h4>
                                    <h4> {{ userInstitution }}</h4>
                                    <h4> {{ userLocation }} </h4>

                                </v-flex>

                            </v-layout>

                            <!--Total disciplinas-->
                            <v-layout class="ma-0">

                                <v-flex xs12>

                                    {{ totalSubjects }} disciplinas

                                </v-flex>

                            </v-layout>
                        </template>

                        <!--Disciplinas-->
                        <v-layout  v-if="totalSubjects > 0" id="disciplinas" class="mt-2" justify-center>

                            <mobile-profile-subjects-info :user="user" :circularSize="circularSize"></mobile-profile-subjects-info>

                        </v-layout>

                        <!--Dados Pessoais-->
                        <v-layout id="dadosPessoais" class="mt-2" justify-center>

                            <mobile-profile-personal-info :user="user" :circularSize="circularSize"></mobile-profile-personal-info>

                        </v-layout>

                        <!--Dados Academicos-->
                        <v-layout id="dadosAcademicos" class="mt-2" justify-center>

                            <mobile-profile-academic-info :user="user" :circularSize="circularSize"></mobile-profile-academic-info>

                        </v-layout>

                        <!--Localizacao-->
                        <v-layout id="localizacao" class="mt-2" justify-center>

                            <mobile-profile-location-info :user="user" :circularSize="circularSize"></mobile-profile-location-info>

                        </v-layout>

                        <!--Disciplinas-->
                        <v-layout v-if="totalSubjects <= 0" id="disciplinas" class="mt-4 mb-4" justify-center>

                            <mobile-profile-subjects-info
                                :showSubjectsEditDialog="showSubjectsEditDialog"
                                :user="user"
                                :circularSize="circularSize"></mobile-profile-subjects-info>

                        </v-layout>

                        <!--Opcao de inquerito-->
                        <v-layout v-if="isMyProfile" id="inquerito-item" class="mt-3 mb-2" justify-center>

                            <div style="width: 100%">

                                <v-flex xs12 sm12>
                                    <v-card>
                                        <!--Cabecalho-->
                                        <v-card-title class="darken-1 pa-0">
                                            <v-btn
                                                    title="Clique para sugerir o registo de informacoes que faltam"
                                                    small=""
                                                    flat=""
                                                    color="blue"
                                                    target="_blank"
                                                    href="https://docs.google.com/forms/d/e/1FAIpQLSerGzBmhk9hdPUgC9awFRP6bdNETUtfo5rUG8MuywPmqtWIVA/viewform?usp=sf_link"
                                            >
                                                Clique aqui para deixar a sua observação Ou nos dizer o que falta.

                                            </v-btn>

                                            <v-spacer></v-spacer>

                                            <v-btn
                                                title="Clique para sugerir o registo de informacoes que faltam"                                                small=""
                                                icon=""
                                                target="_blank"
                                                href="https://docs.google.com/forms/d/e/1FAIpQLSerGzBmhk9hdPUgC9awFRP6bdNETUtfo5rUG8MuywPmqtWIVA/viewform?usp=sf_link"
                                            >
                                                <v-icon color="blue" medium="">info</v-icon>
                                            </v-btn>

                                        </v-card-title>
                                    </v-card>
                                </v-flex>

                            </div>

                        </v-layout>

                        <!--Seccao de confirmacao de email-->
                        <v-layout v-if="isMyProfile && userVerified" id="email-verify" class="mt-3 mb-3 elevation-2">

                            <v-flex xs12>
                                <v-expansion-panel>
                                    <v-expansion-panel-content expand-icon="infos">

                                        <div slot="header">
                                                    <span style="right: 5px;  margin-left: 10px; font-size: 13pt; color: darkblue">
                                                        Voçê ainda não verificou o seu email!
                                                    </span>
                                            <br>
                                            <v-btn
                                                    small=""
                                                    round=""
                                                    flat=""
                                                    color="green"
                                                    title="Clique para te enviarmos o link de verificacao"
                                                    @click.stop="sendVerificationLink"
                                            >
                                                Enviar Link de verificacao

                                                <v-icon>mail</v-icon>
                                            </v-btn>
                                            <!--<div class="mr-"></div>-->
                                            <v-btn
                                                    small=""
                                                    flat=""
                                                    color="red"
                                            >
                                                <v-icon>info</v-icon>
                                                Saiba mais
                                            </v-btn>
                                        </div>
                                        <v-card>
                                            <v-card-text>
                                                <h3>Porque confirmar os dados?</h3>
                                                <br>
                                                <br>
                                                <p>Utilizadores com dados não confirmados:</p>
                                                <ul class="ml-5">
                                                    <li>
                                                        <p>Não serão confiados</p>
                                                    </li>
                                                    <li>
                                                        <p>Poderao não ser informados sobre muitas novidades</p>
                                                    </li>
                                                    <li>
                                                        <p>Serão ocultados ou serão os ultimos a ser alistados</p>
                                                    </li>
                                                </ul>
                                                <br>
                                                <p  class="pa-2" style="border-left: 2px solid blueviolet; background: rgba(200,200,200,.2)">A confirmação de dados é importante, porque dá certeza as pessoas que te procuram de que os seus dados de contactos são válidos.</p>
                                                <br>
                                                <h3>Que dados confirmar?</h3>
                                                <br>
                                                <p>Por enquanto confirme apenas o seu email! é bom que faças isso urgente para não perder nada.</p>
                                                <b>Confirme o seu email, clique no botao acima para te enviarmos para o seu email o link de verificação!</b>

                                                <br>
                                                <br>
                                                <p class="pa-2" style="border-left: 2px solid blueviolet; background: rgba(200,200,200,.1)">Pode <b @click.prevent="sendVerificationLink">Clicar aqui</b>,
                                                    vai para o seu email, clicar no link longo que vamos te enviar, esse e o link de verificação
                                                </p>
                                            </v-card-text>
                                        </v-card>
                                    </v-expansion-panel-content>
                                </v-expansion-panel>
                            </v-flex>

                        </v-layout>


                        <v-layout class="mt-2 elevation-5 hidden-md-and-up" >
                            <v-flex xs12>
                                <p class="mt-2" style="font-size: 8pt;">
                                    Para ver o perfil completo acesse pelo computador</p>
                                <p><a href="https://explicador.co.mz">explicador.co.mz</a></p>
                            </v-flex>
                        </v-layout>
                    </v-card-text>

                </v-flex>

            </v-layout>


        </v-container>


</template>

<script>
    import { eventBus } from './../../main'
    export default {
        props: ['user', 'name'],
        data: () => ({
            hidden: false,
            nameU: '',
            showDialog: false,
            showSubjectsEditDialog: false,
            isThis: false,
            circularSize: 40,
        }),
        mounted () {
            let vm = this
            setTimeout(function(){ vm.saveNewView() }, 10000); //Se o user leva pelo menos 1min a
            // visualizar o perfil de alguem, entao ele ganha mais uma visualizacao...x`

            //Evento emitido no Componente App.vue
            eventBus.$on('showDialogSujects', ()=> {

                this.showSubjectsEditDialog = true

            })

            //evento emitido no dialogs/OtherProfileEditSubjectDialog.vue
            eventBus.$on('resetShowDialogSujects', () => {

                this.showSubjectsEditDialog = false

            })

        },

        created () {

            this.scrollTo(0)

            if (this.isUserAuth && !this.userAuth.updated_at)
            {
                let vm = this
                setTimeout(function () { vm.mostrarDialog('Welcome') }, 5000)

            }
            else if (this.isUserAuth && this.isConfirmMailSent)
            {
                let vm = this
                setTimeout(function(){ vm.mostrarDialog() }, 20000);

            }

            //Verificar aqui se eh o primeiro login, para parabenizar.



        },
        computed: {
            options () { //usado para fazer o scroll
                return {
                    duration: 1000,
                    offset: 0,
                    easing: 'easeInQuint',
                    controlRoute: false,
                }
            },
            isUser () {
                return this.user !== undefined && this.user !== null
            },
            smallImageUrl ()
            {
                return this.$store.getters.getSmallImageUrl
            },
            img () {

                return this.isUser && this.user.photoURLServer ? this.smallImageUrl + '' + this.user.photoURLServer :
                    this.isUser ? this.user.photoURL : '//ssl.gstatic.com/s2/oz/images/sge/grey_silhouette.png'
            },
            nomeUser() {
                return this.isUser && this.user.name !== null && this.user.name !== '' ? this.user.name : 'Sem Nome'
            },
            totalSubjects ()
            {
                let subjects = this.isUser && this.user.Explicador && this.user.Explicador.subjectDetails ? this.user.Explicador.subjectDetails : []
                let total = 0
                subjects.forEach((sbs) => {
                    total += sbs.subjects.length
                })
                return total
            },
            userLocation() {

                const negative = 'Ainda nao registou a localizacao'

                if (!this.isUser || !this.user.Locations) return negative

                let userBairro = this.user.Locations.bairro
                let isFromMaputoCity = this.user.Locations.isFromMaputoCity
                let isFromMaputoProvince = this.user.Locations.isFromMaputoProvince
                let district = this.user.Locations.district
                let locality = this.user.Locations.locality

                return (isFromMaputoCity || isFromMaputoProvince) && userBairro !== null && userBairro
                        ? userBairro : (locality)
                        ? locality : district
                        ? district : negative

            },
            userInstitution () {

                const defaultTitle = 'Sem Instituicao'

                if (!this.isUser || !this.user.Educations) return defaultTitle

                let userEd = this.user.Educations

                let inst = userEd.level === 'Primário' ? userEd.userSchool : defaultTitle
                inst = userEd.level === 'Secundário' ? userEd.userSchool : inst
                inst = userEd.level === 'Técnico' ? userEd.institution : inst
                inst = userEd.level === 'Superior' ? userEd.institution : inst
                inst = userEd.level === 'Outro' ? userEd.institution : inst

                return inst
            },
            userCourseOrClass () {

                const defaultTitle = 'Sem Faculdade'

                if (!this.isUser || !this.user.Educations) return defaultTitle

                let userEd = this.user.Educations

                let inst = userEd.level === 'Primário' ? userEd.userClass : defaultTitle
                inst = userEd.level === 'Secundário' ? userEd.userClass : inst
                inst = userEd.level === 'Técnico' ? userEd.courseNivel : inst
                inst = userEd.level === 'Superior' ? userEd.courseNivel : inst
                inst = userEd.level === 'Outro' ? userEd.userSchool : inst

                return inst
            },
            userAuth () {
                return this.$store.getters.userAuth
            },


            isUserAuth () {
                return this.userAuth !== null && this.userAuth !== undefined
            },
            isMyProfile () {
                return this.isUserAuth && this.isUser && this.user.id === this.userAuth.id
            },
            userVerified () {
                return !this.user.emailVerified
            },
            gender () {

                const negative = 'Sexo nao definido'

                if (!this.isUser) return negative

                let genero = this.user.gender

                return genero && genero !== null && genero && genero !== '' ? genero : negative


            },
            userExplainSome () {

                if (!this.isUser) return false

                return this.user.Explicador && this.user.Explicador.explainLevels
            },

            userFaculty () {

                const negative = 'Sem Faculdade'

                if (!this.isUser) return negative

                let ed = this.user.Educations
                return ed && ed.faculty ?  'Faculdade de ' + ed.faculty : negative
            },
            userCourse () {

                const negative = 'Sem Curso'

                if (!this.isUser) return negative

                let ed = this.user.Educations

                return ed && ed.course ? 'Curso de ' + ed.course : negative

            },
            userDepartment () {

                const negative = 'Sem Departamento'

                if (!this.isUser) return negative

                let ed = this.user.Educations

                return ed && ed.department ? ed.department : negative

            },
            userNivel () {

                const negative = 'Sem Nivel'

                if (!this.isUser) return negative

                let ed = this.user.Educations

                return ed && ed.nivel ? ed.nivel : negative


            },
            hasSubjects () {

                if ( !this.isUser ) return false

                return this.user && this.user.Explicador && this.user.Explicador.subjectDetails && this.user.Explicador.subjectDetails.length
            },

            route() {
                return this.$route
            },
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            },

            isConfirmMailSent () {
                return this.$store.getters.getConfirmMailSent
            },

        },
        watch: {

            route(value) {

                window.location.reload(false);

            },
            loading (value) {
                if (!value && !this.error && this.isThis) {

                    this.mostrarDialog()
                    this.isThis = false

                }
            }

        },
        methods: {

            scrollTo (el) {

                if (isNaN(el))
                {
//                    Scroll to a certain element
                    document.querySelector(el).scrollIntoView({
                        behavior: 'smooth'
                    });
                }
                else
                {
                    window.scroll({
                        top: el,
                        left: 10,
                        behavior: 'smooth'
                    });
                }




            },

            saveNewView ()
            {
                if (this.route.name === 'Profile' && this.isUser && !this.isMyProfile)
                {
                    //Depois de 1min a se visualizar o perfil... - done
                    //Verificamos se essa visualizacao de hoje ja foi adicionada na base de dados
                    //adicionamos mais um no numero de visualizacoes...
                    //Gravamos na base de dados

                    let user = this.user;

                    let whoSawTheProfile = this.isUserAuth ? this.userAuth.id : 'guest'; //Buscamos o id do user que viu o perfil ou apenas visitante

                    let today = new Date() //data da visualizacao


                    //verificar se ja tinha recebido visualizacoes
                    if (user.Profile && user.Profile.views)
                    {
                        let userViews = user.Profile.views

                        let recentViewObject = userViews[userViews.length - 1] //Ultima visualizacao
                        let viewsDate = new Date(recentViewObject.date) //Data da ultima visualizacao

                        let d1 = new Date(viewsDate.getFullYear(), viewsDate.getMonth(), viewsDate.getDate()); //data da visualizacao
                        let d2 = new Date(today.getFullYear(), today.getMonth(), today.getDate()); //data de hoje

                        if (d1.getTime() === d2.getTime()) //se as datas forem as mesmas, precisamos verificar se esse ainda nao foi add.
                        {
                            if ( !(recentViewObject.usersId.includes(whoSawTheProfile)) || whoSawTheProfile === 'guest')
                            {
                                //Ja existem visualizacoes de hhoje
                                //pode gravar a nova visualizacao

                                recentViewObject.usersId.push(whoSawTheProfile)

                                this.verifyViewsAndSendEmailNotification (userViews)
                            }

                        }
                        else //forem diferentes entao, gravamos a primeira visualizacao de hoje.
                        {
                            userViews.push({
                                date: today,
                                usersId: [whoSawTheProfile],
                            })

                            this.verifyViewsAndSendEmailNotification (userViews)
                        }

                        //verificar aqui para notificar por email


                    }
                    else //ainda nao possui visualizacoes...
                    {
                        //Adicionamos a primeira visualizacao
                        //Se tiver recuperamos das ja possui

                        user.Profile = {
                            views: [
                                {
                                    date: today,
                                    usersId: [whoSawTheProfile],
                                }
                            ]
                        }

                    }


                    user.isNotMyProfile = 'isNotMyProfile' //indicador para nao actualizar o userAuth
                    this.$store.dispatch('updateOnlineUserData', user)


                }

            },

            verifyViewsAndSendEmailNotification ( userViews )
            {
                let total = 0

                userViews.forEach((uv) => {

                    total += uv.usersId.length

                })

                if (total % 10 !== 0) return

                let message = 'Parabéns ' + this.user.name + ', O seu perfil do explicador possui agora ' + total + ' visualizações.'
                    + ' Mantenha os seus dados do perfil actualizados para que as pessoas vejam as suas informações recentes. Cumprimentos!.'
                let subject = 'Parabens pelas visualizações  no Explicador'

                let email = this.user.email ? this.user.email : 'joseeugenioseie@hotmail.com'

                let emailObject = {
                    message: message,
                    subject: subject,
//                        to: 'joseeugenioseie@gmail.com',
                    emails: [email]
                }

                this.$store.dispatch('sendMail', emailObject)


            },

            fechandoDialog ()
            {
                this.showDialog = false
            },

            sendVerificationLink () {
                this.isThis = true
                this.$store.dispatch('sendVerificationLink')
            },
            mostrarDialog (value) {

                let title = ''
                let message = ''
                let btn1 = ''
                let btn2 = 'Ok'

                if (value && value === 'Welcome')
                {
                    title = 'Seja bem vindo a plataforma dos explicadores'
                    message = 'Esperamos que por meio desta plataforma consigas encontrar ajuda ou ajudar outras pessoas. ' +
                        'vais encontrar aqui, varios explicadores qualificados ou pode se tornar explicador Por registar apenas as disciplinas que Explicas. ' +
                        'Se Desejar pode alterar a sua disponibilidade, tornar-se anónimo ou ainda Estar Visivel, disponivel para ser procurado. ' +
                        'Preencha os dados do seu perfil, de modo a completar 100% em cada parte. Obrigado. ' +
                        '' +
                        ''
                    btn1 = ''
                    btn2 = 'Ok'

                    //actualizar os dados do user no mostrar dialog
                    let user = this.userAuth
                    user.updated_at = new Date()
                    this.$store.dispatch('updateOnlineUserData', user)
                }
                else {

                    title = 'Email de confirmação enviado com sucesso .. .'
                    message = 'Acabamos de te enviar um email. Acesse a caixa de entrada na sua conta de email para confirmar. Te enviamos um link de confirmação, e só clicar. Cumprimentos.'
                    btn1 = ''
                    btn2 = 'Ok'

                }

                /**
                 * Primeiro param: true: significa abrir a dialog
                 * Segundo param:  title: o titulo a mostrar
                 * Terceiro param:  msg: a mensagem a mostrar
                 */
                this.$store.dispatch('togleConfirmDialog', {show: true, value: value, title: title, message: message,btn1: btn1,btn2: btn2})
                this.$store.dispatch('confirmationMailSet', false) //mostra que o email de confirmacao ja foi enviado

            },

        },
    }
</script>
<style>

    .edit-photo-icon {
        top: -20px;
        right: -20px;
        position: absolute;
    }

    .explicador-bar-fixed {
        z-index: 0;
    }
    .explicador-edit-item {
        float: right;
        cursor: pointer
    }
    .icon {
        font-size: 18px;
    }
    .img-radius {
        width: 50px;
        height: 50px!important;
    }
    .container {
        padding: 0px;
    }

