<template>

    <div style="width: 100%">

        <!--Seccao das disciplinas-->
        <v-flex xs12 class="pa-2">
            <div class="cabecalho-profile">
                Informações Académicas

                <explicador-edit-academic-dialog v-if="isMyProfile" :user="user" style="float: right;"></explicador-edit-academic-dialog>

            </div>

            <div style="margin: 30px"></div>

            <v-container>
                <v-layout row>
                    <v-flex xs4>
                        <span class="mr-3" v-if="titleInstitution"><v-icon>home</v-icon></span>
                        <span class="title" v-if="titleInstitution">{{ titleInstitution }}:</span>
                        <br>
                        <br>
                        <span class="mr-3" v-if="titleEducationType"><v-icon>school</v-icon></span>
                        <span class="title" v-if="titleEducationType">{{ titleEducationType }}:</span>
                        <br>
                        <br>
                        <span class="mr-3" v-if="titleEducationStateOrDepartment"><v-icon>school</v-icon></span>
                        <span class="title" v-if="titleEducationStateOrDepartment">{{ titleEducationStateOrDepartment }}:</span>
                        <br>
                        <br>
                        <span class="mr-3"><v-icon>school</v-icon></span>
                        <span class="title">Nivel:</span>

                    </v-flex>

                    <v-flex xs8 class="pl-3" style="border-left: 2px solid orange; height: 280px">

                        <span>{{ userInstitution }}</span>
                        <br>
                        <br>
                        <span>{{ userCourseOrClass }}</span>
                        <br>
                        <br>
                        <span>{{ userDepartmentOrState }}</span>
                        <br>
                        <br>
                        <span>{{ userNivel }}</span>

                    </v-flex>

                </v-layout>
            </v-container>




        </v-flex>

    </div>

</template>

<script>
    export default {
        props: ['user', 'name'],
        data: () => ({
            hidden: false,
            nameU: ''
        }),
        created () {
        },
        computed: {
            options () { //usado para fazer o scroll
                return {
                    duration: 1000,
                    offset: 0,
                    easing: 'easeInQuint'
                }
            },
            userAuth () {
                return this.$store.getters.userAuth
            },

            isUserAuth () {
                return this.userAuth !== null && this.userAuth !== undefined
            },

            isUser () {
                return this.user !== null && this.user !== undefined
            },

            isMyProfile () {
                return this.isUserAuth && this.isUser && this.user.id === this.userAuth.id
            },
            img () {
                return this.isUser ? this.user.photoURL : '//ssl.gstatic.com/s2/oz/images/sge/grey_silhouette.png'
            },
            nomeUser() {
                return this.isUser && this.user.name !== null && this.user.name !== '' ? this.user.name : 'Sem Nome'
            },
            gender () {

                const negative = 'Sexo nao definido'

                if (!this.isUser) return negative

                let genero = this.user.gender

                return genero && genero !== null && genero && genero !== '' ? genero : negative


            },
            hasEducation ()
            {
                if (!this.isUser) return false
                return this.user.Educations && this.user.Educations.level
            },
            titleInstitution () {
                const defaultTitle = 'Universidade'

                if (!this.hasEducation) return defaultTitle

                let titulo = this.user.Educations.level === 'Primário' ? 'Escola' : defaultTitle
                titulo = this.user.Educations.level === 'Secundário' ? 'Escola' : titulo
                titulo = this.user.Educations.level === 'Técnico' ? 'Instituto' : titulo
                titulo = this.user.Educations.level === 'Outro' ? 'Instituicao' : titulo

                return titulo
            },

            titleEducationType () {
                const defaultTitle = 'Curso'

                if (!this.hasEducation) return defaultTitle

                let titulo = this.user.Educations.level === 'Primário' ? 'Classe' : defaultTitle
                titulo = this.user.Educations.level === 'Secundário' ? 'Classe' : titulo
                titulo = this.user.Educations.level === 'Técnico' ? 'Ano' : titulo
                titulo = this.user.Educations.level === 'Outro' ? 'Instituicao' : titulo

                return titulo
            },

            titleEducationStateOrDepartment () {
                const defaultTitle = 'Departamento'

                if (!this.hasEducation) return defaultTitle

                let titulo = this.user.Educations.level === 'Primário' ? 'Estado' : defaultTitle
                titulo = this.user.Educations.level === 'Secundário' ? 'Estado' : titulo
                titulo = this.user.Educations.level === 'Técnico' ? 'Estado' : titulo
                titulo = this.user.Educations.level === 'Outro' ? 'Estado' : titulo

                return titulo
            },

            userInstitution () {

                const defaultTitle = 'Sem Instituicao'

                if (!this.hasEducation) return defaultTitle

                let userEd = this.user.Educations

                let inst = userEd.level === 'Primário' ? userEd.userSchool : defaultTitle
                inst = userEd.level === 'Secundário' ? userEd.userSchool : inst
                inst = userEd.level === 'Técnico' ? userEd.institution : inst
                inst = userEd.level === 'Superior' ? userEd.institution : inst
                inst = userEd.level === 'Outro' ? userEd.institution : inst

                return inst
            },
            userCourseOrClass () {

                const defaultTitle = 'Sem Faculdade'

                if (!this.hasEducation) return defaultTitle

                let userEd = this.user.Educations

                let inst = userEd.level === 'Primário' ? userEd.userClass : defaultTitle
                inst = userEd.level === 'Secundário' ? userEd.userClass : inst
                inst = userEd.level === 'Técnico' ? userEd.courseNivel : inst
                inst = userEd.level === 'Superior' ? userEd.courseNivel : inst
                inst = userEd.level === 'Outro' ? userEd.userSchool : inst

                return inst
            },
            userDepartmentOrState () {

                const defaultTitle = 'Sem Departamento'

                if (!this.hasEducation) return defaultTitle

                let userEd = this.user.Educations

                let inst = userEd.level === 'Primário' ? userEd.educationState : defaultTitle
                inst = userEd.level === 'Secundário' ? userEd.educationState : inst
                inst = userEd.level === 'Técnico' ? userEd.educationState : inst
                inst = userEd.level === 'Superior' ? userEd.department : inst
                inst = userEd.level === 'Outro' ? userEd.educationState : inst

                return inst

            },
            userNivel () {

                const defaultTitle = 'Sem Nivel'

                if (!this.hasEducation) return defaultTitle

                let userEd = this.user.Educations

                let inst = userEd.level === 'Primário' ? userEd.level : defaultTitle
                inst = userEd.level === 'Secundário' ? userEd.level : inst
                inst = userEd.level === 'Técnico' ? userEd.level : inst
                inst = userEd.level === 'Superior' ? userEd.level + ', ' + userEd.courseNivel + ' ano' : inst
                inst = userEd.level === 'Outro' ? userEd.level : inst

                return inst


            },
            hasSubjects () {

                if ( !this.isUser ) return false

                let subjects = this.user.Educations.subjects

                return subjects.length > 0
            },

            subjectsTitle () {

                if (!this.hasSubjects) return 'Ainda não registou as disciplinas que explica'

                return 'Disciplinas do ' + this.nomeUser
            },
            userSubjects () {

                const negative = []

                if (!this.isUser) return negative

                let subjects = this.user.Educations.subjects

                return subjects


            },

            hasSomeLocation () {

                if ( !this.isUser ) return false

                return this.user.Location

            },

            userProvince () {

                const negative = 'Provincia Não registada'

                if (!this.hasSomeLocation) return negative

                let userProv = this.user.Location.province

                return userProv !== null && userProv !== undefined && userProv !== '' ? userProv : negative

            },

            userCity () {

                const negative = 'Cidade Não registada'

                if (!this.hasSomeLocation) return negative

                let userCity = this.user.Location.city

                return userCity !== null && userCity !== undefined && userCity !== '' ? userCity : negative

            },

            userBairro () {

                const negative = 'Bairro Não registado'

                if (!this.hasSomeLocation) return negative

                let userBairro = this.user.Location.bairro

                return userBairro !== null && userBairro !== undefined && userBairro !== '' ? userBairro : negative

            },

        },
        methods: {

            scrollTo (top) {

                window.scroll({
                    top: top,
                    left: 10,
                    behavior: 'smooth'
                });

                // Scroll to a certain element
//                document.querySelector(el).scrollIntoView({
//                    behavior: 'smooth'
//                });

            },
            click () {
                alert('cliou')
            }

        },
    }
</script>
<style scoped>
    .explicador-bar-fixed {
        z-index: 0;
    }
    .explicador-edit-item {
        float: right;
        cursor: pointer
    }
    .icon {
        font-size: 18px;
    }
    .img-radius {
        width: 50px;
        height: 50px!important;
    }
    .container {
        padding: 0px;
    }


</style>


