<template>
    <v-app id="inspire">

        <!--Drawer do lado esquerdo.-->
        <v-navigation-drawer
            fixed
            clipped
            app
            disable-route-watcher
            :temporary="temporaryDrawer"
            v-model="drawerLeft"
        >
            <!--Menu Left-->
            <menu-left :temporary="temporaryDrawer"></menu-left>

        </v-navigation-drawer>

        <!--Drawer do lado direito.-->
        <v-navigation-drawer
            fixed
            clipped
            temporary
            right
            v-model="drawerRight"
        >
            <v-list dense>
                <template>

                    <menu-right></menu-right>

                </template>
            </v-list>
        </v-navigation-drawer>

        <!--Drawer das configuracoes-->
        <v-navigation-drawer
                fixed
                v-model="drawer"
                app
                v-if="$vuetify.breakpoint.name === 'xs'"
                right=""
        >
            <v-list dense>
                <v-subheader>
                    Quer ver Explicadores de que Nivel?
                </v-subheader>
                <v-list-tile
                    @click.prevent.stop="onSelectNewNivel('Primário')"
                    :color="selectedNivel === 'Primário' ? 'blue' : '' ">
                    <v-list-tile-action>
                        <!--<v-icon>change_history</v-icon>-->
                    </v-list-tile-action>
                    <v-list-tile-content>
                        <v-list-tile-title>Primario</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>
                <v-list-tile
                    @click.prevent.stop="onSelectNewNivel('Secundário')"
                    :color="selectedNivel === 'Secundário' ? 'blue' : '' ">
                    <v-list-tile-action>
                        <!--<v-icon>change_history</v-icon>-->
                    </v-list-tile-action>
                    <v-list-tile-content>
                        <v-list-tile-title>Secundario</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>
                <v-list-tile
                    @click.prevent.stop="onSelectNewNivel('Superior')"
                    :color="selectedNivel === 'Superior' ? 'blue' : '' ">
                    <v-list-tile-action>
                        <!--<v-icon>change_history</v-icon>-->
                    </v-list-tile-action>
                    <v-list-tile-content>
                        <v-list-tile-title>Superior</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>
                <v-list-tile
                    @click.prevent.stop="onSelectNewNivel('Técnico')"
                    :color="selectedNivel === 'Técnico' ? 'blue' : '' ">
                    <v-list-tile-action>
                        <!--<v-icon>change_history</v-icon>-->
                    </v-list-tile-action>
                    <v-list-tile-content>
                        <v-list-tile-title>Tecnico</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>
                <v-list-tile
                    @click.prevent.stop="onSelectNewNivel('Outro')"
                    :color="selectedNivel === 'Outro' ? 'blue' : '' "
                >
                    <v-list-tile-action>
                        <!--<v-icon>change_history</v-icon>-->
                    </v-list-tile-action>
                    <v-list-tile-content>
                        <v-list-tile-title>Outros</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>

                <v-divider dark></v-divider>

                <v-list-tile @click.stop="drawer = !drawer;drawerRight = !drawerRight">
                    <v-list-tile-action>
                        <v-icon>account_circle</v-icon>
                    </v-list-tile-action>
                    <v-list-tile-content>
                        <v-list-tile-title>Criar Conta ou Entrar</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>

                <v-divider dark></v-divider>

                <!--Estado de login aqui.-->
                <v-list-tile @click.stop="drawer = !drawer;drawerRight = !drawerRight">
                    <v-list-tile-action>
                        <v-icon>exit_to_app</v-icon>
                    </v-list-tile-action>
                    <v-list-tile-content>
                        <v-list-tile-title>Explicadores Populares</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>
            </v-list>
        </v-navigation-drawer>

        <!--Barra de cima-->
        <!--v-if="!(isRegister || isLogin)" Adicionar essa condicao para esconder a barra no login e registo-->
        <v-toolbar
                color="blue darken-3"
                dark
                app
                clipped-left
                clipped-right
                fixed
        >
            <v-toolbar-title :style="$vuetify.breakpoint.smAndUp ? 'width: 300px; min-width: 250px' : 'min-width: 72px'"  class="ml-0 pl-3">
                <!--Icon que esconde ou mostra o menu de pesquisa-->
                <v-toolbar-side-icon v-if="isHomePage && $vuetify.breakpoint.smAndUp" @click.stop="drawerLeft = !drawerLeft"></v-toolbar-side-icon>

                <!--Titulo para dispositivos maiores-->
                <span class="hidden-xs-only" style="cursor: pointer" @click="pushMainPage">
                    <v-icon v-if="!isHomePage">home</v-icon>
                    <small v-if="isHomePage">{{ title }}</small>
                    <small v-else="">Voltar</small>
                </span>

                <div style="width: 200px">
                    <!--Titulo para mobile devicess-->
                    <span class="hidden-sm-and-up" style="cursor: pointer;" @click="pushMainPage">
                        <div v-if="isHomePage" @click.stop="drawerLeft = !drawerLeft">
                            <v-icon class="mb-1">menu</v-icon>
                            <small>{{ title.substr(0,3) }}</small>
                        </div>
                        <div v-else="">
                            <v-icon class="mb-1">keyboard_backspace</v-icon>
                            <small>Voltar</small>

                        </div>
                    </span>
                </div>


            </v-toolbar-title>



            <!--Div com o campo de texto-->
            <div style="max-width: 600px; min-width: 150px;">
                <!--Campo de pesquisa-->
                <div :style="{width: textFieldWidth, 'margin-bottom': (!showSearchResult || closeFilter) || $vuetify.breakpoint.smAndDown ? 0 : '-20px'}"
                     style="max-width: 600px; top: 0; bottom: auto;" >
                    <v-text-field
                            v-if="(showSearch || !temporaryDrawer) && route === 'Home' && this.$vuetify.breakpoint.width > 500"
                            light
                            solo
                            prepend-icon="search"
                            v-model="search"
                            placeholder="Pesquise pela Provincia, Cursos, Universidade, Disciplinas, Bairros . . ."
                            class="ml-100px"
                            style="max-width: 100%;"
                            @click="showSearchResult = true;closeFilter = false"
                    ></v-text-field>

                    <!--Div com resulsutados de filtro-->
                    <div v-if=" showSearchResult && !closeFilter"
                         class="white ml-100px input-group elevation-2 pa-0 hidden-xs-only"
                         style="height: 20px; position: relative; margin-top: 1px;">

                        <v-container
                            class='white text-xs-center elevation-2'
                            v-if="usersListResult.length === 0" style="height: 40px">
                            <v-layout wrap class='red--text text-xs-center'>
                                <p>Sem ocorrencias</p>
                            </v-layout>
                        </v-container>
                        <v-container v-else="" class='pa-0'>

                            <v-layout>
                                <v-flex xs12 ofsset-xs8 v-if="!showFilterResult && filterType && filterItem">
                                    <a @click="showFilterResult = !showFilterResult" style="float: right; margin: 0 -70%">Mostrar Indicador</a>
                                </v-flex>
                                <v-flex xs12 ofsset-xs8>
                                    <a @click="onCloseFilter" style="float: right; margin: 0 -70%">Lmpar Filtros</a>
                                </v-flex>
                                <v-flex xs12>
                                    <a @click="showSearchResult = !showSearchResult" style="display: flex;flex-direction: row-reverse; margin: 0 2px;">Fechar</a>
                                </v-flex>
                            </v-layout>
                            <v-layout wrap class='ma-0 azul-principal elevation-2'>
                                <search-result keyType='Províncias' :usersListResult="usersListResult"></search-result>
                                <search-result keyType='Distritos' :usersListResult="usersListResult"></search-result>
                                <search-result keyType='Cidades' :usersListResult="usersListResult"></search-result>
                                <search-result keyType='Dist. Municipais' :usersListResult="usersListResult"></search-result>

                                <!--Activar o posto administrativo aqui, quando for necessario-->
                                <search-result keyType='Postos Administrativos' :usersListResult="usersListResult"></search-result>
                                <search-result keyType='Bairros' :usersListResult="usersListResult"></search-result>
                                <search-result keyType='Universidades' :usersListResult="usersListResult"></search-result>
                                <search-result keyType='Escolas' :usersListResult="usersListResult"></search-result>
                                <search-result keyType='Institutos' :usersListResult="usersListResult"></search-result>
                                <search-result keyType='Faculdades' :usersListResult="usersListResult"></search-result>
                                <search-result keyType='Departamentos' :usersListResult="usersListResult"></search-result>
                                <search-result keyType='Cursos' :usersListResult="usersListResult"></search-result>
                                <search-result keyType='Classes' :usersListResult="usersListResult"></search-result>
                                <search-result keyType='Disciplinas' :usersListResult="usersListResult"></search-result>
                                <search-result keyType='Outros' :usersListResult="usersListResult"></search-result>
                            </v-layout>
                        </v-container>

                    </div>

                </div>

            </div>

            <!-- Limpa o campo de pesquisa nos dispositivos maiores-->
            <v-btn v-if="search !== '' && !temporaryDrawer" icon title="Limpar pesquisa" @click="search = ''"><v-icon>close</v-icon></v-btn>

            <!--Espaco direito com as opcoes de login-->
            <div class="d-flex align-center" style="margin-left: auto">

                <!--&lt;!&ndash; Limpa o campo de pesquisa nos dispositivos menores&ndash;&gt;-->
                <!--<v-btn v-if="search !== '' && temporaryDrawer" icon title="Limpar pesquisa" class="primary" @click="search = ''">-->
                    <!--<v-icon>close</v-icon>-->
                <!--</v-btn>-->

                <!--Fecha o campo de pesquisa nos dispositivos menores-->
                <v-btn v-if="search === '' && temporaryDrawer && showSearch && isHomePage" icon title="Fechar o campo" @click="showSearch = !showSearch"><v-icon>close</v-icon></v-btn>

                <!--Icone de pesquisa que so aparece no mobile-->
                <v-btn v-if="!showSearch && route === 'Home' && this.$vuetify.breakpoint.width > 500 && temporaryDrawer" icon @click="showSearch = !showSearch">
                    <v-icon>search</v-icon>
                </v-btn>

                <v-btn v-if="isHomePage" icon="" slot="activator" class="hidden-sm-and-up" @click="drawer = !drawer">
                    <v-icon>exit_to_app</v-icon>
                </v-btn>

                <!--Opcao do user nao autenticado, vem do componente dropdown-->
                <auth-dropdown></auth-dropdown>

                <!--Menu dropdown para as outras funcionalidades da pagina-->
                <v-menu v-if="false" offset-y xs3>
                    <v-btn icon class="hidden-xs-only" slot="activator">
                        <v-icon>apps</v-icon>
                    </v-btn>
                    <v-list>
                        <div>
                            <v-list-tile router to="#">
                                <v-list-tile-action>
                                    <v-icon>info</v-icon>
                                </v-list-tile-action>
                                <v-list-tile-title>Centros de Explicacao</v-list-tile-title>
                            </v-list-tile>

                            <v-list-tile router to="#">
                                <v-list-tile-action>
                                    <v-icon>info</v-icon>
                                </v-list-tile-action>
                                <v-list-tile-title>Bibliotecas </v-list-tile-title>
                            </v-list-tile>

                            <v-list-tile router to="#">
                                <v-list-tile-action>
                                    <v-icon>info</v-icon>
                                </v-list-tile-action>
                                <v-list-tile-title>Material </v-list-tile-title>
                            </v-list-tile>

                        </div>
                    </v-list>
                </v-menu>

                <!--Icone de notificacoes que so aparece quando houver novas notificacoes-->
                <!--Notificacoes-->
                <!--<v-btn icon class="hidden-xs-only">-->
                    <!--<v-icon>notifications</v-icon>-->
                <!--</v-btn>-->


                <v-tooltip bottom>
                    <v-btn icon  slot="activator" class="hidden-xs-only" v-if="isHomePage" @click.stop="drawerRight = !drawerRight">
                        <v-icon>bubble_chart</v-icon>
                    </v-btn>
                    <span>Explicadores populares</span>
                </v-tooltip>
            </div>

        </v-toolbar>

        <!--Conteudo central-->
        <v-content>

            <!-- O conteudo central sera renderizado aqui -->
            <router-view/>

            <!-- colocando o progressbar que aparece em cima-->
            <vue-progress-bar></vue-progress-bar>

        </v-content>

        <!--Montar aqui a dialog de registo-->
        <template>

            <!--Dialog com as mensagens de confirmacao-->
            <confirm-dialog></confirm-dialog>

            <login-dialog v-if="isHomePage && !isUserAuth"></login-dialog>

            <!--Este Fab button, apresenta dois botoes na pagina do perfil do user.-->
            <!--Veja o componente no Shared/HomeFabButtom.vue-->
            <home-fab-buttom v-if="!isHomePage"></home-fab-buttom>

        </template>

        <!--Rodape da pagina-->
        <v-footer :fixed="fixed" app>
            <span>&copy; Explicador 2018</span>
        </v-footer>

        <!--Snackbar para as notificacoe em baixo-->
        <!--Caso o texto tiver mais 130 caracteres, sera mostrado em multiplas linhas 'multi-line'-->
        <v-snackbar
            :timeout="snackbarTimeout"
            :color="snackBarColor"
            bottom
            :multi-line="snackBarText.length > 130"
            v-model="snackbar"
        >
            {{ snackBarText }}
            <v-btn flat @click.prevent="snackbar = false">Fechar</v-btn>
        </v-snackbar>

        <!--Snackbar para informar sobre visibilidade-->
        <!--Caso o texto tiver mais 130 caracteres, sera mostrado em multiplas linhas 'multi-line'-->
        <v-snackbar
                v-if="!isExplicador && isProfilePage && isMyProfile"
                color="red"
                right
                :timeout="timeoutExplicador"
                bottom
                v-model="snackbarInfoExplicador"
        >
            <span @click.prevent="onInfoExplainSubjects"
                style="cursor: pointer"
                title="Clique para registar as disciplinas que explicas">

                <v-icon title="Voçê não está visivel porque ainda não registou as  disciplinas que explica, clique para registar agora">help</v-icon>
                Voçê é explicador? clique aqui.</span>

            <v-btn flat @click.prevent="snackbarInfoExplicador = false">Fechar</v-btn>
        </v-snackbar>


    </v-app>
</template>

<script>
    import { eventBus } from './main'
    export default {
        name: 'App',
        data: () => ({
            title: 'Explicador',
            search: '',
            showSearch: !this.temporaryDrawer,
            filterType: '',
            filterItem: '',
            showSearchResult: false,
            closeFilter: false,
            selectedNivel: 'Secundário',
            showFilterResult: true,
            usersListResult: [],
            drawer: false,
            drawerLeft: this.route === 'Home' ? this.temporaryDrawer : false, //controlando drawer, para mostrar apenas se estiver na pagina principal
            drawerRight: false,
            fixed: true,
            temporary: false,
            snackbar: false,
            snackbarInfoExplicador: true,
            snackbarTimeout: 6000,
            timeoutExplicador: 5000000,
            snackBarText: '',
            snackBarColor: 'green'

        }),
        beforeCreate () {
        },
        created () {
            let inicialprogress = document.getElementById('timeline-wrapper')
            inicialprogress.setAttribute("style", "display: none;");
        },
        mounted () {

//            Escuta de evento que fecha os drawers esquerdo ou direito
            eventBus.$on('closeDrawer', (position) => {

                if (position === 'left'){
                    this.drawerLeft = !this.drawerLeft
                }
                if (position === 'right'){
                    this.drawerRight = !this.drawerRight
                }

            })
            eventBus.$on('ocultarMenus',  (value) => {

                this.drawerLeft = value
                this.drawerRight = value

            })

            eventBus.$on('advancedSearchResult',  (value) => {


                this.usersListResult = value

            })

            eventBus.$on('showWhatIsFiltering', (filterType, filterItem) => {
                this.showFilterResult =  true
                this.filterType = filterType
                this.filterItem = filterItem
            })

            //evento emitido no dialogs/OtherProfileEditSubjectDialog.vue
            //Evento de feicho da dialog
            eventBus.$on('resetShowDialogSujects', () => {

                this.snackbarInfoExplicador = true

            })

            eventBus.$on('showSnackbar', (text, color, timeout) => {

                this.snackBarColor = color
                this.snackBarText = text
                this.snackbarTimeout = timeout
                this.snackbar = true

            })

            //Evento emitido no MenuLeft.vue e App.vue, este eh escutado duas vezes no Home.vue e App.vue
            //Remove o filtro que tiver sido aplicado, ou seja,
            //Se o user tiver escolhido visualizar os explicadores pelo Bairro,
            //Este eleminina o filtro e volta os dados para default
            eventBus.$on('resetExplicadoresFilter', () => {

                this.filterType = ''
                this.filterName = ''
                this.showFilterResult =  false

            })
            eventBus.$on('closeFilter', () => {

                this.closeFilter =  true

            })

            eventBus.$on('semNet', () => {

                this.semNet()

            })

        },
        computed: {
            isSideMenusShowing () {
                return this.$store.getters.isSideMenusShowing
            },
            isHomePage () {
                return this.route === 'Home'
            },
            isRegister () {
                return this.route === 'Register'
            },
            isProfilePage () {
                return this.route === 'Profile'
            },
            users () {
                return this.$store.getters.getUsers
            },
            areUsers () {
                return this.users.length > 0
            },
            userAuth () {
                return this.$store.getters.userAuth
            },
            isUserAuth () {
                return this.userAuth !== null && this.userAuth !== undefined
            },
            user () {
                return this.$store.getters.user
            },
            isUser () {
                return this.user !== null && this.user !== undefined
            },
            isMyProfile () {
                return this.isUserAuth && this.isUser && this.userAuth.id === this.user.id
            },
            isExplicador () {
                return this.isUserAuth && this.userAuth.Explicador
            },
            isLoginPage () {
                return this.$store.getters.isLoginPage
            },
            isRegisterPage () {
                return this.$store.getters.isRegisterPage
            },
            route () {
                return this.$route.name
            },
            textFieldWidth () {

                if (this.temporaryDrawer) return '100%'

                return '150%'

            },
            temporaryDrawer () {
//                console.log('temporary drawer mudou seie', this.$vuetify.breakpoint.name)
                switch (this.$vuetify.breakpoint.name) {
                    case 'xs':
                    case 'sm': return this.isHomePage //No menor retorna true no home page que signifca que o drawer e temporario mas so faz isso no home page
                    case 'md':
                    case 'lg':
                    case 'xl': return !this.isHomePage //No dispositivo maior retorna falso caso nao estiver na pagina principal, que significa que o drawer nao eh temporario, mas nao sera mostrado por nao estar pagina Home.
                }
            }
        },
        watch: {
            // verificando a rota actual, para que ao sair do perfil, a variavel que controla o user que se esta a visualizar o seu perfil, fique nula.
            route (value) {

                this.$store.dispatch('setProfileUserNull')
                this.$store.dispatch('clearError')

                //Controla o snack que alerta o explicador sobre registar as disciplinas que explica
                this.snackbarInfoExplicador = true
                this.timeoutExplicador = 5000000

                this.drawerLeft = false
                this.search = ''
                this.showSearch = ''

                if (value !== 'Home' && this.drawerLeft)
                {
                    this.drawerLeft = false

                } else if (value === 'Home' && !this.temporaryDrawer) {

                    this.drawerLeft = true

                }


            },
            // value = true, significa que eh temporario, oculta o menu para os dispositivos menores
            // value = false, significa ficar sempre visivel, mostrar o drawer nos dispositivos maiores como laptop
            temporaryDrawer (value)
            {


                this.showSearch = !value
                this.drawerLeft = !value //Se o drawer for temporario, vamos esconder. Caso o user quiser visualizar vai clicar no mesmo.
                if (this.route !== 'Home')
                {
                    this.drawerLeft = false
                }
            },
//          Pesquisa implementada aqui. Quando se pesquisa, um evento eh emitido e processado no ficheiro Home.
//          veja o evento $on('searchForExplicador) No ficheiro home.//
            search (value) {

                eventBus.$emit ('searchForExplicador', value)
                if (value === '')
                {
                    this.onCloseFilter()

                } else {

                    this.showSearchResult = true

                }

            }
        },
        methods: {

            onSelectNewNivel (nivel)
            {
                //Emitindo o evento de novo nivel selecionado, para ser tratado no ficheiro Home.vue
                //Pelo metodo principal que trata de escolha de niveis.
                this.selectedNivel = nivel
                this.drawer = false
                eventBus.$emit('mobileSelectNivelEvent', nivel)


            },
            pushMainPage () {
                // Reseta os criterios de pesquisa de dados na pagina, para o default
                // Este evento eh tratado nos metodos created () dos Componentes Home.vue e MainMenuLeft.vue
                this.onCloseFilter()
                this.$router.push('/')
            },
            onCloseFilter () {
                // Reseta os criterios de pesquisa de dados na pagina, para o default
                // Este evento eh tratado nos metodos created () dos Componentes Home.vue e MainMenuLeft.vue
                this.search = ''

                eventBus.$emit('resetExplicadoresFilter'); //evento tratado no App.vue e Home.vue
            },
            onInfoExplainSubjects ()
            {
                //Evento tratado no profile/OtherProfile.vue
                eventBus.$emit ('showDialogSujects')
                this.snackbarInfoExplicador = false
            },
            semNet () {

                let title = 'Sem conexão a internet'
                let message = 'Voçê não tem acesso a internet, verifique a sua conexão ou clique a tecla F5 para refrescar a pagina, .. .\n' +
                              'Se tiver acesso a internet, pode sair da sua conta e entrar novamente! '
                const btn1 = 'Fechar'
                const btn2 = 'Ok'

//              Chamando a tela de confirmacao global, para informar o user que ele nao esta autenticado
                /**
                 * Primeiro param: true: significa abrir a dialog
                 * Segundo param:  title: o titulo a mostrar
                 * Terceiro param:  msg: a mensagem a mostrar
                 */
                this.$store.dispatch('togleConfirmDialog', {show: true, title: title, message: message,btn1: btn1,btn2: btn2})
            }

        }
    }
</script>

<style>

    .list__tile--active {
        background: #e0e0e0;
    }
    .list__tile__title {
        font-size: 11px!important;
    }
    .headline {
        font-size: 14px!important;
        font-weight: bold;
        line-height: 10px!important;
        letter-spacing: normal!important;
    }
    .search-result {
        max-width: 530px;
    }
    /*Para reduzir o tamanho da fonte nas opcoes de mobile profile.*/

    .search-panel {
        /*background: red;*/
        color: black;
        /*height: 5px;*/
        border-radius: 5px;
        /*margin-left: 838px;*/
        right: 0px;
        margin: 0 5px;
        padding: 15px;
        position: absolute;
    }
    @media screen and (max-width: 1175px){
        .search-result {
            max-width: 380px;
        }
    }
    @media screen and (max-width: 958px){
        .search-result {
            max-width: 256px;
        }
    }
    @media screen and (max-width:800px){
        .search-result {
            max-width: 30%;
        }
    }
    @media screen and (max-width: 600px){
        .search-result {
            max-width: 50%;
        }
    }
    .explicador-bar-fixed {
        position: fixed;
        z-index: 1;
        left: 0;
        width: 100%;
        /*left: 0;*/
    }
    .cabecalho-profile {
        margin: 6px;
        padding: 0px 10px 20px;
        font-weight: bold;
        font-size: 14pt;
        border-bottom: 1px solid blue;
        color: blue
    }
    .azul-principal {
        background: #1565C0!important;
    }
    .azul-principal--text {
        color: #1565C0!important;
    }
    .img-radius {
        display: block;
        margin-left: auto;
        margin-right: auto;
        border-radius: 100%;
        width: 90px;
        height: 90px!important;
    }

    /* Estilo para adicionar pading nos botoes de add que chamam as dialogs no perfil*/
    @media screen and (min-width: 1180px) {
        .ml-100px {
            margin-left: 100px
        }
    }

    @media screen and (max-width: 900px) {
        .personalized-pr {
            padding-right: 5px !important;
        }
    }

    @media screen and (max-width: 400px) {
        .personalized-pr {
            padding-right: 35px!important;
        }
    }

    /*Estilo para o icon de processamento nos botoes de registo*/
    .custom-loader {
        animation: loader 1s infinite;
        display: flex;
    }
    @-moz-keyframes loader {
        from {
            transform: rotate(0);
        }
        to {
            transform: rotate(360deg);
        }
    }
    @-webkit-keyframes loader {
        from {
            transform: rotate(0);
        }
        to {
            transform: rotate(360deg);
        }
    }
    @-o-keyframes loader {
        from {
            transform: rotate(0);
        }
        to {
            transform: rotate(360deg);
        }
    }
    @keyframes loader {
        from {
            transform: rotate(0);
        }
        to {
            transform: rotate(360deg);
        }
    }
    .firebase-docs-color {
        color: #039be5;
    }
    .icon {
        font-size: 20px;
    }
</style>


