<template>
	<v-flex xs4 v-if="filterExplicadores.length >0">
    		<v-list dense class='mr-1 mb-1 white elevation-1 main-list' style="height: 130px">
                <!--Cabecalho de cada card-->
	            <v-subheader class=' link main-title'
                    :title="filterExplicadores.length + ' ' + keyType">
                    {{ keyType | strReduce }} ({{ filterExplicadores.length }})
                </v-subheader>
	            <v-divider></v-divider>
	            <v-list-tile
                    avatar
                    class=' list-modified link'
                    v-for="(item, i) in filterExplicadores" v-if="i < 3" v-bind:key="item">
	              <v-list-tile-content
                      class=' link link-title mb-0'
                      @click="filterSpecificsExplicadores(keyType, item)"
                      :title="'Ver apenas explicadores de ' + item">
	                <v-list-tile-title v-html="(item) + ' ' + totalFiltered(item)"></v-list-tile-title>
	              </v-list-tile-content>
	            </v-list-tile>
                <v-list-tile
                        avatar
                        class=' list-modified link'
                        v-if="filterExplicadores.length > 3 ">
                    <v-list-tile-content class='mr-0 mb-0 link link-title'>
                        <v-list-tile-title v-html="'Ver todos'"></v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>

        	</v-list>
    </v-flex>
</template>

<script>
    import { eventBus } from '../../main'
    export default {
        props: ['keyType', 'usersListResult'],
        computed: {
            explicadores() {
                return []
            },
            users () {
                return this.$store.getters.getUsers
            },
            areUsers () {
                return this.users.length > 0
            },
            filterExplicadores ()
            {

                return  (this.areUsers && this.keyType === 'Provincias') ? this.provinces() :
                        (this.areUsers && this.keyType === 'Distritos') ? this.districts() :
                        (this.areUsers && this.keyType === 'Cidades') ? this.cities() :
                        (this.areUsers && this.keyType === 'Distritos Urbanos') ? this.urbanDistrict() :
                        (this.areUsers && this.keyType === 'Postos Administrativos') ? this.administractivePost() :
                        (this.areUsers && this.keyType === 'Bairros') ? this.bairros() :
                        (this.areUsers && this.keyType === 'Universidades') ? this.universities() :
                        (this.areUsers && this.keyType === 'Escolas') ? [] :  //por implementar esta pesquisa
                        (this.areUsers && this.keyType === 'Institutos') ? [] : //por implementar esta pesquisa
                        (this.areUsers && this.keyType === 'Classes') ? [] : //por implementar esta pesquisa
                        (this.areUsers && this.keyType === 'Outros') ? [] : //por implementar esta pesquisa
                        (this.areUsers && this.keyType === 'Faculdades') ? this.faculties() :
                        (this.areUsers && this.keyType === 'Departamentos') ? this.departments() :
                        (this.areUsers && this.keyType === 'Cursos') ? this.courses() :
                        (this.areUsers && this.keyType === 'Disciplinas') ? this.subjects() : []

            },
        },
        filters: {
            strReduce (str)
            {
                if (str.length < 20) return str
                return str.substr(0, 18) + '.'
            }
        },
        methods: {

            filterSpecificsExplicadores (itemSelected, type)
            {
 //                Enviar o valor e o tipo do valor, ex: Matematica Basica, tipo: Disciplina
                eventBus.$emit('filterExplicadoresBy', type, itemSelected); //Evento sendo escutado no ficheiro Home.vue
                eventBus.$emit('showWhatIsFiltering', type, itemSelected); //Evento sendo escutado no ficheiro Home.vue
                eventBus.$emit('closeFilter'); //Fechar a janela do filtro, tratado no App.vue

            },
            totalFiltered (item)
            {

//                eventBus.$emit('filterExplicadoresBy', item, this.keyType); //Evento sendo escutado no ficheiro Home.vue
//
                return '';
            },
            provinces () {
                let items = []
                this.usersListResult.forEach((u) => {
                    if (u.Locations && u.Locations.province && u.Locations.province !== '' && !items.includes(u.Locations.province))
                    {
                        items.push(u.Locations.province)

                    }
                })
                return items
            },

            districts () {
                let items = []
                this.usersListResult.forEach((u) => {
                    if (u.Locations && u.Locations.district && u.Locations.district !== '' && !items.includes(u.Locations.district))
                    {
                        items.push(u.Locations.district)
                    }
                })
                return items
            },

            cities () {
                let items = []
                this.usersListResult.forEach((u) => {
                    if (u.Locations && u.Locations.city && u.Locations.city !== '' && !items.includes(u.Locations.city))
                    {
                        items.push(u.Locations.city)
                    }
                })
                return items
            },

            urbanDistrict () {
                let items = []
                this.usersListResult.forEach((u) => {
                    if (u.Locations && u.Locations.municipalDistrict && u.Locations.municipalDistrict !== ''
                        && !items.includes(u.Locations.municipalDistrict))
                    {
                        items.push(u.Locations.municipalDistrict)
                    }
                })
                return items
            },

            administractivePost () {
                let items = []
                this.usersListResult.forEach((u) => {
                    if (u.Locations && u.Locations.post && u.Locations.post !== '' && !items.includes(u.Locations.post))
                    {
                        items.push(u.Locations.post)
                    }
                })
                return items
            },

            bairros () {
                let items = []
                this.usersListResult.forEach((u) => {
                    if (u.Locations && u.Locations.bairro && u.Locations.bairro !== '' && !items.includes(u.Locations.bairro))
                    {
                        items.push(u.Locations.bairro)
                    }
                    else if (u.Locations && u.Locations.locality && u.Locations.locality !== '' && !items.includes(u.Locations.locality))
                    {
                        items.push(u.Locations.locality)
                    }
                })
                return items
            },

            universities () {
                let items = []
                this.usersListResult.forEach((u) => {
                    if (u.Educations && u.Educations.institution && u.Educations.institution !== '' && !items.includes(u.Educations.institution))
                    {
                        items.push(u.Educations.institution)
                    }
                })
                return items
            },

            faculties () {
                let items = []
                this.usersListResult.forEach((u) => {
                    if (u.Educations && u.Educations.faculty && u.Educations.faculty !== '' && !items.includes(u.Educations.faculty))
                    {
                        items.push(u.Educations.faculty)
                    }
                })
                return items
            },

            departments () {
                let items = []
                this.usersListResult.forEach((u) => {
                    if (u.Educations && u.Educations.department && u.Educations.department !== '' && !items.includes(u.Educations.department))
                    {
                        items.push(u.Educations.department)
                    }
                })
                return items
            },

            courses () {
                let items = []
                this.usersListResult.forEach((u) => {
                    if (u.Educations && u.Educations.course && u.Educations.course !== '' && !items.includes(u.Educations.course))
                    {
                        items.push(u.Educations.course)
                    }
                })
                return items
            },

            subjects () {
                let items = []
                this.usersListResult.forEach((u) => {
                    if (u.Educations && u.Educations.subjects)
                    {
                        u.Educations.subjects.forEach((s) => {

                            if (s.name !== '' && !items.includes(s.name))
                                items.push(s.name)

                        })

                    }
                })
                return items
            },



        }
    }
</script>
<style scoped>
	.subheader {
     	height: 20px; 
    }
    .avatar img {
	    height: 25px;
	    width: 25px;
	}
	.list-modified {
	 	height: 20px; 
	}
    .link {
        cursor: pointer;
        color: black;
    }
    .main-title {
        font-weight: bold;
        font-size: 1.1em;
        border-bottom: solid #f5f5f5 2px;
    }

    .link-title:hover {
        font-weight: bold
    }
</style>


